import axios from "axios";
import { rgpWebUrl } from "../../services/paths.js";

const initialState = () => ({
  resoluciones: [],
  manuales: [],
  errorConexion: { resoluciones: false, manuales: false },
});

const state = initialState();

const getters = {
  existenResoluciones: (state) => {
    return state.resoluciones.length === 0;
  },
  existenManuales: (state) => {
    return state.manuales.length === 0;
  },
};

const mutations = {
  AGREGAR_RESOLUCIONES(state, param) {
    state.resoluciones = [...param];
  },
  AGREGAR_MANUALES(state, param) {
    state.manuales = [...param];
  },
  MOSTRAR_ERROR_CONEXION(state, valor) {
    state.errorConexion[valor] = true;
  },
  OCULTAR_ERROR_CONEXION(state, valor) {
    state.errorConexion[valor] = false;
  },
 };

const actions = {
  extraerResoluciones({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/resoluciones`)
      .then((response) => {
        commit("AGREGAR_RESOLUCIONES", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarResolucion({ commit, dispatch}, resolucion) {
    axios
      .post(`${rgpWebUrl}/resoluciones`, resolucion)
      .then((response) => {
        dispatch("extraerResoluciones");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarResolucion({ commit, dispatch }, id) {
    axios
      .delete(`${rgpWebUrl}/resoluciones/${id}`)
      .then((response) => {
        dispatch("extraerResoluciones");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarResolucion({ commit, dispatch }, resolucion) {
    axios
      .put(`${rgpWebUrl}/resoluciones`, resolucion)
      .then((response) => {
        dispatch("extraerResoluciones");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },

  extraerManuales({ commit }) {
    axios
    .get(`${rgpWebUrl}/manuales`)
    .then((response) => {
      commit("AGREGAR_MANUALES", response.data);
      commit("OCULTAR_ERROR_CONEXION")
    })
    .catch(() => {
      commit("MOSTRAR_ERROR_CONEXION")
    });
  },
  agregarManual({ commit, dispatch}, manual) {
    axios
      .post(`${rgpWebUrl}/manuales`, manual)
      .then((response) => {
        dispatch("extraerManuales");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarManual({ commit, dispatch }, id) {
    axios
      .delete(`${rgpWebUrl}/manuales/${id}`)
      .then((response) => {
        dispatch("extraerManuales");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarManual({ commit, dispatch }, manual) {
    axios
      .put(`${rgpWebUrl}/manuales`, manual)
      .then((response) => {
        dispatch("extraerManuales");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};