    TitleSection,
    Liquidaciones,
    DownloadSection,
    UserSection,
    InscriptionSection,
    ValuationSection,
    TablaDePagos,
    ModalSeleccionTramites,
  },
  computed: {
    ...mapGetters("Tramites-seleccionados", {
      existeTipoTramiteWH: "existeTipoTramiteWH",
    }),
    ...mapState("Tramites-seleccionados", ["numeroLiquidacion"]),
  },
  methods: {
    ...mapActions("Modal", ["mostrarModalSeleccionTramites"]),
    existeTipoTramite(tipo) {
      return this.existeTipoTramiteWH(tipo);
    },
  },
  data: () => ({
    panel: 0,
  }),
};
</script>

