<script>
import Vue from "vue";
import { mapActions, mapState, mapGetters } from "vuex";
import Editor from '@tinymce/tinymce-vue'

export default Vue.extend({
  components: {
    'editor': Editor
  },
  name: "ResolucionesSection",
  data: () => ({
    result: [],
    showAlert: false,
    redox: [],
    cantidadAMostrar: 5,
    opciones: [5, 10, 20, 50],
    snackbar: false,
    colorSnackbar: null,
    heightSnackbar: null,
    timeSnackbar: -1,
    botonesConfirmarSnackbar: false,
    botonAceptarSnackbar: false,
    mensajeSnackbar: "",
    titleEdition: "",
    textoBoton: "",
    idResolucion: "",
    linkPdf1: "",
    linkPdf2: "",
    linkPdf3: "",
    linkPdf4: "",
    linkPdf5: "",
    descripcionNuevaResolucion: "",
    tituloNuevaResolucion: "",
    anioResolucion: "",
    indiceResolucion: "",
    resolucionEditada: {},
    idResolucionSeleccionada: null,
    showEditionPanel: false,
    nuevaResolucion: false,
    requiredNumber: [
      (v) => !!v || "Campo requerido",
      (v) => !/\D/.test(v) || /\./.test(v) || "Formato numerico",
    ],
    requiredRule: [(v) => !!v || "Campo requerido"],
    resolucionesOrdenadas: [],
    perfilAdmin: false,
    tinymceActivo: false,
  }),
  updated(){
    if(this.tinymceActivo == true){
      tinyMCE.activeEditor.setContent(this.descripcionNuevaResolucion)
      this.tinymceActivo = false
    }  
  },

  computed: {
    ...mapState("Listado-resoluciones", ["resoluciones", "errorConexion"]),
    ...mapState("Logincidi", ["usuarioInterno"]),
    ...mapGetters("Listado-resoluciones", ["existenResoluciones"])
  },
  created() {
    if(this.resoluciones.length == 0){
      this.extraerResoluciones()
    }

    if (this.$route.name.includes("admin")) {
      this.perfilAdmin = true;
      this.nuevaResolucion = true;
    }
    // console.log(this.resoluciones)
  },
  methods: {
    ...mapActions("Listado-resoluciones", [
      "extraerResoluciones",
      "agregarResolucion",
      "eliminarResolucion",
      "modificarResolucion",
    ]),
    mostrarCrearResolucion() {
      this.showEditionPanel = true;
      this.nuevaResolucion = false;
      this.descripcionNuevaResolucion = "";
      this.titleEdition = "Crear Resolución";
      this.showAlert = false;
      this.textoBoton = "Crear";
    },
    cancelarEdicionResolucion() {
      this.showEditionPanel = false;
      this.nuevaResolucion = true;
      this.$refs.formulario.reset();
      this.showAlert = false
      this.tinymceActivo = false
    },
    htmlTransform: function(value, indice) {
      const matchNegrita = value.match(/(["¿])(?:(?=(\\?))\2.)*?\1/g);
      //const matchUrl = value.split(" ");
      if (matchNegrita && matchNegrita.length > 0) {
        matchNegrita.forEach((coincidencia) => {
          const coincidenciaSinCaracter = coincidencia.replace(/¿/g, '"');
          value = value.replace(
            coincidencia,
            `<b>${coincidenciaSinCaracter}</b>`
          );
        });
      }
      return value;
    },
    confirmar() {
      this.$refs.formulario.validate();
      let campo = tinyMCE.activeEditor.getContent()
      campo = campo.replaceAll('&nbsp;', '')
      campo = campo.replaceAll('<p>', '')
      campo = campo.replaceAll('</p>', '')
      campo = campo.trim()  
      campo = campo.replace(/[\r\n]/gm, '');
      this.descripcionNuevaResolucion = this.descripcionNuevaResolucion.replaceAll("&nbsp;", "")
      this.descripcionNuevaResolucion = this.descripcionNuevaResolucion.replace(/[\r\n]/gm, '');
      if (this.$refs.formulario.validate() == true && (campo != "")) {
        this.showAlert = false;
        if (this.titleEdition == "Crear Resolución") {
          this.crearResolucion();
        } else {
          this.editarResolucion();
        }
      } else {
        if (this.$refs.formulario.validate()) {
          this.descripcionNuevaResolucion = ""
        }
        this.showAlert = true;
      }
    },
    crearResolucion() {
      const arregloDeLinks = [this.linkPdf1];
      for(let i = 2; i < 6; i++) {
        if (this['linkPdf' + i]) {
          arregloDeLinks.push(this['linkPdf' + i])
        } else{
          break;
        }
      }
      let resolucion = {};
      resolucion.anio = this.anioResolucion;
      resolucion.indice = this.indiceResolucion;
      resolucion.titulo = this.tituloNuevaResolucion;
      resolucion.descripcion = this.descripcionNuevaResolucion.trim();
      resolucion.links_pdf = arregloDeLinks;
      try {
        this.agregarResolucion(resolucion);
      } catch (error) {
        this.mostrarSnackbar(
          "red",
          5000,
          60,
          "Hubo un error en la conexión. Por favor intente nuevamente.",
          true,
          false
        );
        return;
      }
      this.mostrarSnackbar(
        "success",
        3000,
        30,
        "La creación se realizó con éxito.",
        false,
        false
      );
      this.nuevaResolucion = true;
      this.linkPdf5 = '';
      this.$refs.formulario.reset();
      this.cancelarEdicionResolucion();
    },
    mostrarEliminarResolucion(anio, resolucion) {
      this.idResolucionSeleccionada = resolucion.id;
      this.mostrarSnackbar(
        "primary",
        5000,
        60,
        "¿Confirma que desea eliminar la resolución: " +
          resolucion.indice + " / " + anio +
          "?",
        false,
        true
      );
    },
    scroll() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    borrarResolucion() {
      try {
        this.eliminarResolucion(this.idResolucionSeleccionada);
      } catch (error) {
        this.mostrarSnackbar(
          "red",
          5000,
          60,
          "Hubo un error en la conexión. Por favor intente nuevamente.",
          true,
          false
        );
        return;
      }
      this.mostrarSnackbar(
        "success",
        3000,
        30,
        "La eliminación se realizó con éxito.",
        false,
        false
      );
      //this.actualizarPagina();
      this.idResolucionSeleccionada = null;
    },
    mostrarSnackbar(
      color,
      time,
      height,
      mensaje,
      botonAceptar,
      botonesConfirmar
    ) {
      this.snackbar = true;
      this.colorSnackbar = color;
      this.timeSnackbar = time;
      this.heightSnackbar = height;
      this.mensajeSnackbar = mensaje;
      this.botonAceptarSnackbar = botonAceptar;
      this.botonesConfirmarSnackbar = botonesConfirmar;
    },
    anularSnackbar() {
      this.snackbar = false;
      this.colorSnackbar = "";
      this.timeSnackbar = -1;
      this.heightSnackbar = "";
      this.mensajeSnackbar = "";
      this.botonAceptarSnackbar = false;
      this.botonesConfirmarSnackbar = false;
      this.idResolucionSeleccionada = null;
    },
    mostrarEditarResolucion(param, param2) {
      // console.log(param);
      this.nuevaResolucion = false;
      this.showEditionPanel = true;
      this.titleEdition = "Modificar Resolución";
      this.textoBoton = "Modificar";
      this.showAlert = false;
      this.tituloNuevaResolucion = param.titulo;
      this.descripcionNuevaResolucion = param.descripcion;
      this.tinymceActivo = true
      this.idResolucionSeleccionada = param.id;
      this.anioResolucion = param2;
      this.indiceResolucion = param.indice;
      this.linkPdf1 = param.url_pdf_1;
      this.linkPdf2 = param.url_pdf_2;
      this.linkPdf3 = param.url_pdf_3;
      this.linkPdf4 = param.url_pdf_4;
      this.linkPdf5 = param.url_pdf_5;
      this.scroll();
    },
    editarResolucion() {
      const arregloDeLinks = [this.linkPdf1];
      for(let i = 2; i < 6; i++) {
        if (this['linkPdf' + i]) {
          arregloDeLinks.push(this['linkPdf' + i])
        } else{
          break;
        }
      }
      let resolucion = {};
      resolucion.idResolucion = this.idResolucionSeleccionada;
      resolucion.anio = this.anioResolucion;
      resolucion.indice = this.indiceResolucion;
      resolucion.titulo = this.tituloNuevaResolucion;
      resolucion.descripcion = this.descripcionNuevaResolucion.trim();
      resolucion.links_pdf = arregloDeLinks;
      try {
        this.modificarResolucion(resolucion);
        console.log('Entro correctamente y esto envio', resolucion)
      } catch (error) {
        this.mostrarSnackbar(
          "red",
          5000,
          60,
          "Hubo un error en la conexión. Por favor intente nuevamente.",
          true,
          false
        );
        return;
      }
      this.mostrarSnackbar(
        "success",
        3000,
        30,
        "La modificación se realizó con éxito.",
        false,
        false
      );
      //this.actualizarPagina();
      this.showEditionPanel = false;
      this.nuevaResolucion = true;
      this.$refs.formulario.reset();
      this.idResolucionSeleccionada = null;
    },
    mostrarErrorConexion() {
      if (this.errorConexion == true) {
        this.snackbar = true;
      } else {
        this.snackbar = false;
      }
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    eliminarLink (numeroLink) {
      if (numeroLink === 1) {
        this.linkPdf1 = '';
      }
      if (numeroLink === 2) {
        this.linkPdf2 = '';
      }
      if (numeroLink === 3) {
        this.linkPdf3 = '';
      }
      if (numeroLink === 4) {
        this.linkPdf4 = '';
      }
      if (numeroLink === 5) {
        this.linkPdf5 = '';
      }
      this.limpiarLinksPdf();
    },
    limpiarLinksPdf () {
      if(this.linkPdf1 === '' && this.linkPdf2 !== '') {
        this.linkPdf1 = this.linkPdf2;
        this.linkPdf2 = '';
      }
      if(this.linkPdf2 === '' && this.linkPdf3 !== '') {
        this.linkPdf2 = this.linkPdf3;
        this.linkPdf3 = '';
      }
      if(this.linkPdf3 === '' && this.linkPdf4 !== '') {
        this.linkPdf3 = this.linkPdf4;
        this.linkPdf4 = '';
      }
      if(this.linkPdf4 === '' && this.linkPdf5 !== '') {
        this.linkPdf4 = this.linkPdf5;
        this.linkPdf5 = '';
      }
      
    }
  }
});
</script>

