<script>
import Vue from "vue";
// import TablaDePagos from "./TablaDePagos";
import FormularioAdjunto from "./FormularioAdjunto";
import { mapGetters, mapState, mapActions } from "vuex";

export default Vue.extend({
  name: "ValuationSection",
  components: {
    FormularioAdjunto,
    // TablaDePagos,
  },

  data: () => ({
    estadin: false,
    panel: 0,
    tasaCero: false,
    maximoCaracteres: null,
    valorPredefinido: '0,00',
    // Validaciones de formulario
    valid: true,
    snackbar: false,
    fieldRules: [
      (v) => !!v || "Campo requerido",
      (v) => !/\D/g.test(v) || "Campo Numerico",
    ],
    campoConDecimales: [
      (v) => !!v || "Campo requerido",
      (v) => !/[' ']/g.test(v) || "Solo números y coma",
      (v) => !/[A-Za-z]/g.test(v) || "Solo números y coma",
      (v) => /[0-9,]/g.test(v) || "Solo números y coma",
      (v) => !/[.]/g.test(v) || "Solo números y coma",
      (v) => !/^([,])?$/g.test(v) || "Ingrese el entero",
      (v) => !/^([,][0-9]+)?$/g.test(v) || "Ingrese el entero",
      (v) => /^[0-9]+([,][0-9]+)?$/g.test(v) || "Ingrese hasta 2 decimales",
      (v) => /^[0-9]+([,][0-9]{1,2})?$/g.test(v) || "Máximo 2 decimales",
    ],
    campoConDecimalesTasas: [
      (v) => !!v || "Campo requerido",
      (v) => !/[' ']/g.test(v) || "Solo números y coma",
      (v) => !/[A-Za-z]/g.test(v) || "Solo números y coma",
      (v) => /[0-9,]/g.test(v) || "Solo números y coma",
      (v) => !/[.]/g.test(v) || "Solo números y coma",
      (v) => !/^([,])?$/g.test(v) || "Ingrese el entero",
      (v) => !/^([,][0-9]+)?$/g.test(v) || "Ingrese el entero",
      (v) => /^[0-9]+([,][0-9]+)?$/g.test(v) || "Ingrese hasta 2 decimales",
      (v) => /^[0-9]+([,][0-9]{1,2})?$/g.test(v) || "Máximo 2 decimales",
      (v) => {
    // Normalizar el valor: reemplazar comas por puntos para facilitar el análisis de decimales
    let normalizedValue
    if (v && v.includes(',')) {
      normalizedValue = v.replace(/,/g, '.');
    } else {
      normalizedValue = v + ',00'
      normalizedValue = normalizedValue.replace(/,/g, '.');
    }
    
    // Separar la parte entera y decimal
    const [integerPart, decimalPart] = normalizedValue.split('.');
    
    // Validar parte decimal (máximo 2 dígitos)
    if (decimalPart && decimalPart.length > 2) {
      return "Solo hasta 2 decimales permitidos";
    }
    
    // Validar parte entera (máximo 10 dígitos)
    if (integerPart.replace(/^0+/, '').length > 10) {
      return "La parte entera debe tener como máximo 10 dígitos";
    }
    
    // Validar formato general
    return /^[0-9]{1,10}(\.[0-9]{0,2})?$/.test(normalizedValue) || "Número inválido";
  }
  ],
    formRequired: {
      required: true,
    },
    nameRules: [
      (v) => !!v || "Campo requerido",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    variablesConDecimal: [
      "BIISVE",
      "BIIMID",
      "NVAENT",
      "MONABO",
      "VCP",
      "BIIS",
      "PRECES",
      "CERTVALESP",
      "BIIMPINM",
      "MONTOCRED",
      "MONCED",
      "CAPINTCONV",
      "MONAFIAN",
      "MONGRAV",
      "BIINMLM",
      "PRECSUB",
      "ABONO",
      "MONCONT",
    ],
    variablesConDecimalDeCredito: [
      "BIIMPINM",
      "BIIS",
      "VCP",
      "MONTOCRED",
      "BIIMID",
      "MONGRAV",
    ],
    lazy: false,
  }),
  created() {
    if (this.publicidad_seleccionada.length != 0) {
      this.borrarPublicidadSeleccionada();
    }
  },
  watch: {
    checkFormularioTramite() {
      if (this.validarFormularioTramite == true) {
        this.verificarFormAnexo();
        this.cancelFormularioTramite();
      }
    },
  },

  computed: {
    ...mapGetters("Tramites-seleccionados", ["existeFormularioAdjunto"]),
    ...mapGetters("Listado-tramites", ["obtenerTramitePorId"]),
    ...mapState("Tramites-seleccionados", [
      "warehouse",
      "userFormValidado",
      "publicidad_seleccionada",
      "tasas_seleccionadas",
      "validarFormularioTramite",
    ]),
    ...mapState("Tramites-completados", ["completados"]),
    checkFormularioTramite() {
      return this.validarFormularioTramite;
    },
  },
  methods: {
    ...mapActions("Tramites-seleccionados", [
      "duplicarRenglon",
      "eliminarRenglon",
      "actualizarCampo",
      "borrarPublicidadSeleccionada",
      "eliminarTramiteWh",
      "validarTramite",
      "cancelFormularioTramite",
      "validateGenerarPago",
    ]),
    permitirDecimales(param) {
      return (
        this.variablesConDecimal.find((element) => element == param) !=
        undefined
      );
    },
    permitirDecimal(param, event) {
      if (
        this.variablesConDecimal.find((element) => element == param) ==
        undefined
      ) {
        this.onlyNumber(event);
      }
    },
    permitirDecimalesDeCredito(param) {
      return (
        this.variablesConDecimalDeCredito.find((element) => element == param) !=
        undefined
      );
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    /* validarCampoNumerico(param) {
      const regexpNumeros = /\D/;
      if (param.target.value.match(regexpNumeros)) {
        param.target.value = null;
      }
    }, */
    validarCampoNumerico(param) {
      const regexpNumeros = /\D/;
      return param.match(regexpNumeros);
    },
    ...mapActions("Tramites-completados", ["agregarTramiteCompledo"]),
    agregarRenglon(id) {
      const campos = this.obtenerTramitePorId(id);
      this.duplicarRenglon(JSON.parse(JSON.stringify(campos)));
    },
    quitarRenglon(id_tramite, indice_renglon) {
      const path = {
        id_tramite,
        indice_renglon,
      };
      this.eliminarRenglon(path);
    },
    quitarTramite(id_tramite) {
      this.eliminarTramiteWh(id_tramite);
    },
    update(indice_tramite, indice_renglon, indice_variable, $event) {
      let valor = null;
      if (!event.target.value.includes(",")) {
        if (Number(event.target.value) == 0) {
          valor = "0";
        } else {
          valor = Number(event.target.value);
        }
      } else {
        if (event.target.value < "0,01") {
          valor = "0";
        } else {
          valor = event.target.value.toString().replace(",", ".");
        }
      }
      const indices = {
        indice_tramite,
        indice_renglon,
        indice_variable,
        valor: valor,
      };
      this.actualizarCampo(indices);
    },
    validarValor(param) {
      if (param.trim() == "" || param == null || param == undefined) {
        return 0;
      } else {
        if (param != 0) {
          param = Number(param).toString();
        } else {
          param = 0;
        }
      }
      return param;
    },
    verificarFormAnexo() {
      if (this.userFormValidado == true) {
        if (
          this.warehouse.find((element) => element.completado == false) !=
          undefined
        ) {
          this.snackbar = true;
        } else {
          this.validateGenerarPago();
        }
      }
    },

    reset() {
      this.$refs.form7.reset();
    },
    validar(id_tramite, tramite, tramiteGuardado) {
      tramite.formulario.campos[0].campos.forEach(campo => {
        if (!campo.valor) {
          campo.valor = 0
        }
      });

      if (this.$refs[id_tramite][0].validate()) {
        this.validarTramite(id_tramite);
        this.agregarTramiteCompledo(JSON.parse(JSON.stringify(tramite)));
      }
    },
  },
  // watch: {
  //   estadin: function(newQuestion, oldQuestion) {
  //     console.log(`${oldQuestion} -> ${newQuestion}`);
  //   }
  // }
});
</script>
