<template>
  <v-row class="mt-0" id="paymentTable">
    <v-col cols="12" class="pb-0 pt-0">
      <v-row class="mx-titulo">
        <v-col cols="auto" class="titulo-margen px-0">
          <v-card outlined>
            <div class="titulo py-2 px-3">Tabla de pagos</div>
          </v-card>
        </v-col>
      </v-row>
      <v-card v-if="!loading" elevation="2" class="text-xs-center footerBntCard">
        
        <!-- loading -->
        <v-data-table
          :headers="headers"
          :items="tCompletados"
          :items-per-page="-1"
          hide-default-footer
          class="pt-6 pb-0 px-3"
        >
 
          <template v-slot:[`item.action`]="{ item }">
            <div class="row" v-if="reimprimirEfectivo == null">
              <div class="col-6">
                <v-btn
                  class="px-0 primary"
                  @click="editarItem(item.id)"
                  small
                  v-if="item.conceptos && item.conceptos != ''"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
              </div>
              <div class="col-6">
                <v-btn class="px-0 error" @click="eliminarItem(item.id)" small>
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </div>
            </div>
          </template>
            <template v-slot:[`item.tasa`]="{ item }">
              {{ item.tasa.replace(".", ",") }}
            </template>
        </v-data-table>
        <div>
          <hr
            role="separator"
            aria-orientation="horizontal"
            class="mx-4 v-divider theme--light"
          />
          <v-row class="d-flex py-1 mx-4 justify-md-end">
            <div class="px-4 textTotal">
              <span><b>Subtotal:</b></span>
            </div>
            <div class="px-5 numberTotal">
              <span>{{ `$ ${calcularTotal.toFixed(2).replace(".", ",")}` }}</span>
            </div>
          </v-row>
          <v-row class="d-flex py-1 mx-4 justify-md-end">
            <div class="px-4 textTotal">
              <span><b>Saldo a favor:</b></span>
            </div>
            <div class="px-5 numberTotal">
              <span>{{ `$ ${totalFaltaReponer.toFixed(2).replace(".", ",")}` }}</span>
            </div>
          </v-row>
          <v-row class="d-flex py-1 mx-4 justify-md-end">
            <div class="px-4 textTotal">
              <span><b>TOTAL:</b></span>
            </div>
            <div class="px-5 numberTotal">
              <span>{{
                `$ ${(calcularTotal + totalFaltaReponer).toFixed(2).replace(".", ",")}`
              }}</span>
            </div>
          </v-row>
        </div>

        <hr
          role="separator"
          aria-orientation="horizontal"
          class="mx-4 v-divider theme--light"
        />
        <v-alert
          text
          outlined
          color="red"
          class="py-2 ml-7 mr-7 mb-2 mt-4 text-center body-2"
          close-icon="close"
          dismissible
          v-if="showAlert"
        >
          <template v-slot:close="{}">
            <v-icon size="18" color="error" @click="showAlert = false"
              >close</v-icon
            >
          </template>
          {{mensajeAlert}}
        </v-alert>
        <v-alert
          text
          outlined
          color="red"
          class="py-2 ml-7 mr-7 mb-2 mt-4 text-center body-2"
          close-icon="close"
          dismissible
          v-if="alert"
        >
          <template v-slot:close="{}">
            <v-icon size="18" color="error" @click="alert = false"
              >close</v-icon
            >
          </template>
            Debe completar o cancelar la tasa seleccionada.
        </v-alert>
        <div class="main mx-4 justify-around">
          <v-btn
            tile
            outlined
            color="primary"
            :disabled="reimprimirEfectivo != null"
            class="no-text-transforms boton my-3"
            @click="limpiarTabla()"
          >
            Restaurar
          </v-btn>
          <v-btn
            tile
            outlined
            color="primary"
            class="no-text-transforms boton my-3"
            @click="imprimir()"
            :loading="imprimiendo == true"
            v-if="reimprimirEfectivo == null"
          >
            Generar
          </v-btn>
          <v-btn
            tile
            outlined
            color="primary"
            class="no-text-transforms boton my-3"
            v-if="liquidacion != null || reimprimirEfectivo != null"
            @click="reimprimirPDFefectivoLocal(JSON.parse(JSON.stringify(reimprimirEfectivo)))"
          >
            Imprimir
          </v-btn>
        </div>
        <v-snackbar
          v-model="snackbar"
          centered
          color="primary"
          timeout="5000 "
          outlined
          text
        >
          <v-row class="snackbar">
            <v-col>
              ¿Confirma que desea eliminar los cambios realizados? 
            </v-col>
          </v-row>
          <v-row class="snackbar">
            <v-col>
              <v-btn
                class="botonSnackbar"
                text
                color="primary"
                @click="snackbar = false"
              >
                NO
              </v-btn>
              <v-btn
                class="botonSnackbar"
                text
                color="primary"
                @click="confirmarLimpiarTabla()"
              >
                SI
              </v-btn>
            </v-col>
          </v-row>
        </v-snackbar>
      </v-card>
      <div v-if="loading" class="final d-flex align-center justify-center pb-3">
        <v-card
          outlined
          height="100%"
          min-height="200px"
          width="100%"
          class="d-flex align-center justify-center"
        >
          <div>
            <v-progress-circular
              :size="40"
              :width="5"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </div>
        </v-card>
      </div>
    </v-col>
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapGetters, mapState, mapActions } from "vuex";

export default Vue.extend({
    name: "TasasFaltaReponer",

    data: () => ({
        // Tabla de valores
        headers: [
        { text: "ACCIONES", sortable: false, value: "action" },
        {
            text: "AÑO",
            sortable: false,
            align: "left",
            value: "anio",
        },
        { text: "SERIE", sortable: false, value: "serie" },
        { text: "TIPO", sortable: false, value: "tipo" },
        { text: "FORMULARIO", sortable: false, value: "formulario" },
        { text: "CÓDIGO", sortable: false, value: "codigo" },
        { text: "TRAMITE A PAGAR", sortable: false, value: "tramite" },
        { text: "CONCEPTOS INVOLUCRADOS", sortable: false, value: "conceptos" },
        {
            text: "TASA A PAGAR POR TRAMITE",
            sortable: false,
            align: "center",
            value: "tasa",
            width: 140,
        },
        ],
        loading: false,
        totalFaltaReponer: 0,
        showAlert: false,
        alert: false,
        mensajeAlert: "",
        imprimiendo: false,
        snackbar : false,
        itemsPrevios: []
    }),
    created() {
      if(this.faltaReponerCargada) {
        this.buscarTramitePorItem()
      }
    },
    computed: {
      ...mapGetters("Listado-tramites", ["tramitesTipoTasaPorAnio", "obtenerTramitePorId"]),
      ...mapGetters("Tramites-completados", {
        calcularTotal: "calcularTotal",
        tCompletados: "formatearTramitesCompletados",
        liquidacion: "liquidacion",
        reimprimirEfectivo: "reimprimirEfectivo",
      }),
      ...mapState("Tramites-seleccionados", ["warehouse"]),
      ...mapState("Listado-tramites", ["tramitesPorAnio", "tramites"]),
      ...mapState("Tramites-completados", [
        "faltaReponer", "completados", "errorConexion", "faltaReponerCargada"
      ]),
      busquedaValidada() {
        return this.faltaReponerCargada
      },
      verWarehouse(){
        return this.warehouse;
      },
      mostrarError(){
        return this.errorConexion;
      },
      tramitesCompletados(){
        return this.tCompletados;
      },
      cargaDeTramites(){
        return this.tramitesPorAnio
      }
    },
    beforeUpdate() {
        if (this.calcularTotal + this.totalFaltaReponer > 0) {
          this.showAlert = false;
        }
        if (this.warehouse.find((element) => element.completado == false) == undefined){
          this.alert = false;
        }
    },
    methods: {
        ...mapActions("Tramites-completados", [
            "limpiarTramitesCompletados",
            "eliminarTramiteCompledo","agregarFaltaReponer", "agregarTramiteCompledo", "reimprimirPDFefectivo"
        ]),
        ...mapActions("Tramites-seleccionados", {
            eliminarTramiteWh: "eliminarTramiteWh",
            editarTramiteWh: "editarTramiteWh",
        }),
        ...mapActions("Listado-tramites", ["setTramitesAnioAnterior", "cargarTramitesPorAnio", "agregarTramitePorAnio"]),
        ...mapActions("Tramites-seleccionados", ["reqAddProcedure", "validarTramite", "resetearWarehouse", "actualizarCampo", "duplicarRenglon"]),
        limpiarTabla() {
          this.imprimiendo = false;
          this.snackbar = true;            
        },
        confirmarLimpiarTabla(){
          this.resetearWarehouse();
          this.tCompletados.forEach((element) => this.eliminarItem(element.id));
          this.limpiarTramitesCompletados();
          this.snackbar = false
          this.resetForm = true;       
          this.buscarTramitePorItem()
        },
        eliminarItem(id_tramite) {
            this.eliminarTramiteWh(id_tramite);
            this.eliminarTramiteCompledo(id_tramite);
        },
        buscarTramitePorItem(){
          this.setTramitesAnioAnterior([])
          this.loading = true
          let ultimo = this.faltaReponer.length - 1;
          this.itemsPrevios = this.faltaReponer[ultimo].items
          this.imprimiendo = false;
          if( this.itemsPrevios.length != 0) { 
            this.itemsPrevios.forEach((element, index)=>{ 
              if(element.anio == new Date().getFullYear()) {
                let tramite = this.tramites.find(e => e.id_item.trim() == element.id_item.trim())
                this.agregarTramitePorAnio(tramite)
              } else {
                this.cargarTramitesPorAnio(element)
              }
            })
          } else {
            this.loading = false
          }
        },
        guardarTasas(param){
          const tasasCompletadas = [];
          this.completados.forEach((element) => {
            let tramiteGuardado = {};
              tramiteGuardado.id = element.id;
              tramiteGuardado.id_item = element.id_item;
              tramiteGuardado.formula = element.formulario.formula;
              tramiteGuardado.monto_fijo = element.formulario.monto_fijo;
              tramiteGuardado.monto_minimo = element.formulario.monto_minimo;
              tramiteGuardado.monto_maximo = element.formulario.monto_maximo;
              tramiteGuardado.serie = element.serie;
              tramiteGuardado.anio = parseInt(element.anio);
            let nuevaFila = [];
            if (element.formulario.campos.length != 0) {
              element.formulario.campos.forEach((elemento) => {
                let nuevoIngreso = [];
                elemento.campos.forEach((elementoInterno) => {
                let valor = elementoInterno.valor;
                if (elementoInterno.valor != undefined) {
                  if(typeof(valor) == "string") {
                        valor = valor.replace(",", ".")
                      }
                      valor = parseFloat(valor)
                      valor.toFixed(2)
                }
                let campo = {};
                (campo.nombre_variable = elementoInterno.nombre_variable),
                  (campo.valor = valor);
                nuevoIngreso.push(campo);
              });
                nuevaFila.push(nuevoIngreso);
              });
            } else {
              nuevaFila = [[]];
            }
            tramiteGuardado.campos = nuevaFila;

            tasasCompletadas.push(tramiteGuardado);
            param.items = tasasCompletadas;
            return param;
          })
        },
        imprimir() {
          if (this.calcularTotal + this.totalFaltaReponer <= 0) {
            this.showAlert = true;
            this.mensajeAlert = "El total a abonar no puede ser un saldo menor o igual a 0.";
          } else if (this.warehouse.find((element) => element.completado == false) != undefined) {
            this.alert = true;
          } else {
            this.showAlert = false;
            this.imprimiendo = true;
            let datos = {};
              let ultimo = this.faltaReponer.length - 1;
              datos.cuit = this.faltaReponer[ultimo].cuit.toString();
              if(this.faltaReponer[ultimo].tasa_total_exenta != 0){
                datos.exento  = true;
              }else{
                datos.exento = false;
              }
              datos.idLey = this.faltaReponer[ultimo].idley;
              datos.mp = this.faltaReponer[ultimo].mp.toString();
              datos.nro_doc = parseInt(this.faltaReponer[ultimo].nro_doc);
              datos.nro_liquidacion = parseInt(this.faltaReponer[ultimo].nro_liquidacion);
              if(this.faltaReponer[ultimo].pago_diferido == "S"){
                datos.pago_diferido = true;
              }else{
                datos.pago_diferido = false;
              }
              datos.profesion = this.faltaReponer[ultimo].profesion;
              datos.solicitante= this.faltaReponer[ultimo].solicitante;
              datos.tipo_usuario = parseInt(this.faltaReponer[ultimo].tipo_usuario);
              datos.faltaReponer = true;
              datos.formulario = parseInt(this.faltaReponer[ultimo].nro_formulario);
              datos.medioDePago = "20";
              datos.faltaReponer = true;
              datos.numeroUnico = parseInt(this.faltaReponer[ultimo].nrounico);
              //let tasasprevias = (parseFloat(this.totalFaltaReponer.toString().replace('.', ',')));
              let tasasprevias = (parseFloat(this.totalFaltaReponer.toString()));


              datos.tasasPrevias = - tasasprevias;
              this.guardarTasas(datos);
              datos.items.forEach(element => {
                element.campos.forEach(el => {
                  el.forEach(e => {
                    if(typeof(e.valor) == "string") {
                      e.valor = e.valor.replace(",", ".")
                    }
                    e.valor = parseFloat(e.valor)
                    e.valor.toFixed(2)
                  });
                })
              });
              this.agregarFaltaReponer(datos);
          }
        },
        editarItem(id_tramite) {
          this.editarTramiteWh(id_tramite);
          this.eliminarTramiteCompledo(id_tramite);
        },
        reimprimirPDFefectivoLocal(reimpresion) {
          this.reimprimirPDFefectivo(reimpresion);
        },
        cargarTasasInicial(){
          this.totalFaltaReponer = 0;
          let valor = 0
          let ultimo = this.faltaReponer.length - 1;
          let tasas = this.faltaReponer[ultimo].items
          let tramitesPrevios = JSON.parse(JSON.stringify(this.tramitesPorAnio))
          tramitesPrevios.forEach((element, index) => {
            let tasa = tasas.filter(e => e.id_item.trim() == element.id_item.trim()) 
            element.formulario.formula = tasa[0].formula;
            element.anio = tasa[0].anio;

            this.reqAddProcedure(element);
    
            // BUSCO LA POSICION DEL TRAMITE EN WAREHOUSE PARA ACTUALIZAR LOS VALORES EN LOS CAMPOS CON LA FUNCION UPDATE
            let tramiteWH = this.warehouse.find(el => el.id_item == element.id_item);
            let posicion = this.warehouse.indexOf(tramiteWH);
            tasa[0].campos.forEach((el, index) => {
              if(index > 0) {
                 this.agregarRenglon(element.id);
              }
              el.forEach((e, i) => {
                let valor = e.valor
                if(valor[0] == ",") {
                  valor = "0" + valor.replace(",", ".")
                }
                this.update(posicion, index, i, valor )
              })
            });

            this.quitarDeWarehouse(element, index, tasa[0]);
            let tram = this.warehouse.filter(
              (e) => e.id == element.id
            );
            this.agregarTramiteCompledo(JSON.parse(JSON.stringify(tram[0])));
          })
          if(this.faltaReponer[ultimo].tasa_total_exenta != 0){
            //valor = parseFloat(this.faltaReponer[ultimo].tasa_total_exenta.replace(",", "."));
            valor = parseFloat(this.faltaReponer[ultimo].tasa_total_exenta);
          }else{
            //valor = parseFloat(this.faltaReponer[ultimo].tasa_total.replace(",", "."));
            valor = parseFloat(this.faltaReponer[ultimo].tasa_total);
          }    
          // this.totalFaltaReponer = this.totalFaltaReponer + valor + parseFloat(this.faltaReponer[ultimo].tasas_previas.replace(",", "."));
          this.totalFaltaReponer = this.totalFaltaReponer + valor + parseFloat(this.faltaReponer[ultimo].tasas_previas);
          this.totalFaltaReponer = -this.totalFaltaReponer;
          this.loading = false 
        },
        agregarRenglon(id) {
          const campos = this.obtenerTramitePorId(id);
          this.duplicarRenglon(JSON.parse(JSON.stringify(campos)));
        },
     
        update(indice_tramite, indice_renglon, indice_variable, valor) {
          const indices = {
            indice_tramite,
            indice_renglon,
            indice_variable,
            valor,
          };
          this.actualizarCampo(indices);
        },

        quitarDeWarehouse(tramite, index, element){
          let tram = this.warehouse.find((el) => el.id == tramite.id);
          let posicion = this.warehouse.indexOf(tram);
          // let indice_tramite = index;
          // let indice_renglon = 0;
          // let indice_variable = element.campos[0].valor
          // let indices = {indice_tramite, indice_renglon, };
          this.validarTramite(posicion);
        }
    },
    watch: {
      busquedaValidada() {
        if(this.faltaReponerCargada) {
          this.buscarTramitePorItem()
        }
      },
      cargaDeTramites(){
        if(this.tramitesPorAnio.length == this.itemsPrevios.length && this.tramitesPorAnio.length != 0){
          this.cargarTasasInicial()
        }
      },
      mostrarError(){
        if(this.errorConexion == true){
          this.imprimiendo = false;
        }
      },
    }
});
</script>

