<template>
    <div>
        <v-row class="mx-titulo">
            <v-col cols="auto" class="titulo-margen ml-1 pt-3">
                <v-card outlined>
                    <div class="titulo py-2 px-3" v-if="tramite.linkSolicitud != null">
                        Editar link
                    </div>
                    <div class="titulo py-2 px-3" v-else>
                        Crear link
                    </div>
                </v-card>
            </v-col>
        </v-row>  
            
        <v-card elevation="2" class="text-xs-center pt-5 mb-3">
            <v-form
                ref="formularioLink"
                class="px-3"
            >
                <v-row class="pt-1 px-3" >
                    <v-col cols="12" class="px-3 pb-0" >
                        <v-text-field
                            v-model="linkSolicitud"
                            item-text="descripcion"
                            dense
                            outlined
                            autocomplete = "off"
                            class="sm-6 pt-1"
                            color="primary"
                            clearable
                            required
                            :rules="requiredRule"
                        >
                        </v-text-field>
                    </v-col>
                </v-row> 
                <v-row class="pt-1 px-6 d-flex justify-space-between" > 
                    <v-btn
                        @click="cancelarEditarLink()"
                        color="primary"
                        outlined 
                        class="mb-5"            
                    >
                        Cancelar
                    </v-btn>
                    <v-btn
                        @click="confirmarEditarLink()"
                        color="primary"
                        outlined 
                        class="mb-5"            
                    >
                        Modificar
                    </v-btn>
                </v-row>  
            </v-form>
        </v-card>
    </div>    
</template>
<script>
import Vue from "vue";
import {mapActions, mapState} from "vuex";



export default Vue.extend ({
    name: "EdicionLink",
    props:{
      link: String,
      tramite: {}
    },
    data:() => ({
        requiredRule: [(v) => !!v || "Campo requerido"],
        linkSolicitud: null,
    }),
    created(){
      this.linkSolicitud = this.link
    },
    computed: {
    ...mapState("Listado-tramites", ["pasosyRequerimientos", "tramites", "tramiteEditable"]),
    },
    methods:{
        ...mapActions("Listado-tramites", ["mostrarEdicionLink", "ocultarEdicionLink", "editarLink"]),
    
        cancelarEditarLink(){
            this.$refs.formularioLink.reset()
            this.ocultarEdicionLink()
            this.$emit('closeLink')
        },
        confirmarEditarLink(){
            this.$refs.formularioLink.validate()
            if(this.$refs.formularioLink.validate() == true){
                let params = Object.assign(
                  {},
                  {
                    id: this.tramiteEditable.id,
                    tipo: this.tramiteEditable.solicitud,
                    url: this.linkSolicitud,
                  }
                );
                try{
                    this.editarLink(params)
                    this.ocultarEdicionLink()
                    this.$emit('closeLink')
                    this.$emit("confirmacionSnackbar")
                }catch(error){
                    error="error en la conexion"
                    alert(error)
                }
            }
        }
    }
 
})
</script>
<style scoped>