<script>
import Vue from "vue";
import ModalItemSelection from "../utils/ModalItemSelection";
import ModalTramiteExistente from "../utils/ModalTramiteExistente";
import ModalLimiteTramites from "../utils/ModalLimiteTramites";
import ModalBloqueoCompletados from "../utils/ModalBloqueoCompletados";
import { mapState, mapActions, mapGetters } from "vuex";

export default Vue.extend({
  /* TODO:
        * Tomar como parametro el titulo, requerimiento, pasos, btn-txt y btn-lnk.
         ---- Marcar en la documentacion que se tendrá por defecto los botones.
         ---- Ademas como minimo 1 requerimiento y 1 paso. --------------------
  */

  name: "ReqTimeline",
  components: {
    ModalItemSelection,
    ModalTramiteExistente,
    ModalLimiteTramites,
    ModalBloqueoCompletados,
  },
  computed: {
    ...mapState("Tramites-seleccionados", ["tramite_seleccionado"]),
    ...mapState("Tramites-seleccionados", ["warehouse"]),
    ...mapState("Tramites-completados", ["completados", "reimprimirEfectivo"]),
    ...mapGetters("Tramites-seleccionados", [
      "existeTramiteYTasaSeleccionado",
      "existeTasa",
    ]),
    ...mapGetters("Tramites-completados", ["existeTramiteYTasaCompletado"]),
  },
  mounted() {
    this.ajustarColumnas();
    if (this.title == "Requisitos") {
      this.anularCursor = "anularCursor";
    }
  },
  beforeUpdate() {
    this.ajustarColumnas();
  },
  props: {
    id: String,
    title: String,
    show: Boolean,
    subtitle: String,
    requires: Array,
    timeline: Array,
    presencial: Boolean,
  },
  data: () => ({
    panel: 0,
    bloquearExpansion: true,
    columnasExternas: 12,
    columnasInternas: 12,
    anularCursor: "",
    vari: "lalala",
    pasoConLinkTasa: [],
  }),
  methods: {
    ...mapActions("Tramites-seleccionados", ["reqAddProcedure", "validarTramite"]),
    ...mapActions("Modal", [
      "mostrarModal",
      "mostrarModalTramiteExistente",
      "mostrarModalLimiteTramites",
      "mostrarModalCompletados",
    ]),
    ...mapActions("Tramites-completados", ["agregarTramiteCompledo"]),
    htmlTransform: function(value, indice) {
      const matchNegrita = value.match(/(["¿])(?:(?=(\\?))\2.)*?\1/g);
      const matchUrl = value.split(" ");

      if (matchNegrita && matchNegrita.length > 0) {
        matchNegrita.forEach((coincidencia) => {
          const coincidenciaSinCaracter = coincidencia.replace(/¿/g, '"');
          value = value.replace(
            coincidencia,
            `<b>${coincidenciaSinCaracter}</b>`
          );
        });
      }

      if (matchUrl && matchUrl.length > 0) {
        matchUrl.forEach((coincidencia) => {
          if (coincidencia.includes("http")) {
            if (coincidencia.includes("s/tasas")) {
              value = value.replace(`aquí: ${coincidencia}`, ``);
            } else {
              value = value.replace(
                `aquí: ${coincidencia}`,
                `<a href="${coincidencia}" target="_blank">aquí</a>`
              );
            }
          }
        });
      }

      return value;
    },
    esLinkTasa(item) {
      // console.log(item, index);
      if (item.includes("http")) {
        if (item.includes("s/tasas")) {
          return true;
        } else {
          return false;
        }
      }
    },
    ajustarColumnas() {
      if (
        (this.tramite_seleccionado.habilita_web == true &&
          this.tramite_seleccionado.habilita_presencial == false &&
          this.tramite_seleccionado.habilita_cvd == false) ||
        (this.tramite_seleccionado.habilita_web == false &&
          this.tramite_seleccionado.habilita_presencial == true &&
          this.tramite_seleccionado.habilita_cvd == false) ||
        (this.tramite_seleccionado.habilita_web == false &&
          this.tramite_seleccionado.habilita_presencial == false &&
          this.tramite_seleccionado.habilita_cvd == true)
      ) {
        this.columnasExternas = 6;
      } else if (
        (this.presencial == true &&
          this.tramite_seleccionado.habilita_web == true &&
          this.tramite_seleccionado.habilita_presencial == true &&
          this.tramite_seleccionado.habilita_cvd == false) ||
        (this.presencial == true &&
          this.tramite_seleccionado.habilita_web == false &&
          this.tramite_seleccionado.habilita_presencial == true &&
          this.tramite_seleccionado.habilita_cvd == true) ||
        (this.presencial == true &&
          this.tramite_seleccionado.habilita_web == true &&
          this.tramite_seleccionado.habilita_presencial == false &&
          this.tramite_seleccionado.habilita_cvd == true)
      ) {
        this.columnasExternas = 12;
        if (
          this.requires.filter((element) => element.descripcion.length > 150)
            .length > 0
        ) {
          this.columnasInternas = 12;
        } else {
          this.columnasInternas = 6;
        }
      } else if (
        (this.presencial == false &&
          this.tramite_seleccionado.habilita_web == true &&
          this.tramite_seleccionado.habilita_presencial == true &&
          this.tramite_seleccionado.habilita_cvd == false) ||
        (this.presencial == false &&
          this.tramite_seleccionado.habilita_web == false &&
          this.tramite_seleccionado.habilita_presencial == true &&
          this.tramite_seleccionado.habilita_cvd == true) ||
        (this.presencial == false &&
          this.tramite_seleccionado.habilita_web == true &&
          this.tramite_seleccionado.habilita_presencial == false &&
          this.tramite_seleccionado.habilita_cvd == true)
      ) {
        this.columnasExternas = 6;
      } else {
        this.columnasExternas = 6;
      }
    },
    agregar() {
      if (this.reimprimirEfectivo != null) {
        return this.mostrarModalCompletados();
      }

      if (
        !this.existeTasa(this.tramite_seleccionado.id_item) &&
        this.warehouse.length == 10
      ) {
        return this.mostrarModalLimiteTramites();
      } else if (
        this.existeTramiteYTasaSeleccionado(
          this.tramite_seleccionado.id,
          this.tramite_seleccionado.id_item
        ) ||
        this.existeTramiteYTasaCompletado(
          this.tramite_seleccionado.id,
          this.tramite_seleccionado.id_item
        )
      ) {
        return this.mostrarModalTramiteExistente();
      }
      let tramiteSeleccionado = this.tramite_seleccionado
      this.reqAddProcedure(
        JSON.parse(JSON.stringify(this.tramite_seleccionado))
      ).then((resp) => {
        if (resp === false) {
          this.mostrarModal(this.tramite_seleccionado);
        }
        if (resp == undefined) {
          if (
            tramiteSeleccionado.tipo == "Tasa" &&
            tramiteSeleccionado.formulario.campos.length == 0
          ) {
            let tramite = this.warehouse.find(
              (element) => element.id == tramiteSeleccionado.id
            );
            let posicion = this.warehouse.indexOf(tramite);
            this.validarTramite(posicion);
            this.agregarTramiteCompledo(
              JSON.parse(JSON.stringify(tramiteSeleccionado))
            );
          }
        }
      });
    },
  },
});
</script>

