<script>
import Vue from "vue";
import TramiteCoincidente from "./TramiteCoincidente";
import { mapActions, mapState } from "vuex";
import { tutorialesUrl } from "../../../services/paths.js";

export default Vue.extend({
  name: "Buscador",
  components: {
    TramiteCoincidente,
  },
  data: () => ({
    modalTutoriales: false,
    ruta_tutoriales: tutorialesUrl,
    pagina: 1,
    tramitesFiltrados: [],
    busqueda: "",
    loading: false,
    loader: null,
    page: 1,
    expand: false,
    advanceData: [],
    search: "",
    textoBusqueda: "",
    LoadTramite: false,
