    ...mapState("Listado-tramites", ["tramites", "errorConexion"]),
    // ...mapGetters("Listado-tramites", ["existenTramites"]),
    paginacion() {
      const items = [...this.tramitesFiltrados];
      const result = new Array(Math.ceil(items.length / 4))
        .fill()
        .map((_) => items.splice(0, 4));
      return result;
    },
  },

  methods: {
    ...mapActions("Listado-tramites", ["extraerTramites"]),

    contraer() {
      this.expand = false;
    },
    next(page) {},
    busquedaTramites() {
      this.textoBusqueda = this.busqueda;
      this.loading = true;

      setTimeout(() => {
        this.loading = false;
        this.expand = true;
      }, 500);

      this.tramitesFiltrados = [];
      if (!this.busqueda) {
        return;
      }

      this.tramitesPorFiltrar = [...this.tramites];

      let palabras_omitidas = [
        // "a",
        // "o",
        // "de",
        // "del",
        // "un",
        // "una",
        // "uno",
        // "el",
        // "la",
        // "los",
        // "las",
        // "es",
        // "son",
        // "con",
        // "por",
        // "para",
      ];
      this.tramitesFiltrados = this.tramitesPorFiltrar.filter(
        (tramite) => tramite.titulo == this.busqueda
      );

      if (this.tramitesFiltrados.length == 1) {
        return;
      }

      const palabras = this.busqueda
        .toLowerCase()
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "")
        .split(" ")
        .filter((palabra) => !palabras_omitidas.includes(palabra))
        .join(" ");

      // this.tramitesFiltrados = this.tramitesPorFiltrar.filter(
      //   (tramite) =>
      //     tramite.titulo
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .split(" ")
      //       .some((titulo) => palabras.includes(titulo)) ||
      //     tramite.descripcion
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .split(" ")
      //       .some((descripcion) => palabras.includes(descripcion))
      // );

      let arrayBusqueda = palabras.split(" ");
      arrayBusqueda.forEach(
        (element) =>
          (this.tramitesFiltrados = this.tramitesPorFiltrar.filter(
            (tramite) =>
              tramite.titulo
                .toLowerCase()
                .normalize("NFD")
                .replace(/[\u0300-\u036f]/g, "")
                .includes(element) ||
              tramite.descripcion
                .toLowerCase()
                .normalize("NFD")
                .replace(/[\u0300-\u036f]/g, "")
                .includes(element)
          ))
      );

      // const palabras = this.busqueda
      //   .toLowerCase()
      //   .normalize("NFD")
      //   .replace(/[\u0300-\u036f]/g, "")
      //   .split(" ")
      //   .filter((palabra) => !palabras_omitidas.includes(palabra));

      // const palabrasString = this.busqueda
      //   .toLowerCase()
      //   .normalize("NFD")
      //   .replace(/[\u0300-\u036f]/g, "")
      //   .split(" ")
      //   .filter((palabra) => !palabras_omitidas.includes(palabra))
      //   .join(" ");

      // const tramitesArray = this.tramitesPorFiltrar.filter(
      //   (tramite) =>
      //     tramite.titulo
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .split(" ")
      //       .some((titulo) => palabras.includes(titulo)) ||
      //     tramite.descripcion
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .split(" ")
      //       .some((descripcion) => palabras.includes(descripcion))
      // );

      // const tramitesString = this.tramitesPorFiltrar.filter(
      //   (tramite) =>
      //     tramite.titulo
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .includes(palabrasString) ||
      //     tramite.descripcion
      //       .toLowerCase()
      //       .normalize("NFD")
      //       .replace(/[\u0300-\u036f]/g, "")
      //       .includes(palabrasString)
      // );

      // var ids = new Set(tramitesArray.map((tramite) => tramite.id));

      // this.tramitesFiltrados = [
      //   ...tramitesArray,
      //   ...tramitesString.filter((tramite) => !ids.has(tramite.id)),
      // ];

      this.pagina = 1;
    },

    text: (procedures) => procedures.titulo,

    getAdvenceData() {
      this.advanceData = [...this.tramites];
      let filteredDataBySearch = [];
      if (this.search) {
        filteredDataBySearch = this.advanceData.filter(
          (obj) => obj.titulo === this.search
        );
        this.advanceData = filteredDataBySearch;
      }
    },
  },
});
</script>

