<template>
  <div class="panelsheader usrfrm">
    <v-expansion-panels v-model="panel">
      <v-expansion-panel @click="cerrarPanel()">
        <v-expansion-panel-header id="userForm"
          >Datos del usuario</v-expansion-panel-header
        >
        <v-expansion-panel-content>
          <UserForm ref="userform" :showSnackbar="snackbar" />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import Vue from "vue";
import UserForm from "./UserForm";
import { mapState, mapActions } from "vuex";

export default Vue.extend({
  /* TODO: * */
  name: "UserSection",
  computed: {
    ...mapState("Tramites-seleccionados", ["userFormValidado"]),
  },
  components: {
    UserForm,
  },
  data: () => ({
    panel: 0,
    snackbar: false,
    mensaje: "",
  }),

  methods: {
    cerrarPanel() {
      this.$refs.userform.validateUserForm();
      if (this.$refs.userform.validateUserForm() == false) {
        this.snackbar = true;
        this.panel = 1;
      }
    },
  },
});
</script>

