<template>
    <v-breadcrumbs :items="breadcrumbList" large class="pa-0" divider=">">
        <v-breadcrumbs-item
            slot="item"
            slot-scope="{ item }"
            exact
            :disabled="!item.link"
            :to="item.link"
            :text="item.name"
        >
            {{
            item.parameter ? `${item.name} - ${$route.params.id}` : item.name
            }}
        </v-breadcrumbs-item>
    </v-breadcrumbs>
</template>

<script>
import Vue from "vue";

export default Vue.extend({
  name: "Breadcrumb",
  data() {
    return {
      breadcrumbList: []
    };
  },
  mounted() {
    this.updateList();
  },
  watch: {
    $route() {
      this.updateList();
    }
  },
  methods: {
   
    routeTo(pRouteTo) {
      if (this.breadcrumbList[pRouteTo].link) {
        this.$router.push(this.breadcrumbList[pRouteTo].link);
      }
    },
    updateList() {
      this.breadcrumbList = this.$route.meta.breadcrumb;
    }
  }
});
</script>
