<template>
  <div>
    <v-row class="pt-3">
      <v-col cols="12" class="pb-0 pt-0">
        <v-card elevation="2" class="text-xs-center">
          <v-form ref="form" class="px-3">
            <v-row class="pt-1 px-3 pb-1">
              <v-col cols="12" xs="12" class="px-2 py-0">
                <v-row class="mx-1">
                  <v-col
                    cols="4"
                    md="4"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Trámite
                    </label>
                  </v-col>
                  <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                    <v-autocomplete
                      v-model="idTramiteSeleccionado"
                      :items="tramites"
                      item-text="titulo"
                      item-value="id"
                      dense
                      outlined
                      prepend-icon="search"
                      @change="changeTramite()"
                      append-icon
                    >
                    </v-autocomplete>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12" xs="12" sm="4" md="3" class="px-2 py-0">
                <v-row class="mx-1">
                  <v-col
                    cols="4"
                    md="4"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Código
                    </label>
                  </v-col>
                  <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                    <v-text-field
                      v-model="idSeleccionado"
                      disabled
                      @keypress="soloLetras($event)"
                      outlined
                      dense
                    >
                    </v-text-field>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12" xs="12" sm="4" md="3" class="px-2 py-0">
                <v-row class="mx-1">
                  <v-col
                    cols="4"
                    md="4"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Solicitud
                    </label>
                  </v-col>
                  <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                    <v-select
                      v-model="solicitudSeleccionada"
                      :items="tipoSolicitud"
                      outlined
                      :disabled="habilitarSolicitud"
                      dense
                      @change="changeSelect()"
                      :rules="requiredRule"
                      required
                    >
                    </v-select>
                  </v-col>
                </v-row>
              </v-col>
              <v-col cols="12" xs="12" sm="4" md="3" class="px-2 py-0">
                <v-row class="mx-1">
                  <v-col
                    cols="4"
                    md="4"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      TIPO
                    </label>
                  </v-col>
                  <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                    <v-select
                      v-model="tipoSeleccionado"
                      :items="datosAModificar"
                      outlined
                      dense
                      :rules="requiredRule"
                      required
                      @change="changeSelect()"
                    >
                    </v-select>
                  </v-col>
                </v-row>
              </v-col>
              <v-col
                cols="12"
                xs="12"
                md="3"
                class="d-flex justify-center align-center"
              >
                <v-row class="d-flex justify-end pt-7">
                  <v-btn
                    color="primary"
                    class="mb-2"
                    outlined
                    medium
                    :disabled="habilitarBotonModificar"
                    @click="consultar()"
                  >
                    Modificar
                  </v-btn>
                </v-row>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import Vue from "vue";
import { mapActions, mapState } from "vuex";
import { EventBus } from "../../../eventBus.js";

export default Vue.extend({
  name: "EleccionTramite",
  data: () => ({
    opcionesTramite: {},
    datosAModificar: ["Paso a paso", "Requerimientos"],
    tipoSolicitud: ["Presencial"],
    habilitarSolicitud: true,
    idTramiteSeleccionado: null,
    tramiteSeleccionado: {},
    tipoSeleccionado: null,
    solicitudSeleccionada: null,
    requiredRule: [(v) => !!v || "Campo requerido"],
  }),

  created() {
    this.anularPanelEdicionTramite();
    EventBus.$emit("cerrarPanelEdicion");
    this.anularFormularioEleccion();
  },
  computed: {
    ...mapState("Listado-tramites", [
      "tramites",
      "errorConexion",
      "listadoRequerimientos",
      "listadoPasos",
      "pasosyRequerimientos",
      "mostrarCajaEdicionLink",
    ]),

    selectedItem() {
      return this.tramites.find(
        (tramite) => tramite.id == this.idTramiteSeleccionado
      );
    },
    idSeleccionado() {
      return this.selectedItem ? this.selectedItem.id_item : "";
    },
    habilitarBotonModificar() {
      let disabled = true;
      if (
        this.idTramiteSeleccionado !== null &&
        this.solicitudSeleccionada !== null &&
        this.tipoSeleccionado !== null
      ) {
        disabled = false;
      } else {
        disabled = true;
      }
      return disabled;
    },
  },
  methods: {
    ...mapActions("Listado-tramites", [
      "validarFormularioEleccion",
      "anularFormularioEleccion",
      "ocultarEdicionItemTramite",
      "ocultarCreacionItemTramite",
      "ocultarEdicionLink",
      "anularPanelEdicionTramite",
      "habilitarPanelEdicionTramite",
      "guardarTramiteEditable",
      "extraerPasos",
      "filtrarItemsTramiteEditable",
      "extraerRequerimientos",
    ]),

    changeTramite() {
      this.tipoSolicitud = ["Presencial"];
      let tramite = this.tramites.find(
        (tramite) => tramite.id == this.idTramiteSeleccionado
      );
      this.anularPanelEdicionTramite();
      EventBus.$emit("cerrarPanelEdicion");
      this.anularFormularioEleccion();
      this.tipoSeleccionado = null;
      this.solicitudSeleccionada = null;
      this.$refs.form.resetValidation();
      this.habilitarSolicitud = false;

      if (tramite.habilita_cvd == true) {
        this.tipoSolicitud.push("CVD");
      }
      if (tramite.habilita_web == true) {
        this.tipoSolicitud.push("Web");
      }

      this.extraerPasos(this.idTramiteSeleccionado);
      this.extraerRequerimientos(this.idTramiteSeleccionado);
    },
    consultar() {
      this.$refs.form.validate();

      if (this.$refs.form.validate() == true) {
        this.validarFormularioEleccion();
        this.ocultarEdicionItemTramite();
        this.ocultarCreacionItemTramite();
        this.ocultarEdicionLink();
        EventBus.$emit("abrirPanelEdicion");
        let tramiteSelected = this.tramites.find(
          (element) => element.id == this.idTramiteSeleccionado
        );
        let tramite = {};
        tramite.id = this.idTramiteSeleccionado;
        tramite.tipo = this.tipoSeleccionado;
        tramite.solicitud = this.solicitudSeleccionada;
        tramite.id_item = tramiteSelected.id_item;

        this.guardarTramiteEditable(tramite);
        this.filtrarItemsPorSolicitud();
      }
    },
    filtrarOpcionesTramite() {
      if (this.tipoSeleccionado == "Paso a paso") {
        this.extraerPasos(this.idTramiteSeleccionado);
      } else {
        this.extraerRequerimientos(this.idTramiteSeleccionado);
      }
    },
    changeSelect() {
      this.ocultarEdicionItemTramite();
      this.ocultarCreacionItemTramite();
      this.ocultarEdicionLink();
      if (this.tipoSeleccionado != null && this.solicitudSeleccionada != null) {
        this.habilitarPanelEdicionTramite();
        this.filtrarItemsPorSolicitud();
        this.validarFormularioEleccion();

        let tramiteSelected = this.tramites.find(
          (element) => element.id == this.idTramiteSeleccionado
        );
        let tramite = {};
        tramite.id = this.idTramiteSeleccionado;
        tramite.tipo = this.tipoSeleccionado;
        tramite.solicitud = this.solicitudSeleccionada;
        tramite.id_item = tramiteSelected.id_item;
        this.guardarTramiteEditable(tramite);
      }
    },
    filtrarItemsPorSolicitud() {
      if (this.tipoSeleccionado == "Requerimientos") {
        let items = [];
        items = this.listadoRequerimientos.filter(
          (element) => element.tipo == this.solicitudSeleccionada.toUpperCase()
        );
        this.filtrarItemsTramiteEditable(items);
      }
      if (this.tipoSeleccionado == "Paso a paso") {
        let items = [];
        items = this.listadoPasos.filter(
          (element) => element.tipo == this.solicitudSeleccionada.toUpperCase()
        );
        this.filtrarItemsTramiteEditable(items);
      }
    },
    soloLetras: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 65 || (charCode > 90 && charCode < 97) || charCode > 122) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    validarCampoNumerico(param) {
      const regexpNumeros = /\D/;
      if (param.target.value.match(regexpNumeros)) {
        param.target.value = null;
      }
    },
  },
});
</script>
