<template>
  <v-row class="mt-0" id="paymentTable">
    <v-col cols="12" class="pb-0 pt-0">
      <v-row class="mx-titulo">
        <v-col cols="auto" class="titulo-margen px-0">
          <v-card outlined>
            <div class="titulo py-2 px-3">Tabla de pagos</div>
          </v-card>
        </v-col>
      </v-row>
      <v-card elevation="2" class="text-xs-center footerBntCard">
        <!-- loading -->
        <v-data-table
          no-data-text="Completar los formularios."
          :headers="headers"
          :items="tCompletados"
          :items-per-page="-1"
          hide-default-footer
          class="pt-6 pb-0 px-3"
        >
          <template v-slot:[`item.action`]="{ item }">
            <div class="row" v-if="reimprimirEfectivo == null">
              <div class="col-6">
                <v-btn
                  class="px-0 primary"
                  @click="editarItem(item.id)"
                  small
                  v-if="item.conceptos && item.conceptos != ''"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
              </div>
              <div class="col-6">
                <v-btn class="px-0 error" @click="eliminarItem(item.id)" small>
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </div>
            </div>
          </template>
        </v-data-table>
        <div v-if="tCompletados.length > 0">
          <hr
            role="separator"
            aria-orientation="horizontal"
            class="mx-4 v-divider theme--light"
          />

          <v-row class="d-flex py-4 mx-4 justify-md-end">
            <div class="px-4 textTotal">
              <span><b>TOTAL:</b></span>
            </div>
            <div class="px-5 numberTotal">
              <span v-if="!esExento">
                {{ `$ ${calcularTotal.toFixed(2).replace(".", ",")}` }}
              </span>
              <span v-if="esExento">
                {{ `$ 0` }}
              </span>
            </div>
          </v-row>
        </div>

        <hr
          role="separator"
          aria-orientation="horizontal"
          class="mx-4 v-divider theme--light"
        />

        <div class="main mx-4 justify-around">
          <v-btn
            tile
            outlined
            color="primary"
            :disabled="botonLimpiar"
            class="no-text-transforms boton my-3"
            @click="limpiarTablaPagos()"
          >
            {{ txtBtnLimpiar }}
          </v-btn>
          <v-btn
            v-if="reimprimirEfectivo == null"
            tile
            outlined
            color="primary"
            class="no-text-transforms boton my-3"
            @click="imprimir()"
          >
            <span
              v-if="
                esExento || esDiferido ? (msg = 'Imprimir') : (msg = 'Generar')
              "
            >
              {{ msg }}
            </span>
          </v-btn>
          <div v-if="liquidacion != null || reimprimirEfectivo != null">
            <v-menu
              transition="slide-x-transition"
              origin="center center"
              offset-y
              left
            >
              <template v-slot:activator="{ on }">
                <v-btn
                  tile
                  outlined
                  color="primary"
                  class="no-text-transforms boton my-3"
                  v-on="on"
                >
                  Imprimir
                </v-btn>
              </template>

              <v-list>
                <v-list-item
                  v-if="reimprimirEfectivo != null && liquidacion == null"
                  @click="
                    reimprimirPDFefectivoLocal(
                      JSON.parse(JSON.stringify(reimprimirEfectivo))
                    )
                  "
                >
                  <v-list-item-title class="font-title">
                    Imprimir para abonar por cajero
                  </v-list-item-title>
                </v-list-item>
                <v-list-item
                  v-if="
                    liquidacion != null && reimprimirEfectivoClickeado == null
                  "
                  @click="
                    reimprimirPDFpagoOnline(
                      JSON.parse(JSON.stringify(liquidacion))
                    )
                  "
                >
                  <v-list-item-title class="font-title">
                    Imprimir tasa pagada online
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </div>
        </div>
      </v-card>
    </v-col>
    <ModalMedioDePago></ModalMedioDePago>
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapGetters, mapState, mapActions } from "vuex";
import ModalMedioDePago from "../utils/ModalMedioDePago.vue";
import { EventBus } from "../../eventBus";

export default Vue.extend({
  name: "PaymentTable",

  components: {
    ModalMedioDePago,
  },
  data: () => ({
    // Tabla de valores
    headers: [
      { text: "ACCIONES", sortable: false, value: "action" },
      {
        text: "AÑO",
        sortable: false,
        align: "left",
        value: "anio",
      },
      { text: "SERIE", sortable: false, value: "serie" },
      { text: "TIPO", sortable: false, value: "tipo" },
      { text: "FORMULARIO", sortable: false, value: "formulario" },
      { text: "CÓDIGO", sortable: false, value: "codigo" },
      { text: "TRAMITE A PAGAR", sortable: false, value: "tramite" },
      { text: "CONCEPTOS INVOLUCRADOS", sortable: false, value: "conceptos" },
      {
        text: "TASA A PAGAR POR TRAMITE",
        sortable: false,
        align: "center",
        value: "tasa",
        width: 140,
      },
    ],
    botonLimpiar: true,
    user: {
      name: "Prueba",
      name2: "Prueba2",
    },
    esExento: false,
    esDiferido: false,
    msg: null,
  }),
  mounted() {
    this.habilitarBotonLimpiar();
  },
  beforeUpdate() {
    this.habilitarBotonLimpiar();
  },
  created() {
    this.busExento();
    this.busDiferido();
  },
  updated() {
    this.busExento();
    this.busDiferido();
  },
  computed: {
    ...mapGetters("Tramites-completados", {
      calcularTotal: "calcularTotal",
      tCompletados: "formatearTramitesCompletados",
      reimprimirEfectivo: "reimprimirEfectivo",
      liquidacion: "liquidacion",
      reimprimirEfectivoClickeado: "reimprimirEfectivoClickeado",
    }),

    ...mapState("Tramites-seleccionados", [
      "warehouse",
      "datosIngresadosPorUsuario",
      "tasas_seleccionadas",
    ]),

    txtBtnLimpiar() {
      return this.reimprimirEfectivo == null ? "Limpiar" : "Nueva tasa";
    },
  },
  methods: {
    ...mapActions("Tramites-completados", [
      "editarTramiteCompledo",
      "eliminarTramiteCompledo",
      "reimprimirPDFefectivo",
      "reimprimirPDFpagoOnline",
      "reimprimirClickeado",
      "persistirTasas",
      "persistirPublicidadDirecta",
    ]),
    ...mapActions("Tramites-completados", ["agregarTramiteCompledo"]),
    ...mapActions("Tramites-seleccionados", {
      eliminarTramiteWh: "eliminarTramiteWh",
      editarTramiteWh: "editarTramiteWh",
    }),
    ...mapActions("Tramites-seleccionados", [
      "invalidarInscriptionForm",
      "validarTramite",
      "validateUserform",
      "resetDatosPublicidad",
    ]),
    ...mapActions("Tramites-completados", [
      "mostrarModalMedioDePago",
      "limpiarTramitesCompletados",
    ]),
    reimprimirPDFefectivoLocal(reimpresion) {
      this.reimprimirPDFefectivo(reimpresion);
      this.reimprimirClickeado();
    },
    limpiarTablaPagos() {
      if (this.warehouse[0].tipo == "Publicidad Directa") {
        this.resetDatosPublicidad();
      }
      this.tCompletados.forEach((element) => this.eliminarItem(element.id));
      this.limpiarTramitesCompletados();
    },
    editarItem(id_tramite) {
      this.editarTramiteWh(id_tramite);
      this.eliminarTramiteCompledo(id_tramite);
      this.invalidarInscriptionForm();
    },
    eliminarItem(id_tramite) {
      if (this.warehouse[0].tipo == "Publicidad Directa") {
        this.resetDatosPublicidad();
      }
      this.eliminarTramiteWh(id_tramite);
      this.eliminarTramiteCompledo(id_tramite);
      this.invalidarInscriptionForm();
    },
    habilitarBotonLimpiar() {
      if (this.tCompletados.length != 0) {
        this.botonLimpiar = false;
      } else {
        this.botonLimpiar = true;
      }
    },
    busExento() {
      EventBus.$on("esExento", (data) => {
        this.esExento = data;
      });
    },
    busDiferido() {
      EventBus.$on("esDiferido", (data) => {
        this.esDiferido = data;
      });
    },
    usuarioGuardado() {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve();
        }, 500);
      });
    },
    async pagoEfectivoDirecto() {
      await this.usuarioGuardado();
      let MedioDePago = 20;
      let datosPorConsola = this.datosIngresadosPorUsuario;
      datosPorConsola.medioPago = MedioDePago;
      try {
        if (Array.isArray(datosPorConsola.items)) {
          this.persistirTasas(datosPorConsola);
        } else {
          this.persistirPublicidadDirecta(datosPorConsola);
        }
      } catch (error) {
        console.log(error);
        return;
      }
    },
    imprimir() {
      this.validateUserform();
      if (this.esExento || this.esDiferido) {
        this.pagoEfectivoDirecto();
      }
    },
  },
});
</script>

<style>
.v-btn:not(.v-btn--round).v-size--small {
  min-width: 30px;
}
</style>

