  // ? JSON.parse(localStorage.getItem("version_app"))
  // : null,
  userForm: localStorage.getItem("datos_usuario")
    ? { ...JSON.parse(localStorage.getItem("datos_usuario")) }
    : {},
  liquidacion: [],
  errorConexion: {
    tipo_usuario: false,
    tipo_inscripcion: false,
    departamento: false,
    interes_legitimo: false,
  },
  tramite_seleccionado: {},
  tasas_seleccionadas: [],
  numeroLiquidacion: null,
  listadoLiquidaciones: {},
  liquidacionBuscada: [],
  textoDescripcionFormularios:
    "<p>Con el propósito de facilitar la gestión de los trámites se pone a disposición de los usuarios registrales los formularios normatizados.</p><p>Los mismos se pueden descargar, completar e imprimir desde su computadora.</p><p>Se recuerda que la impresión debe realizarse en una sola hoja.</p>",
  publicidad_seleccionada: {},
  datosIngresadosPorUsuario: {},
  inscripcion_seleccionada: {},
  userFormValidado: false,
  inscriptionFormValidado: false,
