function compareVersion( v1, v2 ) {
  v1 = v1.split('.');
  v2 = v2.split('.');
  var len = Math.max( v1.length, v2.length );
  while ( v1.length < len ) {
    v1.push( '0' );
  }
  while ( v2.length < len ) {
    v2.push( '0' );
  }
  for ( var i = 0; i < len; i++ ) {
    var n1 = parseInt( v1[i] );
    var n2 = parseInt( v2[i] );
    if ( n1 > n2 ) {
      return 1;
    } else if ( n1 < n2 ) {
      return 2;
    }
  }
  return 0;
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
