<template>
  <v-row class="pt-3">
    <v-col cols="12" class="pb-0 pt-0">
      <v-data-table
        :no-data-text="textoSinFormularios"
        :headers="headers"
        :items="dataForms"
        :items-per-page="5"
        class="elevation-1 pa-3"
      >
        <template v-slot:[`item.anverso`]="{ item }">
          <div v-if="item.anverso" class="p-2">
            <a :href="`./FormulariosPDF/${item.anverso}`" target="_blank">
              <v-img
                :src="require('@/assets/img/img_pdf.gif')"
                height="21px"
                width="21px"
              ></v-img>
            </a>
          </div>
        </template>
        <template v-slot:[`body.append`]="{}">
          <tr>
            <td colspan="12" class="tdTexto">
              <div class="d-flex align-items-center">
                <div class="textoPiePagina">
                  La totalidad de los formularios para descarga se encuentran en
                  su respectiva sección al pie de página
                </div>
              </div>
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapGetters } from "vuex";

export default Vue.extend({
  name: "FormTable",
  data: () => ({
    panel: 0,
    textoSinFormularios: "",
    headers: [
      { text: "FORMULARIOS / ANEXOS", value: "nombre", align: "center" },
      { text: "DESCRIPCION", value: "descripcion", align: "center" },
      { text: "ANVERSO", sortable: false, value: "anverso" },
    ],
  }),
  created() {
    if (this.tramitesActivos.length == 0) {
      this.textoSinFormularios = "Debe seleccionar un trámite.";
    } else {
      this.textoSinFormularios =
        "El trámite seleccionado no posee formularios asociados.";
    }
  },
  methods: {
    getFile(file) {
      // const str = "../../assets/FormulariosPDF/" + file;
      console.log(file);

      window.open("./FormulariosPDF/" + file, "_blank");
      // return str;
    },
  },
  computed: {
    ...mapGetters("Tramites-seleccionados", ["tramitesActivos"]),
    dataForms() {
      const objetin = [];
      const formulario_nuevo = this.tramitesActivos
        .filter((tramite) => tramite.formularios_adjuntos.length !== 0)
        .map((tramite) => {
          const { id: id_tasa, formularios_adjuntos } = tramite;
          formularios_adjuntos.map((obj) => {
            const found = objetin.some((el) => el.id === obj.id);
            if (!found) objetin.push({ id_tasa, ...obj });
          });
        });
      return objetin;
    },
  },
});
</script>

