<template>
  <v-row>
    <v-col cols="12" xs="12" sm="12" class="pb-0">
      <v-card outlined elevation="2" height="100%">
        <v-form ref="formu">
          <v-row>
            <v-col cols="12">
              <v-alert
                v-model="alert"
                text
                outlined
                color="red"
                class="py-2 ml-7 mr-7 mb-2 mt-4 text-center body-2"
                close-icon="close"
                dismissible
              >
                {{mensajeAlert}}
              </v-alert>
              <v-row class="d-flex justify-space-between align-center mx-4">
                <v-col cols="12" sm="4" class="pb-0 pt-4 px-3 completo">
                  <v-text-field
                    v-model="comprobante"
                    @keyup="validarNumeros(comprobante)"
                    @keypress="onlyNumber($event)"
                    maxlength="10"
                    label="Número Único"
                    outlined
                    required
                    :rules="requiredRule"
                    dense
                    :disabled="activar"
                    class="centered-input shrink unico"
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="4"
                  class="d-flex justify-space-between px-3 justify-sm-end py-2 px-0"
                >
                  <v-btn
                    outlined
                    color="primary"
                    class="no-text-transforms mr-4 boton"
                    :disabled="faltaReponer == null"
                    @click="limpiar()"
                  >
                    limpiar
                    <v-icon color="primary" size="14"> </v-icon>
                  </v-btn>
                  <v-btn
                    outlined
                    color="primary"
                    class="no-text-transforms boton"
                    @click="buscar()"
                    :disabled="activar"
                    :loading="buscando"
                  >
                    buscar
                    <v-icon color="primary" size="14"> </v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-snackbar
                v-model="snackbar"
                centered
                color="primary"
                timeout="5000 "
                outlined
                text
              >
                <v-row class="snackbar">
                  <v-col>
                    ¿Confirma que desea eliminar la búsqueda? <br/> Si no se generó la tasa, se perderán las modificaciones <br/> que haya realizado.
                  </v-col>
                </v-row>
                <v-row class="snackbar">
                  <v-col>
                    <v-btn
                      class="botonSnackbar"
                      text
                      color="primary"
                      @click="anularSnackbar()"
                    >
                      NO
                    </v-btn>
                    <v-btn
                      class="botonSnackbar"
                      text
                      color="primary"
                      @click="confirmarLimpiar()"
                    >
                      SI
                    </v-btn>
                  </v-col>
                </v-row>
              </v-snackbar>
            </v-col>
          </v-row>
        </v-form>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapState, mapGetters, mapActions } from "vuex";

export default Vue.extend({
  name: "busquedaFaltaReponer",
  data: () => ({
    comprobante: null,
    buscando: false,
    activar: false,
    snackbar: false,
    alert: false,
    mensajeAlert: "",
    mensaje:"",
    requiredRule: [(v) => !!v || "Campo requerido"],
  }),
  computed: {
    ...mapState("Tramites-completados", ["faltaReponer", "errorConexion"]),
    ...mapGetters("Tramites-completados", ["validarComplementaria"]),
    ...mapState("Modal", ["modalPublicidadDirecta"]),
    ...mapState("Listado-tramites", ["faltaReponerCargada"]),
    resultadoBusqueda() {
     return this.faltaReponer;
    },
    mostrarError(){
      return this.errorConexion;
    }
  },
  methods: {
    ...mapActions("Tramites-completados", [
      'consultarFaltaReponer',
      "resetFaltaReponer",
      "agregarTramiteComplementaria",
      "limpiarTramitesCompletados", "ocultarErrorConexion", "mostrarErrorConexion", "setFaltaReponerCargada"
    ]),
    ...mapActions("Modal", ["mostrarModalPublicidadDirecta"]),
    ...mapActions("Tramites-seleccionados", [
      "validarTramite",
      "resetearWarehouse",
    ]),
    validarNumeros(param) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        this.comprobante = null;
      }
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },

    buscar() {
      this.buscando = true;
      this.resetFaltaReponer();
      this.resetearWarehouse();
      this.limpiarTramitesCompletados();
      this.$refs.formu.validate();
      this.setFaltaReponerCargada(false)
      if(this.$refs.formu.validate() == true){
        this.consultarFaltaReponer(this.comprobante); 
      }else{
        this.buscando = false;
      }
    },
    anularSnackbar() {
      this.snackbar = false;
    },
    limpiar() {
      this.snackbar = true;       
    },
    confirmarLimpiar() {
        this.snackbar = false;
        this.comprobante = null;
        this.activar = false;
        this.alert = false;
        this.resetearWarehouse();
        this.limpiarTramitesCompletados();
        this.resetFaltaReponer();
        this.$refs.formu.resetValidation();
    },
  },
  watch: {
    resultadoBusqueda() {
      if(this.faltaReponer != null) {
        if(this.faltaReponer.length == 0 ){
          this.alert = true;
          this.buscando = false;
          this.activar = false;
          this.mensajeAlert = "No hay datos cargados para el número ingresado."
        }else{
          this.alert = false;
          this.buscando = false;
          this.activar = true
          this.mensajeAlert = "";
          let year = new Date().getFullYear();
          let ultimo = this.faltaReponer.length - 1;
          if(this.faltaReponer[ultimo].tasa_total_exenta != 0){
            this.alert = true;
            this.mensajeAlert = "Disculpe, no puede generar una tasa Falta Reponer si es exento."
          }
          if(this.faltaReponer[ultimo].items.find(element => element.anio != year)){
            this.alert = true;
            this.mensajeAlert = "El número único consultado posee ítems de años anteriores. Puede actualizarlos eliminándolos y agregándolos nuevamente."
          }  
        }
      }
    },
    mostrarError(){
      if(this.errorConexion == true){
        this.buscando = false;
      }
    }
  },

  beforeDestroy() {
    this.resetearWarehouse();
    this.resetFaltaReponer();
    this.limpiarTramitesCompletados();
  },
});
</script>
<style scoped>
  .snackbar {
    text-align: center;
    display: flex;
    align-items: center;
  }
  .botonSnackbar {
    color: red !important;
  }
</style>
