<template>
  <div id="inicioPagina">
    <!-- LOADING -->
    <div
      v-if="!this.existenNovedades"
      class="final d-flex align-center justify-center px-0 pb-3 pt-3"
    >
      <v-card
        outlined
        height="100%"
        min-height="200px"
        width="100%"
        class="d-flex align-center justify-center py-0"
      >
        <div>
          <v-progress-circular
            :size="40"
            :width="5"
            color="primary"
            indeterminate
          ></v-progress-circular>
        </div>
      </v-card>
    </div>
    
    <!-- ADMIN -->
    <v-row class="anchoFijo mt-2" v-if="(usuarioInterno && perfilAdmin) && this.existenNovedades" >
      <v-col class="rowAgregarNovedad pb-0" cols="12" md="12" sm="12" v-if="!mostrarTinymce">
        <v-row>
          <v-btn
            small
            fab
            depressed
            elevation="3"
            color="primary"
            @click="mostrarCrearNovedad()"
          >
            <v-icon>add</v-icon>
          </v-btn>
          <v-divider 
            class="linea mt-5"
          ></v-divider>
          <label class="form-inmueble px-3 py-2 cursorPointer" @click="mostrarCrearNovedad()">
            Agregar Novedad
          </label>
        </v-row>
      </v-col>
    </v-row>  

    <!-- SIN RESULTADOS -->
    <v-row class="d-flex rowSuperior justify-space-between" v-if="this.existenNovedades">
        <v-col cols="12" sm="4" class="d-flex justify-start align-self-start campoRowSuperior cajaRowSuperior">
          <v-select
            label="Ordenar por fecha"
            v-model="ordenFecha"
            item-text="text"
            dense
            outlined
            hide-details
            background-color="#fff"
            :items="opcionesFecha"
            @change="OrdenDeFechas()"
          >
          </v-select>
        </v-col>
        <v-col cols="12" sm="4" class="d-flex justify-end campoRowSuperior cajaRowSuperior mt-0" id="contenedorNovedades">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            offset-x
            :nudge-left="290"
            min-width="auto"
            class="px-2"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="picker"
                label="Buscar por fecha"
                prepend-icon="mdi-calendar"
                dense
                outlined
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              ref="picker"
              v-model="date"
              :max="new Date().toISOString().substr(0, 10)"
              scrollable
              @input="menu = false"
              @change="buscarPorFecha()"
              class="ocultarResponsive"
            ></v-date-picker>
          </v-menu>
          <v-btn
            class="botonCancelarBusqueda mt-1"
            color="primary"
            dark
            @click="anularBusqueda()"
            v-if="cancelarBusqueda"
          >
            <v-icon
            dark
            >
              mdi-arrow-left
            </v-icon>
          </v-btn>
        </v-col>      
    </v-row>
    <div
      v-if="novedades.length === 0 && !mostrarTinymce && this.existenNovedades"
      width=100%
      min-height="100%"
      class="d-flex align-center justify-center"
      :class="perfilAdmin ? 'sinResultados' : 'final'"
    >
      <v-card
        outlined
        height="100%"
        min-height="250px"
        width="100%"
        class="final  d-flex align-center justify-center"
      >
        <p>No se encontraron resultados</p>
      </v-card>
    </div>

    <!-- buscador -->
    <div class="contenedorNovedades" v-if="novedades.length != 0 && this.existenNovedades">
      <v-form ref="formu">
        <v-card v-if="mostrarTinymce" class="mt-6 cardCrearNovedad">
          <v-row class="ml-0 mx-titulo">
            <v-col cols="auto" class=" px-0">
              <v-card outlined>
                <div class="titulo py-2 px-3">{{titleEdition}}</div>
              </v-card>
            </v-col>
          </v-row>
          <v-text-field 
            v-model="tituloNuevaNovedad"
            outlined
            required
            maxlength="250"
            :rules="requiredRule"
            autocomplete="off"
            label="Titulo"
            id="title"
            class="py-2"
          > 
          </v-text-field>
         <!-- <tinymce id="tinymce" v-model="textoNuevaNovedad" language='es' toolbar2="false">          
          </tinymce>
  -->       <editor
              id="tinymce"
              api-key="no-api-key"
              v-model="textoNuevaNovedad"
              language= "es"
              :init="{
                selector: 'editor',
                height: 200,
                menubar: false,
                plugins: [
                  'advlist autolink lists link image charmap print preview anchor',
                  'searchreplace visualblocks code fullscreen',
                  'insertdatetime media table paste code help wordcount'
                ],
                file_picker_types: 'image',
                automatic_uploads: true,
                /* and here's our custom image picker*/
                toolbar:
                  'undo redo | image | formatselect | bold italic backcolor | \
                  alignleft aligncenter alignright alignjustify | \
                  bullist numlist outdent indent | removeformat | help'
              }"
          />
          <v-row class="errorNovedad" v-if="errorContenido">
            {{errorContenido}}
          </v-row>
          <v-row class="rowBotonesEdicion">
            <button class="editionButton boton" @click="ocultarTinymce()">
              Cancelar
            </button>
            <button class="editionButton boton" @click="confirmarNovedad()">
              Confirmar
            </button>
          </v-row>  
        </v-card> 
        <v-row class=" contenedorPanel">
          <div class="panelsheader contenedorPanel">
            <v-expansion-panels accordion>
              <v-expansion-panel
                class="mt-2"
                v-for="novedad in pagination[page - 1]"
                :key="novedad.id"
              >
              
                <v-expansion-panel-header id="userForm">
                  <div>
                    <span>{{novedad.nombre}}</span> - 
                    <span> {{ novedad.fecha }} </span>
                  </div>
                  <div class="p-2 mr-3 d-flex justify-end" v-if="(usuarioInterno && perfilAdmin)">
                    <v-btn
                      class="px-0 mr-2 primary botonEdicion"
                      @click="mostrarEditarNovedad(novedad)"
                      small
                     
                    >
                      <v-icon small>
                        edit
                      </v-icon>
                    </v-btn>
                    <v-btn
                      class="px-0 error botonEdicion"
                      @click="mostrarEliminarNovedad(novedad)"
                      small
                      @click.native.stop
                    >
                      <v-icon small>
                        delete
                      </v-icon>
                    </v-btn>
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content class="contenedorCardMensaje" >
                  <v-card class="cardMensajeNovedad">
                    <span  v-html="novedad.contenido"></span>
                  </v-card>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels> 
          </div> 
          
        </v-row>
      </v-form>
    </div>  
      <v-row class="rowInferior" v-if="this.novedades.length != 0 && this.existenNovedades">
        <v-col
          cols="12"
          sm="2"
          md="4"
          class="cantidadResultados ocultarResponsive"
        >
          {{ this.resultado }}
        </v-col>
        <v-col cols="12" xs="12" sm="8" md="4" class="paginacion d-flex align-center justify-center">
          <v-pagination
            v-model="page"
            :length="Math.ceil(this.novedades.length / this.cantidadAMostrar)"
            total-visible="5"
            @input="next"
            color="primary"
          ></v-pagination>
        </v-col>
        <v-col cols="12" sm="2" md="4" class="selectCantidad ocultarResponsive">
          <v-row class="container_show d-flex justify-end">
            <v-col cols="12" sm="12" md="12" class="px-0 py-0 d-flex dimension">
              <v-select
                label="Mostrar de a..."
                v-model="cantidadAMostrar"
                item-text="text"
                dense
                outlined
                hide-details
                background-color="#fff"
                :items="opciones"
                @change="resetPage()"
              >
              </v-select>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-snackbar
        v-model="snackbar" 
        centered
        :color="colorSnackbar"
        :timeout="timeSnackbar "
        outlined
        text
        :height="heightSnackbar"
      >
        <v-row class="snackbar">
          <v-col>
            {{mensajeSnackbar}}
          </v-col>  
        </v-row>
        <v-row 
          v-if="botonAceptarSnackbar"
          class="snackbar"
        >
          <v-col>
            <v-btn
              class="botonSnackbar"
              text
              @click="snackbar = false"
            >
              ACEPTAR
            </v-btn>
          </v-col>
        </v-row>
        <v-row 
          v-if="botonesConfirmarSnackbar"
          class="snackbar"
        >
          <v-col>
            <v-btn
              class="botonSnackbar"
              text
              color="primary"
              @click="anularSnackbar()"
            >
              NO
            </v-btn>
            <v-btn
              class="botonSnackbar"
              text
              color="primary"
              @click="borrarNovedad()"
            >
              SI
            </v-btn>
          </v-col>
        </v-row>  
      </v-snackbar>     
   </div>
</template>

<script>
import Vue from "vue";
import { mapActions, mapState, mapGetters } from "vuex";
import Editor from '@tinymce/tinymce-vue'

export default Vue.extend({
 
 components: {
     'editor': Editor
   },
  name: "NovedadesSection",
  data: vm => ({
    novedades: [],
    page:1,
    snackbar:false, 
    colorSnackbar: null,
    botonAceptarSnackbar: false,
    botonesConfirmarSnackbar:false,
    heightSnackbar:"",
    timeSnackbar:-1,
    mensajeSnackbar: "",
    tituloNuevaNovedad: "",
    textoNuevaNovedad: "",
    idNovedadSeleccionada: null,
    titleEdition:"",
    tinymceActivo: false,
    ordenFecha:"",
    perfilAdmin: false,
    mostrarTinymce:false,
    cancelarBusqueda: false,
    date: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    novedadAEditar:{},
    cantidadAMostrar: 10,
    opciones: [5, 10, 20, 50],
    opcionesFecha:["Recientes", "Anteriores"],
    menu: false,
    picker: null,
    resultado: null,
    errorContenido: "",
    requiredRule: [
      (v) => !!v || "Campo requerido",
      (v) => (v.trim() != "") || "Campo requerido"
    ],
  }),
   computed:{
    ...mapState("Listado-novedades", ["listadoNovedades", "errorConexion"]),
    ...mapState("Logincidi", ["usuarioInterno"]),
    ...mapGetters("Listado-novedades", ["existenNovedades"]),
    pagination() {
      const items = [...this.novedades];
      const result = new Array(Math.ceil(items.length / this.cantidadAMostrar))
        .fill()
        .map((_) => items.splice(0, this.cantidadAMostrar));
      return result;
    }, 
    busquedaNovedades() {
      return this.listadoNovedades;
    },
    errorDeConexion() {
      return this.errorConexion;
    },   
  },
  created() {
    if(!this.existenNovedades) {
      this.extraerNovedades();
    } else {
      this.consultarNovedades()
    }
    if(this.$route.name.includes("admin")){
      this.perfilAdmin = true  
    }
  },
  updated(){
    if(this.tinymceActivo == true){
      tinyMCE.activeEditor.setContent(this.textoNuevaNovedad)
      this.tinymceActivo = false
    }
    if(this.mostrarTinymce) {
      this.validarContenido()
    }
  },
  methods: {
    ...mapActions("Listado-novedades", ["extraerNovedades", "agregarNovedad", "eliminarNovedad",  "crearNovedad", "modificarNovedad"]),

    next(page) {},
    consultarNovedades(){
      this.novedades =[]
      this.listadoNovedades.forEach(element => {
        let elemento = {}
          elemento.id = element.id
          elemento.nombre = element.nombre
          elemento.contenido = element.contenido
          elemento.fecha = (element.fechamodif != null && element.fechamodif != "") ? element.fechamodif : element.fechaalta 
          elemento.date = elemento.fecha.split("/");
          elemento.date = new Date(+elemento.date[2], elemento.date[1] - 1, +elemento.date[0]);
        this.novedades.push(elemento)
      });
      this.novedades.sort(function (a, b) { return (b.date) - (a.date) })
      if(this.novedades.length === 1){
        this.resultado = "Se encontró 1 resultado"
      }else {
        this.resultado = "Se encontraron " + this.novedades.length + " resultados"
      }
    },
    mostrarSnackbar(color, time, height, mensaje, botonAceptar, botonesConfirmar){
      this.snackbar = true;
      this.colorSnackbar = color;
      this.timeSnackbar = time;
      this.heightSnackbar = height;
      this.mensajeSnackbar = mensaje;
      this.botonAceptarSnackbar= botonAceptar;
      this.botonesConfirmarSnackbar= botonesConfirmar;
    },
    OrdenDeFechas() {
      if(this.ordenFecha == "Recientes" || this.ordenFecha == "") {
        this.novedades.sort(function (a, b) { return new Date(b.date) - new Date(a.date) })
      } else {
        this.novedades.sort(function (a, b) { return new Date(a.date) - new Date(b.date) })
      }
    },
    mostrarCrearNovedad(){
      this.errorContenido = ""
      this.mostrarTinymce = true;
      this.titleEdition = "Crear novedad";
      this.tituloNuevaNovedad = "";
      this.textoNuevaNovedad = "";
    },
    mostrarEditarNovedad(param){
      this.errorContenido= ""
      this.tinymceActivo = true;
      this.mostrarTinymce=true;
      this.titleEdition = "Editar novedad";
      this.idNovedadSeleccionada = param.id;
      this.tituloNuevaNovedad = param.nombre;
      this.textoNuevaNovedad = param.contenido;
      this.scroll("contenedorNovedades");
    },
    confirmarNovedad(){
      if(this.titleEdition == "Crear novedad"){
          this.crearNovedad()
      } else{
          this.guardarNovedadModificada()
      }
    },
    crearNovedad(){
      this.$refs.formu.validate()
      let campo = tinyMCE.activeEditor.getContent()
      this.errorContenido = ""
     /*  if (campo.length > 250) {
        this.errorContenido = "Pasó la cantidad Máxima de caracteres"
      } */
      campo = campo.replaceAll('&nbsp;', '')
      campo = campo.replaceAll('<p>', '')
      campo = campo.replaceAll('</p>', '')
      campo = campo.trim()  
      campo = campo.replace(/[\r\n]/gm, '');
      if(campo == "" || this.textoNuevaNovedad == ""){
        this.textoNuevaNovedad = ""
        this.errorContenido = "Campo requerido"
        return
      } 
      if (this.errorContenido || !this.$refs.formu.validate()) {
        return
      }

        let novedad= {}
        novedad.nombre = this.tituloNuevaNovedad
        novedad.contenido = this.textoNuevaNovedad.replaceAll("&nbsp; ", "")
        novedad.contenido = novedad.contenido.replace(/[\r\n]/gm, '');
        try{
          this.agregarNovedad(novedad)
        } catch(error){
          this.mostrarSnackbar("red", 5000, 60,"Hubo un error en la conexión. Por favor intentalo nuevamente.", true, false);
          return
        }
        this.mostrarSnackbar("success", 3000, 30, "La creación se realizó con éxito.", false, false);
        this.actualizarPagina();
        this.ocultarTinymce();
        this.tituloNuevaNovedad= "";
        this.textoNuevaNovedad = "";
      
    },
    validarContenido() {
      let campo = tinyMCE.activeEditor.getContent()
      if (campo.trim() != ""){
        this.errorContenido = ""
      }
    },
    guardarNovedadModificada(){
      let campo = tinyMCE.activeEditor.getContent()
      this.errorContenido = ""
     /*  if (campo.length > 250) {
        this.errorContenido = "Pasó la cantidad Máxima de caracteres"
      } */
      campo = campo.replaceAll('&nbsp;', '')
      campo = campo.replaceAll('<p>', '')
      campo = campo.replaceAll('</p>', '')
      campo = campo.trim()  
      campo = campo.replace(/[\r\n]/gm, '');
      if(campo == "" || this.textoNuevaNovedad == ""){
        this.textoNuevaNovedad = ""
        this.errorContenido = "Campo requerido"
        return
      } 
      if (this.errorContenido || !this.$refs.formu.validate()) {
        return
      }
      let novedad= {}
        novedad.id = this.idNovedadSeleccionada
        novedad.nombre = this.tituloNuevaNovedad.trim()
        novedad.contenido = this.textoNuevaNovedad.replaceAll("&nbsp; ", "")
        novedad.contenido = novedad.contenido.replace(/[\r\n]/gm, '');
        novedad.idUsuario = 5;
        novedad.estadoBaja = "N"
      try{
          this.modificarNovedad(novedad)
        } catch(error){
          this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intentalo nuevamente.", true, false);
          return
        }
        this.mostrarSnackbar("success", 3000, 30, "La modificación se realizó con éxito.", false, false);
        this.actualizarPagina();
        this.ocultarTinymce();
        this.idNovedadSeleccionada= null;
        this.tituloNuevaNovedad= "";
        this.textoNuevaNovedad = "";
      
    },
    mostrarEliminarNovedad(param){
      this.idNovedadSeleccionada = param.id
      this.mostrarSnackbar("primary", 5000, 60, "¿Confirma que desea eliminar la novedad: " + param.nombre + "?", false, true);
    },
    borrarNovedad(){
      try{
        this.eliminarNovedad(this.idNovedadSeleccionada)
      } catch(error){
        this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intentalo nuevamente.", true, false);
        return
      }
      this.mostrarSnackbar("success", 3000, 30, "La eliminación se realizó con éxito.", false, false);
      this.actualizarPagina();
      this.idNovedadSeleccionada = null;
    },
    anularSnackbar(){
      this.snackbar = false;
      this.colorSnackbar = null;
      this.botonAceptarSnackbar = false;
      this.botonesConfirmacionSnackbar = false;
      this.heightSnackbar=null;
      this.timeSnackbar=-1;
      this.mensajeSnackbar = "";
      this.idNovedadSeleccionada =   null;
    },
    scroll(id) {  
      document.getElementById(id).scrollIntoView({
        behavior: "smooth"
      });
    },
    ocultarTinymce(){
      this.mostrarTinymce = false;
      this.tituloNuevaNovedad= "";
      this.textoNuevaNovedad= "";
      this.novedadAEditar = {};
    },
    actualizarPagina(){
      this.consultarNovedades();
      this.OrdenDeFechas();
    },
    resetPage() {
      this.page = 1;
    },

    // Formato de Dia que es utilizado en la region DD/MM/AAAA
    formatDate(date) {
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    anularBusqueda(){
      this.cancelarBusqueda = false
      this.consultarNovedades()
      this.OrdenDeFechas()
      this.picker = null
    },
    buscarPorFecha(){
      this.consultarNovedades()
      let formattedDate = this.formatDate(this.date)
      this.picker = formattedDate
      this.filtrarPorFecha(formattedDate)  
      this.cancelarBusqueda = true
      this.resetPage()
    },
    filtrarPorFecha(formattedDate){
      let listado = this.novedades
      this.novedades = []
      listado.forEach(element =>{
        let fechaElemento = element.fecha
        const [day, month, year] = fechaElemento.split('/')
               
        let dia = 0
        let mes = 0
        if(day.length == 1){
          dia = "0"+day
        }else{
          dia = day
        }
        
        if(month.length == 1){
          mes = "0"+month
        }else{
          mes = month
        }
             
        fechaElemento = `${dia}/${mes}/${year}`
       
        if(fechaElemento == formattedDate){
          this.novedades.push(element)
        }
      })
      this.novedades.sort(function (a, b) { return new Date(b.date) - new Date(a.date) })
      if(this.novedades.length === 1){
        this.resultado = "Se encontró " + this.novedades.length + " resultado"
      }else {
        this.resultado = "Se encontraron " + this.novedades.length + " resultados"
      }
    },
  }, 

  watch: {
    busquedaNovedades() {
      this.consultarNovedades();
    },
    errorDeConexion() {
      if(this.errorConexion == true){
        this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intentalo nuevamente.", true, false);
      }
    }
  }
});


</script>
<style>
  .v-application p {
    margin-bottom: 8px !important;
  }
 
</style>

