<script>
import Vue from "vue";
import { mapState, mapActions, mapGetters } from "vuex";

export default Vue.extend({
  name: "InscriptionForm",

  data: () => ({
    valid: true,
    lazy: false,
    snackbar: false,
    colorSnackbar: "",
    mensaje: "",
    mensajeValidacionDatos: "",
    validacionDatos: false,
    loadingInscripcion: true,
    loadingDepartamento: true,
    loadingInteresLegitimo: true,
    centrarSnack: "",
    darkSnackbar: false,
    requiredRule: [(v) => !!v || "Campo requerido"],
    requiredNumero: [],
    requiredMatricula: [],
    requiredAnio: [],
    requiredFolio: [],
    requiredNumber: [
      (v) => !!v || "Campo requerido",
      (v) => !/\D/.test(v) || "Campo Numérico",
    ],
    datosInscripcion: {},
    motivoPedido: null,
    idInscripcionSeleccionada: "FR",
    planillaCompleta: false,
    adjuntaAutorizacion: false,
    interesLegitimo: 2,
    numero: null,
    idDepartamento: null,
    numeroBis: null,
    folio: null,
    folioBis: null,
    tomo: null,
    anio: null,
    dark: false,
    matricula: null,
    matriculaBis: null,
    ph: null,
    botonSnackbar: "",
    lote: null,
    manzana: null,
    vuelto: false,
    checkbox: false,
    inscrip: {},
    loading: false,
    aplicarDisabled: false,
    alertInscriptionForm: false,
  }),
  computed: {
    ...mapState("Tramites-seleccionados", [
      "departments",
      "legitimateInterest",
      "typeInscription",
      "tasas_seleccionadas",
      "warehouse",
      "userFormValidado",
      "inscriptionFormValidado",
      "inscripcion_seleccionada",
      "errorConexion",
      "validacionPub",
      "datosPublicidad",
      "validarFormularioPublicidad",
    ]),
    ...mapState("Tramites-completados", ["completados"]),

    ...mapGetters("Tramites-seleccionados", [
      "inscripcionPorId",
      "getPublicidad",
    ]),

    estadoValidacion() {
      return this.validacionPub;
    },
    checkFormularioPublicidad() {
      return this.validarFormularioPublicidad;
    },
  },
  created() {
    if (this.legitimateInterest.length == 0) {
      this.fetchLegitimateInterest().then(() => {
        this.loadingInteresLegitimo = false;
      });
    } else {
      this.loadingInteresLegitimo = false;
    }

    if (this.departments.length == 0) {
      this.fetchDepartments().then(() => {
        this.loadingDepartamento = false;
      });
    } else {
      this.loadingDepartamento = false;
    }

    if (this.typeInscription.length == 0) {
      this.fetchTypeInscription().then(() => {
        this.loadingInscripcion = false;
      });

      if (this.idInscripcionSeleccionada == "FR") {
        this.requiredMatricula = [(v) => !!v || "Campo requerido"];
      }
      this.update();
    } else {
      this.loadingInscripcion = false;
    }
    if (this.datosPublicidad != {}) {
      this.actualizarPagina();
    }
    if (this.tasas_seleccionadas.length != 0) {
      this.borrarTasasSeleccionadas();
    }
  },

  mounted() {
    this.actualizarPagina();
    if (this.typeInscription.length != 0) {
      this.requerirCampos(
        this.typeInscription.find(
          (element) => element.id == this.idInscripcionSeleccionada
        )
      );
      this.seleccionarInscripcion(
        this.typeInscription.find(
          (element) => element.id == this.idInscripcionSeleccionada
        )
      );
    }
    if (this.interesLegitimo == undefined) {
      this.interesLegitimo = 2;
    }
    this.actualizarPagina();
  },
  methods: {
    ...mapActions("Tramites-seleccionados", [
      "fetchLegitimateInterest",
      "fetchDepartments",
      "fetchTypeInscription",
      "validarTramite",
      "actualizarCampoPublicidad",
      "borrarTasasSeleccionadas",
      "almacenarTasaSeleccionada",
      "eliminarTramiteWh",
      "almacenarPublicidadSeleccionada",
      "seleccionarInscripcion",
      "validarInscriptionForm",
      "validarPub",
      "resetDatosPublicidad",
      "anularValidacionPub",
      "cancelFormularioPulicidad",
      "validateGenerarPago",
      "invalidarInscriptionForm",
    ]),
    ...mapActions("Tramites-completados", ["agregarTramiteCompledoTasa"]),
    tipoSeleccion(idSeleccion) {
      if (idSeleccion == "FP") {
        return 6;
      }
      if (idSeleccion == "CR") {
        return 5;
      }
      return 8;
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    validarCampoNumerico(param) {
      const regexpNumeros = /\D/;
      if (param.target.value.match(regexpNumeros)) {
        param.target.value = null;
      }
    },
    calcularTomo() {
      if (this.folio != 0 && this.inscripcion_seleccionada.tomo == true) {
        if (this.folio % 250 == 0) {
          this.tomo = this.folio / 250;
        } else {
          this.tomo = Math.floor(this.folio / 250 + 1);
        }
      } else {
        this.tomo = null;
      }
    },
    validarNumeros(param, param2) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        let campo = eval("this." + param2 + "= null");
      }
    },
    actualizarPagina() {
      this.motivoPedido = this.datosPublicidad.motivoPedido;
      this.interesLegitimo = this.datosPublicidad.interesLegitimo;
      this.idInscripcionSeleccionada = this.datosPublicidad.idInscripcionSeleccionada;
      this.numero = this.datosPublicidad.numero;
      this.adjuntaAutorizacion = this.datosPublicidad.adjuntaAutorizacion;
      this.vuelto = this.datosPublicidad.vuelto;
      this.idDepartamento = this.datosPublicidad.idDepartamento;
      this.numero = this.datosPublicidad.numero;
      this.numeroBis = this.datosPublicidad.numeroBis;
      this.folio = this.datosPublicidad.folio;
      this.folioBis = this.datosPublicidad.folioBis;
      this.tomo = this.datosPublicidad.tomo;
      this.anio = this.datosPublicidad.anio;
      this.matricula = this.datosPublicidad.matricula;
      this.matriculaBis = this.datosPublicidad.matriculaBis;
      this.ph = this.datosPublicidad.ph;
      this.lote = this.datosPublicidad.lote;
      this.manzana = this.datosPublicidad.manzana;
      this.planillaCompleta = this.datosPublicidad.planillaCompleta;
    },
    update() {
      let indices = {};
      indices.motivoPedido = this.motivoPedido;
      indices.interesLegitimo = this.interesLegitimo;
      indices.idInscripcionSeleccionada = this.idInscripcionSeleccionada;
      indices.numero = this.numero;
      indices.adjuntaAutorizacion = this.adjuntaAutorizacion;
      indices.vuelto = this.vuelto;
      indices.idDepartamento = this.idDepartamento;
      indices.numero = this.numero;
      indices.numeroBis = this.numeroBis;
      indices.folio = this.folio;
      indices.folioBis = this.folioBis;
      indices.tomo = this.tomo;
      indices.anio = this.anio;
      indices.matricula = this.matricula;
      indices.matriculaBis = this.matriculaBis;
      indices.ph = this.ph;
      indices.lote = this.lote;
      indices.manzana = this.manzana;
      indices.planillaCompleta = this.planillaCompleta;
      this.actualizarCampoPublicidad(indices);
    },
    validate() {
      this.$refs.form[0].validate();
    },
    quitarTramite(id_tramite) {
      this.resetDatosPublicidad();
      this.eliminarTramiteWh(id_tramite);
      this.invalidarInscriptionForm();
    },
    comprobarConexion(value) {
      if (this.errorConexion[value] == true) {
        this.snackbar = true;
        this.centrarSnack = true;
        this.darkSnackbar = false;
        this.colorSnackbar = "red";
        this.botonSnackbar = "botonSnackbarRed";
        this.mensaje =
          "Hubo un error en la conexión. Por favor intentalo nuevamente.";
      } else {
        this.snackbar = false;
        this.centrarSnack = false;
        this.darkSnackbar = false;
        this.colorSnackbar = "";
        this.botonSnackbar = "";
        this.mensaje = "";
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form[0].resetValidation();
    },
    async aplicarFormulario() {
      this.alertInscriptionForm = false;
      this.$refs.form[0].validate();
      if (this.$refs.form[0].validate() == true) {
        this.datosInscripcion.idInscripcionSeleccionada = this.idInscripcionSeleccionada;
        this.vuelto == true
          ? (this.datosInscripcion.vuelto = "S")
          : (this.datosInscripcion.vuelto = "N");
        this.departamento == undefined || this.departamento == null
          ? (this.datosInscripcion.dpto = 37)
          : (this.datosInscripcion.dpto = this.departamento);
        this.numero != null
          ? (this.datosInscripcion.nro = parseInt(this.numero))
          : (this.datosInscripcion.nro = null);
        this.numeroBis != null
          ? (this.datosInscripcion.bis = parseInt(this.numeroBis))
          : (this.datosInscripcion.bis = null);
        this.folio != null
          ? (this.datosInscripcion.folio = parseInt(this.folio))
          : (this.datosInscripcion.folio = null);
        this.folioBis != null
          ? (this.datosInscripcion.nroRep = parseInt(this.folioBis))
          : (this.datosInscripcion.nroRep = null);
        this.tomo != null
          ? (this.datosInscripcion.tomo = parseInt(this.tomo))
          : (this.datosInscripcion.tomo = null);
        this.anio != null
          ? (this.datosInscripcion.anio = parseInt(this.anio))
          : (this.datosInscripcion.anio = null);
        this.matricula != null
          ? (this.datosInscripcion.nroMatricula = parseInt(this.matricula))
          : (this.datosInscripcion.nroMatricula = null);
        this.matriculaBis != null
          ? (this.datosInscripcion.bis2 = parseInt(this.matriculaBis))
          : (this.datosInscripcion.bis2 = null);
        this.datosInscripcion.ph = this.ph;

        this.datosInscripcion.tipoInscripcion = this.datosInscripcion.idInscripcionSeleccionada;
        delete this.datosInscripcion.idInscripcionSeleccionada;

        if (this.datosInscripcion.tipoInscripcion === "LE") {
          this.datosInscripcion.tomo = this.datosInscripcion.nro;
          this.datosInscripcion.nro = null;
        }
        if (this.datosInscripcion.tipoInscripcion == "PL") {
          this.datosInscripcion.nroMatricula = this.datosInscripcion.nro;
          this.datosInscripcion.nro = null;
        }
        this.loading = true;
        this.aplicarDisabled = true;
        this.validarPub(this.datosInscripcion);
      }
    },
    validar(tramite) {
      const listadoTramitesSeleccionados = [];
      let tramiteGuardado = [];
      tramiteGuardado["id_item"] = this.warehouse[0].id_item;
      tramiteGuardado["id"] = this.warehouse[0].id;
      tramiteGuardado["formula"] = this.warehouse[0].formulario.formula;
      tramiteGuardado["monto_fijo"] = this.warehouse[0].formulario.monto_fijo;
      tramiteGuardado[
        "monto_minimo"
      ] = this.warehouse[0].formulario.monto_minimo;
      tramiteGuardado[
        "monto_maximo"
      ] = this.warehouse[0].formulario.monto_maximo;
      tramiteGuardado["serie"] = this.warehouse[0].serie;
      tramiteGuardado["anio"] = this.warehouse[0].anio;
      listadoTramitesSeleccionados.push(tramiteGuardado);
      this.almacenarTasaSeleccionada(listadoTramitesSeleccionados);
      this.agregarTramiteCompledoTasa(JSON.parse(JSON.stringify(tramite)));
    },
    cambioEstadoValidacion() {
      if (this.validacionPub.codigo == 1) {
        this.validacionDatos = false;
        let tramite = this.warehouse[0];

        this.validar(tramite);
        this.validarTramite(0);
        this.validarInscriptionForm();
      }
      if (
        this.validacionPub.codigo != 1 &&
        Object.keys(this.validacionPub).length != 0
      ) {
        this.alertInscriptionForm = true;
      }
      if (this.validacionPub != {}) {
        this.loading = false;
        this.aplicarDisabled = false;
      }
    },
    verificarPublicidad() {
      if (this.userFormValidado == true) {
        if (this.inscriptionFormValidado != true) {
          this.snackbar = true;
          this.colorSnackbar = "";
          this.darkSnackbar = true;
          this.dark = true;
          this.centrarSnack = false;
          this.botonSnackbar = "botonSnackbarWhite";
          this.mensaje = "Debe aplicar el formulario seleccionado";
        } else {
          this.snackbar = false;
          this.centrarSnack = "";
          this.darkSnackbar = false;
          this.colorSnackbar = "";
          this.botonSnackbar = "";
          this.mensaje = "";
          let publicidad = [];

          this.datosInscripcion.motivoPedido = this.motivoPedido;
          this.datosInscripcion.interesLegitimo = this.interesLegitimo;
          this.adjuntaAutorizacion == true
            ? (this.datosInscripcion.adjuntaAutorizacion = "S")
            : (this.datosInscripcion.adjuntaAutorizacion = "N");
          this.lote != null
            ? (this.datosInscripcion.lote = parseInt(this.lote))
            : (this.datosInscripcion.lote = null);
          this.manzana != null
            ? (this.datosInscripcion.manzana = parseInt(this.manzana))
            : (this.datosInscripcion.manzana = null);
          this.planillaCompleta == true
            ? (this.datosInscripcion.planillaCompleta = "S")
            : (this.datosInscripcion.planillaCompleta = "N");

          let serieParse = JSON.parse(
            JSON.stringify(parseInt(this.completados[0].serie))
          );
          let anioParse = JSON.parse(
            JSON.stringify(parseInt(this.completados[0].anio))
          );
          this.datosInscripcion.id_item = this.completados[0].id_item.padEnd(8);
          this.datosInscripcion.serie_item = serieParse;
          this.datosInscripcion.anio_item = anioParse;
          publicidad.push(this.datosInscripcion);
          this.almacenarPublicidadSeleccionada(publicidad);
          this.validateGenerarPago();
        }
      } else {
        this.snackbar = false;
      }
    },
    cambioInscripcion(tipoInscripcion) {
      this.idInscripcionSeleccionada = tipoInscripcion.id;
      this.anularValidacionPub();
      this.requerirCampos(tipoInscripcion);
      this.seleccionarInscripcion(tipoInscripcion);
      this.resetCampos();
      this.resetDatosPublicidad();
      this.update();
      this.resetValidation();
    },
    requerirCampos(tipoInscripcion) {
      tipoInscripcion.numero_requerido
        ? (this.requiredNumero = [(v) => !!v || "Campo requerido"])
        : (this.requiredNumero = []);
      tipoInscripcion.matricula_requerido
        ? (this.requiredMatricula = [(v) => !!v || "Campo requerido"])
        : (this.requiredMatricula = []);
      tipoInscripcion.anio_requerido
        ? (this.requiredAnio = [(v) => !!v || "Campo requerido"])
        : (this.requiredAnio = []);
      tipoInscripcion.folio_requerido
        ? (this.requiredFolio = [(v) => !!v || "Campo requerido"])
        : (this.requiredFolio = []);
    },
    cambioDepartamento(departamento) {
      this.departamento = departamento.idDepartamento;
    },
    cambioInteresLegitimo(interesLegitimo) {
      this.anularValidacionPub();
      this.interesLegitimo = interesLegitimo.id;
      this.update();
    },
    resetCampos() {
      this.numero = null;
      this.numeroBis = null;
      this.folio = null;
      this.folioBis = null;
      this.tomo = null;
      this.anio = null;
      this.matricula = null;
      this.matriculaBis = null;
      this.ph = null;
      this.lote = null;
      this.manzana = null;
      this.vuelto = null;
    },
  },
  watch: {
    estadoValidacion() {
      this.cambioEstadoValidacion();
    },
    checkFormularioPublicidad() {
      if (this.validarFormularioPublicidad == true) {
        this.verificarPublicidad();
        this.cancelFormularioPulicidad();
      }
    },
  },
});
</script>

