<template>
  <v-row>
    <v-col cols="12" xs="12" sm="12" class="pb-0">
      <v-card outlined elevation="2" height="100%">
        <v-form ref="formu">
          <v-row>
            <v-col cols="12">
              <v-alert
                v-model = alert
                text
                outlined
                color="red"
                class="py-2 ml-7 mr-7 mb-2 mt-4 text-center body-2"
                close-icon="close"
                dismissible
                v-if="alert"
              >
                {{mensajeAlert}}
              </v-alert>
              <v-row class="d-flex justify-space-between align-center mx-4">
                <v-col cols="12" sm="4" class="pb-0 pt-4 px-3 completo">
                  <v-text-field
                    v-model="formulario"
                    @keyup="validarNumeros(formulario)"
                    @keypress="onlyNumber($event)"
                    maxlength="12"
                    label="Número formulario"
                    outlined
                    required
                    :rules="requiredRule"
                    dense
                    :disabled="busquedaCompletada"
                    class="centered-input shrink unico"
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="4"
                  class="d-flex justify-space-between px-3 justify-sm-end py-2 px-0"
                >
                  <v-btn
                    outlined
                    color="primary"
                    class="no-text-transforms mr-4 boton"
                    :disabled="complementaria == null"
                    @click="limpiar()"
                  >
                    limpiar
                    <v-icon color="primary" size="14"> </v-icon>
                  </v-btn>
                  <v-btn
                    outlined
                    color="primary"
                    class="no-text-transforms boton"
                    @click="buscar()"
                    :disabled="busquedaCompletada"
                    :loading="buscando == true"
                  >
                    buscar
                    <v-icon color="primary" size="14"> </v-icon>
                  </v-btn>
                </v-col>
              </v-row>
              <v-snackbar
                v-model="snackbar"
                centered
                color="primary"
                timeout="5000 "
                outlined
                text
              >
                <v-row class="snackbar">
                  <v-col>
                    ¿Confirma que desea eliminar la búsqueda? <br/> Si no se generó la tasa, se perderán las modificaciones que haya realizado.
                  </v-col>
                </v-row>
                <v-row class="snackbar">
                  <v-col>
                    <v-btn
                      class="botonSnackbar"
                      text
                      color="primary"
                      @click="anularSnackbar()"
                    >
                      NO
                    </v-btn>
                    <v-btn
                      class="botonSnackbar"
                      text
                      color="primary"
                      @click="confirmarLimpiar()"
                    >
                      SI
                    </v-btn>
                  </v-col>
                </v-row>
              </v-snackbar>
              
            </v-col>
          </v-row>
        </v-form>
      </v-card>
    </v-col>
    <!--<ModalPublicidadDirecta /> -->
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapState, mapGetters, mapActions } from "vuex";
//import ModalPublicidadDirecta from "../../utils/ModalPublicidadDirecta";

export default Vue.extend({
  name: "busquedaComplementaria",
  components: {
    //ModalPublicidadDirecta,
  },
  data: () => ({
    formulario: null,
    buscando: false,
    snackbar: false,
    alert: false,
    busquedaCompletada: false,
    requiredRule: [(v) => !!v || "Campo requerido"],
  }),
  computed: {
    ...mapState("Tramites-completados", ["complementaria", "errorConexion"]),
    ...mapGetters("Tramites-completados", ["validarComplementaria"]),
    ...mapState("Modal", ["modalPublicidadDirecta"]),
    resultadoBusqueda() {
      return this.complementaria;
    },
     mostrarError(){
      return this.errorConexion;
    }
  },
  methods: {
    ...mapActions("Tramites-completados", [
      "buscarComplementaria", 'consultarComplementaria',
      "resetComplementaria",
      "agregarTramiteComplementaria",
      "limpiarTramitesCompletados", 
    ]),
    ...mapActions("Modal", ["mostrarModalPublicidadDirecta"]),
    ...mapActions("Tramites-seleccionados", [
      "validarTramite",
      "resetearWarehouse",
    ]),
    validarNumeros(param) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        this.formulario = null;
      }
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    buscar() {
      this.buscando = true;
      this.$refs.formu.validate();
      if(this.$refs.formu.validate() == true){
        this.consultarComplementaria(this.formulario);  
      }else{
        this.buscando = false;
      }
    },
    anularSnackbar() {
      this.snackbar = false;
    },
    limpiar() {
      this.snackbar = true;
    },
    confirmarLimpiar() {
      this.snackbar = false;
      this.formulario = null;
      this.resetearWarehouse();
      this.resetComplementaria();
      this.limpiarTramitesCompletados();
      this.$refs.formu.resetValidation();
      this.alert= false;
      this.busquedaCompletada = false;
      this.mensajeAlert = "";
    },
  },
  watch: {
    resultadoBusqueda() {
      if(this.complementaria != null){
        if(this.complementaria.length == 0){
          this.alert = true;
          this.mensajeAlert = "No hay datos cargados para el número ingresado."
        }else{
          if(this.complementaria[0].nrounico != ""){
            this.alert = true;
            this.mensajeAlert = "El formulario ingresado ya posee un número único asociado."
          } else if(this.complementaria[0].tasa_total_exenta != 0) {
            this.alert = true;
            this.mensajeAlert = "Disculpe, no puede generar una tasa complementaria si es exento."
          } else if (this.complementaria[0].tiene_tasa_complementaria == "S"){
            this.alert = true;
            this.mensajeAlert = "El formulario ingresado ya tiene asociada una tasa complementaria."
          }else if(this.complementaria[0].es_tasa_complementaria == "S"){
            this.alert = true;
            this.mensajeAlert = "El formulario ingresado ya es complementaria de otro formulario."
          }else{
            this.alert = false;
            this.mensajeAlert = "";
            this.busquedaCompletada = true;
          }
        }
          this.buscando = false;        
        }
    },
    mostrarError(){
      if(this.errorConexion == true){
        this.buscando = false;
      }
    }
  },
  beforeDestroy() {
    this.resetearWarehouse();
    this.resetComplementaria();
    this.limpiarTramitesCompletados();
  },
});
</script>
<style scoped>
.snackbar {
  text-align: center;
  display: flex;
  align-items: center;
}
  .botonSnackbar {
    color: red !important;
  }
</style>
