<template>
    <div >
        <v-row>
            <!-- FORMULARIO SELECCION -->
            <v-col cols="12" md="6"  id="formularioEdicion" class="pb-0 pt-0">
                <v-row class="mx-titulo" >
                  <v-col cols="auto" class="titulo-margen ml-4 px-0">
                    <v-card outlined>
                      <div class="titulo py-2 px-3">
                        <div>Selección</div>
                      </div>
                    </v-card>
                  </v-col>
                </v-row>  
                <v-card elevation="2" class="text-xs-center pt-4 vCardResultados">
                    <v-form
                        ref="formularioSeleccion"
                        class="px-3"
                    >
                        <v-row class="pt-1 px-3">
                            <v-col cols="12" xs="12" sm="6" md="6" class="px-2 py-0">
                                <v-row class="mx-1">
                                <v-col
                                    cols="4"
                                    md="4"
                                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                                >
                                    <label class="v-label v-label--active theme--light">
                                    Tipo
                                    </label>
                                </v-col>
                                <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                                    <v-select
                                        v-model="tipoSeleccionado"
                                        :items="datosAModificar"
                                        outlined
                                        dense
                                        :rules="requiredRule"
                                        required
                                        @change="changeSelect()"
                                    >
                                    </v-select>
                                </v-col>
                                </v-row>
                            </v-col>
                            <v-col cols="12" xs="12" sm="6" md="6" class="px-2 py-0">
                                <v-row class="mx-1">
                                <v-col
                                    cols="4"
                                    md="4"
                                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                                >
                                    <label class="v-label v-label--active theme--light">
                                    Solicitud
                                    </label>
                                </v-col>
                                <v-col cols="12" md="12" class="px-0 pt-1 pb-0">
                                    <v-select
                                    v-model="solicitud"
                                    :items="tipoSolicitud"
                                    outlined
                                    dense
                                    :rules="requiredRule"
                                    required
                                    @change="changeSelect()"
                                    >
                                    </v-select>
                                </v-col>
                                </v-row>
                            </v-col>    
                        </v-row>
                        <v-row class="pt-1 px-6 d-flex justify-space-between">
                            <v-btn
                                @click="showCrearItemGenerico()"
                                color="primary"
                                outlined 
                                class="mb-5"            
                            >
                                Crear
                            </v-btn>
                            <v-btn
                                @click="showEditarItemGenerico()"
                                color="primary"
                                outlined 
                                class="mb-5"            
                            >
                                Editar
                            </v-btn>
                        </v-row>    
                    </v-form>
                </v-card>
            </v-col>
            <!-- FORMULARIO CREACION -->
            <v-col cols="12" md="6" v-if="showCreacionItemGenerico" class="pb-0 pt-0">
                <v-row class="mx-titulo" >
                    <v-col cols="auto" class="titulo-margen ml-4 px-0">
                        <v-card outlined>
                            <div class="titulo py-2 px-3">
                                {{title}}
                            </div>
                        </v-card>
                    </v-col>
                </v-row>  
                <v-card elevation="2" class="text-xs-center pt-3 mb-3 vCardResultados" >
                    <v-form
                        ref="formularioCreacion"
                        class="px-3"
                    >
                        <v-row class="pt-1 px-3" >
                            <v-col cols="12" class="px-3 pt-1 pb-0" >
                                <v-col
                                    cols="12"
                                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                                >
                                    <label class="v-label v-label--active theme--light">
                                    Descripción
                                    </label>
                                </v-col>
                                <v-text-field
                                    v-model="descripcionItemCreacion"
                                    item-text="descripcion"
                                    dense
                                    outlined
                                    autocomplete = "off"
                                    class="sm-6 pt-1"
                                    color="primary"
                                    clearable
                                    required
                                    :rules="requiredRule"
                                >
                                </v-text-field>
                            </v-col>
                                    
                        </v-row> 
                        <v-row class="pt-1 px-6 d-flex justify-space-between" > 
                            <v-btn
                            @click="cancelarCrearItemGenerico()"
                            color="primary"
                            outlined 
                            class="mb-5"            
                            >
                             Cancelar
                            </v-btn>
                            <v-btn
                            @click="crearItemGenerico()"
                            color="primary"
                            outlined 
                            class="mb-5"            
                            >
                            Confirmar
                            </v-btn>
                        </v-row>  
                    </v-form>
                </v-card>
            </v-col>
           <!-- FORMULARIO EDICION -->
            <v-col cols="12" md="6" v-if="showEdicionItemGenerico" class="pb-0 pt-0">
                <v-row class="mx-titulo" >
                  <v-col cols="auto" class="titulo-margen ml-4 px-0">
                    <v-card outlined>
                      <div class="titulo py-2 px-3">
                        <div>{{title}}</div>
                      </div>
                    </v-card>
                  </v-col>
                </v-row>  
                <v-card elevation="2" class="text-xs-center pt-10 mb-3 vCardResultados">
                  <v-form
                    ref="formularioEdicion"
                    class="px-3"
                  >
                    <v-row class="pt-1 px-3" >              
                      <!-- SELECT OPCIONES -->
                      <v-col cols="12" class="px-3 pt-1 pb-0">
                        <v-row class="ml-0">
                         
                        </v-row>  
                        <v-row class="pt-1 px-3 d-flex">
                          <v-col cols="10" class="px-0 pt-1 pb-0">
                            <v-autocomplete
                                v-model="idItemSeleccionado"
                                item-text="descripcion"
                                :items="itemsFiltrados"
                                item-value="id"
                                dense
                                outlined
                                label="Seleccione descripción"
                                @change="seleccionItem()"
                                color="primary"
                                clearable      
                                :search-input.sync="descripcionSeleccionada"
                            >
                            </v-autocomplete>
                          </v-col>    
                          <v-col cols="2" class="px-4 pt-2 pb-0 rowBotones" >
                              <v-row class="position" >
                                <v-btn
                                  @click="editarItemGenerico()"
                                  color="primary"
                                  x-small
                                  fab              
                                >
                                  <v-icon>mdi-pencil</v-icon>
                                </v-btn>
                                <v-btn
                                  @click="eliminarItemGenerico()"
                                  color="red"
                                  dark
                                  x-small
                                  fab
                                  class="mx-2"            
                                >
                                  <v-icon>delete</v-icon>
                                </v-btn>
                              </v-row>
                          </v-col>
                        </v-row> 
                        <v-form
                            ref="formNuevaDescripcionItem"
                        >
                            <v-row class="pt-1 px-3 d-flex" v-if="showGenerarDescripcion">
                                <v-col cols="12" class="px-0 pt-1 pb-0">
                                    <v-text-field
                                    v-model="descripcionItemEdicion"
                                    dense
                                    outlined
                                    label="nueva descripcion"
                                    autocomplete = "off"
                                    class="sm-6 pt-1"
                                    color="primary"
                                    clearable
                                    required
                                    :rules="requiredRule"  
                                    >
                                    </v-text-field>
                                </v-col>
                            </v-row>         
                        </v-form>        
                      </v-col>       
                    </v-row> 
                    <v-row class="pt-1 px-6 d-flex justify-space-between"> 
                        <v-btn
                            @click="cancelarEditarItemGenerico()"
                            color="primary"
                            outlined 
                            class="mb-5"            
                            >
                            Cancelar
                        </v-btn>
                        <v-btn
                          @click="confirmarEditarItemGenerico()"
                          color="primary"
                          outlined  
                          class="mb-5"
                          :disabled="habilitarBotonEdicion"         
                        >
                          confirmar
                        </v-btn>
                    </v-row>  
                  </v-form>
                </v-card>
            </v-col>
        </v-row>
        <!-- SNACKBAR -->
        <v-snackbar
            v-model="snackbar" 
            centered
            :color="colorSnackbar"
            :timeout="timeSnackbar" 
            outlined
            text
            height=60 
            class="pl-2"
        >
            <div class="ml-2">
                <div class="centrarTexto mt-3 mb-2">
                    {{mensaje}}
                </div>
                <div v-if="tramitesEnSnackbar">
                    <v-list 
                        v-if="!scrollSnackbar"
                    >
                        <v-list-item
                            v-for="(titulo) in this.tramitesFiltradosPorItem.slice(0,3)"
                            :key="titulo"
                            class="listadoTramitesSnackbar"
                            max-height="50px"
                        >
                            - {{titulo}}
                        </v-list-item>
                        <div @click="scrollSnackbar = true" v-if="this.tramitesFiltradosPorItem.length>3" class="verMasSnackbar">
                            ...
                        </div>
                    </v-list>
                    <v-list 
                        style="max-height: 150px"
                        class="overflow-y-auto"
                        v-else
                    >
                        <v-list-item
                            v-for="(titulo) in this.tramitesFiltradosPorItem"
                            :key="titulo"
                            class="listadoTramitesSnackbar"
                            max-height="50px"
                        >
                            - {{titulo}}
                        </v-list-item>
                    </v-list>
                </div>
                <div class="snackbar justify-center"
                    v-if="botonAceptarSnackbar"
                >
                    <v-col>
                        <v-btn
                            class="botonSnackbar"
                            text
                            @click="snackbar = false, scrollSnackbar=false"
                            >
                            ACEPTAR
                        </v-btn>
                    </v-col>
                </div>
                <div class="snackbar mt-3 justify-center" v-if="botonesConfirmacionSnackbar">
                    <v-btn
                        class="botonSnackbar"
                        text
                        color="primary"
                        @click="confirmarEliminarItem()"
                    >
                        SI
                    </v-btn>
                    <v-btn
                        class="botonSnackbar"
                        text
                        color="primary"
                        @click="cancelarEliminarItem()"
                    >
                        NO
                    </v-btn>
                </div>
            </div>
        </v-snackbar>
    </div>    
</template>
<script>
import Vue from "vue";
import {mapActions, mapState} from "vuex";
import { EventBus } from '../../../eventBus.js';


export default Vue.extend ({
    name: "ItemsGenericos",

    data:() => ({
        datosAModificar: ["Paso a paso", "Requerimientos"],
        tipoSolicitud:["Web", "Presencial", "CVD"],
        tipoSeleccionado:"",
        typeSelected: "",
        idItemSeleccionado: null,
        showCreacionItemGenerico: false,
        showEdicionItemGenerico: false,
        descripcionItemCreacion:"",
        descripcionItemEdicion:"",
        listadoEditable: [],
        solicitud:"",
        solicitudSeleccionada:"",
        itemsFiltrados:[],
        title: "",
        timeSnackbar: -1,
        tramitesEnSnackbar: false,
        habilitarBotonEdicion:true,
        descripcionSeleccionada:null,
        showGenerarDescripcion: false,
        mensaje: "",
        colorSnackbar:"",
        scrollSnackbar: false,
        botonAceptarSnackbar: false,
        botonesConfirmacionSnackbar: false,
        snackbar:false,
        tramitesFiltradosPorItem: [],
        snackbarConfirmacion:false,
        requiredRule: [(v) => !!v || "Campo requerido"],
    }),
  
    computed: {
    ...mapState("Listado-tramites", ["listadoRequerimientosGenericos", "listadoPasosGenericos", "tramites"]),
    },
    methods:{
        ...mapActions("Listado-tramites", [
                "extraerRequerimientosGenericos", "extraerPasosGenericos",  
                "agregarRequerimientoGenerico", "agregarPasoGenerico", 
                "eliminarRequerimientoGenerico", "eliminarPasoGenerico",
                "modificarPasoGenerico", "modificarRequerimientoGenerico" 
        ]),
        showCrearItemGenerico(){
            this.$refs.formularioSeleccion.validate()
            if(this.$refs.formularioSeleccion.validate() == true){
                this.showCreacionItemGenerico = true;
                this.showEdicionItemGenerico = false;
                this.descripcionItemCreacion = "";
                this.armarTituloTarjeta("Crear");
                this.filtrarItems();
            }  
        },
        showEditarItemGenerico(){
            this.$refs.formularioSeleccion.validate()
            if(this.$refs.formularioSeleccion.validate() == true){
                this.showCreacionItemGenerico = false
                this.showEdicionItemGenerico = true
                this.idItemSeleccionado = null
                this.showGenerarDescripcion = false
                this.habilitarBotonEdicion = true
                this.descripcionItemEdicion = null
                this.armarTituloTarjeta("Editar")
                this.filtrarItems()
            }
        },
        changeSelect(){
            this.cancelarEditarItemGenerico()
            this.cancelarCrearItemGenerico()
             if(this.tipoSeleccionado == "Paso a paso"){
                this.typeSelected = "paso_a_paso"
            }else{
                this.typeSelected = "requerimientos"
            }
            
            if(this.solicitud == "Web"){
                this.solicitudSeleccionada = "web"
            }else if(this.solicitud == "Presencial"){
                this.solicitudSeleccionada = "presencial"
            }else{
                this.solicitudSeleccionada = "cvd"
            }
        },
        filtrarItems(){
            this.itemsFiltrados = []
            if(this.typeSelected == "requerimientos"){
                this.itemsFiltrados = this.listadoRequerimientosGenericos.filter(element => element.tipo.toLowerCase() == this.solicitudSeleccionada.toLowerCase())
            }
            if(this.typeSelected == "paso_a_paso"){
                this.itemsFiltrados = this.listadoPasosGenericos.filter(element => element.tipo.toLowerCase() == this.solicitudSeleccionada.toLowerCase())
            }
        },
        cancelarCrearItemGenerico(){
            this.showCreacionItemGenerico = false
            this.descripcionItemCreacion = ""
        },
        armarTituloTarjeta(param){
            let tipo = ""
            let solicitud = ""
            
            if(this.typeSelected == "paso_a_paso"){
                tipo = "paso"
            }else{
                tipo = "requerimiento"
            }

            if(this.solicitudSeleccionada == "web"){
                solicitud = "web"
            }else if(this.solicitudSeleccionada == "presencial"){
                solicitud = "presencial"
            }else{
                solicitud = "cvd"
            }
            this.title = param + " " +  tipo + " " + solicitud
        },
        cancelarEditarItemGenerico(){
            this.idItemSeleccionado = null
            this.showGenerarDescripcion = false
            this.showEdicionItemGenerico = false
            this.habilitarBotonEdicion = true
        }, 
        mostrarSnackbar(mensaje, time, color, botonAceptar, tramitesEnSnackbar, botonesConfirmacion){
            this.scrollSnackbar = false;
            this.snackbar = true;
            this.mensaje = mensaje;
            this.timeSnackbar = time;
            this.colorSnackbar = color;
            this.botonAceptarSnackbar = botonAceptar;
            this.tramitesEnSnackbar = tramitesEnSnackbar;
            this.botonesConfirmacionSnackbar = botonesConfirmacion;
        },
        seleccionItem(){
            this.showGenerarDescripcion = false
            this.descripcionItemEdicion = ""
            this.habilitarBotonEdicion = true
        },
        validarNuevoItem(){
            if(this.tipoSeleccionado == "Paso a paso"){
                this.listadoEditable = this.listadoPasosGenericos
            }else{
                this.listadoEditable = this.listadoRequerimientosGenericos
            }
            if(this.listadoEditable.find(element => (element.descripcion.toLowerCase() == this.descripcionItemCreacion.toLowerCase()) &&
                (element.tipo.toLowerCase() == this.solicitud.toLowerCase())
            ) == undefined){
                return true
            }else{
                this.mostrarSnackbar("El item que desea crear ya existe", "5000", "red", true, false, false);
            }        
        },     
        //FUNCIONES CREACION ITEM GENERICO
        crearItemGenerico(){
            this.descripcionItemCreacion = this.descripcionItemCreacion.trim()
            this.$refs.formularioCreacion.validate()  
            if(this.$refs.formularioCreacion.validate()== true){
                if(this.descripcionItemCreacion == ""){
                    this.$refs.formularioCreacion.validate()
                }else{
                    this.validarNuevoItem()
                    if(this.validarNuevoItem() == true){
                        let nuevaOpcion = {}
                            nuevaOpcion.tipo = this.solicitudSeleccionada.toUpperCase()
                            nuevaOpcion.descripcion = this.descripcionItemCreacion
                        try{
                            if(this.tipoSeleccionado == "Paso a paso"){
                                this.agregarPasoGenerico(nuevaOpcion)
                            }else{
                                this.agregarRequerimientoGenerico(nuevaOpcion)
                            }
                        }catch(error){
                            this.mostrarSnackbar("Hubo un error en la conexión. Por favor intente nuevamente.", "5000", "red", true, false, false);
                            return
                        }
                        this.$refs.formularioCreacion.reset();
                        this.showCreacionItemGenerico = false;
                        this.mostrarSnackbar("La creación se realizó con éxito.", "3000", "success", false, false, false);
                        EventBus.$emit("actualizarOpciones")
                    }
                }   
            }
        },
        //FUNCIONES EDICION ITEM GENERICO
        editarItemGenerico(){
             if(this.descripcionSeleccionada == null){
                this.mostrarSnackbar("Debe seleccionar el item a modificar.", "5000", "red", true, false, false);
            }else{
                this.showGenerarDescripcion = true
                this.habilitarBotonEdicion = false
            }
        },
        confirmarEditarItemGenerico(){ 
            this.descripcionItemEdicion = this.descripcionItemEdicion.trim()
            this.$refs.formNuevaDescripcionItem.validate()
            if( this.$refs.formNuevaDescripcionItem.validate() == true){
                if(this.descripcionItemEdicion == ""){
                  
                    this.$refs.formNuevaDescripcionItem.validate()
                }else{
                    let parametro={}
                        parametro.tipo = this.solicitudSeleccionada
                        parametro.descripcion = this.descripcionItemEdicion
                        parametro.id = this.idItemSeleccionado
                    try{
                        if(this.tipoSeleccionado == "Paso a paso"){
                            this.modificarPasoGenerico(parametro)
                        }else{
                            this.modificarRequerimientoGenerico(parametro)
                        }
                    }catch(error){
                        this.mostrarSnackbar("Hubo un error en la conexión. Por favor intente nuevamente.", "5000", "red", true, false, false);
                        return
                    } 
                    this.idItemSeleccionado=null;
                    this.showEdicionItemGenerico = false;
                    this.mostrarSnackbar("La modificación se realizó con éxito.", "3000", "success" , false, false, false);
                    this.showGenerarDescripcion = false;
                    this.habilitarBotonEdicion = true;
                    EventBus.$emit("actualizarOpciones"); 
                }
            }
        },
        //FUNCIONES ELIMINACION ITEM GENERICO
        cancelarEliminarItem() {
            this.snackbar = false;
        },  
        confirmarEliminarItem() {
            this.snackbar = false;
            this.confirmarEliminarItemGenerico();
        },
        eliminarItemGenerico(){
            if(this.descripcionSeleccionada == null){
                this.mostrarSnackbar("Debe seleccionar el item a eliminar.", "5000", "red" , true, false, false);
            }else{
                this.buscarItemEnTramite()
                if(this.tramitesFiltradosPorItem.length == 0){
                    this.mostrarSnackbar("¿Confirma que desea eliminar el ítem seleccionado?", "5000", "primary" , false, false, true);
                } else if(this.tramitesFiltradosPorItem.length == 1){
                    this.mostrarSnackbar("El ítem seleccionado no puede eliminarse ya que se encuentra asociado al siguiente trámite:", "7000", "red" , true, true, false);
                }else{
                    let time = 5000 + (this.tramitesFiltradosPorItem.length * 1000)
                    this.mostrarSnackbar("El ítem seleccionado no puede eliminarse ya se encuentra asociado a los siguientes trámites:", time, "red" , true, true, false);
                }
            }
        },
        buscarItemEnTramite(){
            let tramites = [];
            this.tramitesFiltradosPorItem = [];
            if(this.tipoSeleccionado == "Paso a paso"){ 
                tramites = this.tramites.filter(element=>element.paso_a_paso.find(elemento=> elemento.id == this.idItemSeleccionado))
            }else{
               tramites = this.tramites.filter(element=>element.requerimientos.find(elemento=> elemento.id == this.idItemSeleccionado))
            }
            tramites.forEach(element => {
                this.tramitesFiltradosPorItem.push(element.titulo)
            })
        },
        confirmarEliminarItemGenerico(){
            try{
                if(this.tipoSeleccionado == "Paso a paso"){
                   this.eliminarPasoGenerico(this.idItemSeleccionado)
                }else{
                   this.eliminarRequerimientoGenerico(this.idItemSeleccionado)
                }
            }catch(error){
                this.mostrarSnackbar("Hubo un error en la conexión. Por favor intente nuevamente.", "5000", "red" , true, false, false);
                return
            } 
            this.mostrarSnackbar("La eliminación se realizó con éxito.", "3000", "success" , false, false, false);
            this.idItemSeleccionado = null
            this.showEdicionItemGenerico=false
            EventBus.$emit("actualizarOpciones")
        }, 
    }
})
</script>
<style scoped>
.snackbar{
  text-align: center;
  display: flex;
  width:100% !important;
  align-items: center;
  margin-right:0;
}
.botonSnackbar{
  color: red !important;
}
.centrarTexto{
    text-align: center;
}
.rowBotones{
    display: flex;
    justify-content: center;
    align-items: center;
    height:40px;
    margin: 4px 0 0 ;
    padding:0 0 0 12px !important;
}
.listadoTramitesSnackbar{
    text-align: justify;
}
.theme--light.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled){
    color: red !important;
}
.contenedorSnackbar{
    margin-left:8px
}
.vCardResultados{
    min-height:180px;
}
.titulo-margen{
    padding-top:10px !important;
}
.verMasSnackbar{
    text-align: right;
    font-size:20px;
    color:red;
}
.verMasSnackbar:hover{
    cursor: pointer;
}
  
</style>