<template>
  <transition name="slide-fade" mode="out-in">
    <div class="panelsheader">
      <v-expansion-panels v-model="panel">
        <v-expansion-panel>
          <v-expansion-panel-header
            >Datos del documento</v-expansion-panel-header
          >
          <v-expansion-panel-content>
            <v-card class="mt-3">
              <v-list two-line class="py-0">
                <v-list-item-group>
                  <template v-for="(item, index) in this.items">
                    <v-list-item :key="item.title" class="px-0">
                      <template v-slot:default="{}">
                        <v-list-item-content>
                          <v-list-item-subtitle
                            v-text="item.nombre"
                            class="subtitle-2"
                            dense
                          ></v-list-item-subtitle>
                        </v-list-item-content>
                        <v-divider vertical></v-divider>
                        <v-list-item-content>
                          <v-list-item-subtitle
                            v-text="item.valor"
                            class="subtitle-2"
                            dense
                          ></v-list-item-subtitle>
                        </v-list-item-content>
                      </template>
                    </v-list-item>

                    <v-divider
                      v-if="index < items.length - 1"
                      :key="index"
                    ></v-divider>
                  </template>
                </v-list-item-group>
              </v-list>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
  </transition>
</template>

<script>
import Vue from "vue";
import moment from "moment";
import { mapGetters } from "vuex";

export default Vue.extend({
  name: "DatosDocumento",
  data: () => ({
    panel: 0,
  }),
  computed: {
    ...mapGetters("Consulta-documentos", ["documento"]),
    items() {
      if (this.documento.terminoSusp === "SI") {
        return [
          {
            nombre: "Nro. único",
            valor: this.documento.nroUnico,
          },
          {
            nombre: "Estado",
            valor: this.capitalize(this.documento.descripcionEstado),
          },
          {
            nombre: "Fecha y hora alta",
            valor: this.fechaHora(this.documento.fechaIngreso),
          },
          {
            nombre: "Fecha vto.",
            valor: "Término Suspendido",
          },
          {
            nombre: "Ubicación",
            valor: this.capitalize(
              this.documento.ubicacion.replace(/ *\([^)]*\) */g, "")
            ),
          },
          {
            nombre: "Tipo expedición",
            valor: this.capitalize(this.documento.tipoExpedicion),
          },
        ];
      } else {
        return [
          {
            nombre: "Nro. único",
            valor: this.documento.nroUnico,
          },
          {
            nombre: "Estado",
            valor: this.capitalize(this.documento.descripcionEstado),
          },
          {
            nombre: "Fecha y hora alta",
            valor: this.fechaHora(this.documento.fechaIngreso),
          },
          {
            nombre: "Fecha vto.",
            valor: this.fecha(this.documento.vencimiento),
          },
          {
            nombre: "Ubicación",
            valor: this.capitalize(
              this.documento.ubicacion.replace(/ *\([^)]*\) */g, "")
            ),
          },
          {
            nombre: "Tipo expedición",
            valor: this.capitalize(this.documento.tipoExpedicion),
          },
        ];
      }
    },
  },
  methods: {
    capitalize: function(value) {
      if (!value) return "";
      value = value.toString().toLowerCase();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    fechaHora: function(value) {
      if (!value) return "";
      return moment(String(value)).format("DD/MM/YYYY hh:mm");
    },
    fecha: function(value) {
      if (!value) return "";
      return moment(String(value)).format("DD/MM/YYYY");
    },
  },
});
</script>

<style scoped>
.v-list-item__content >>> {
  padding: 0px !important;
}

.v-list--two-line .v-list-item,
.v-list-item--two-line >>> {
  min-height: 48px;
}

.v-list-item__title,
.v-list-item__subtitle >>> {
  text-align: center;
}

.v-list-item--link >>> {
  user-select: text;
  cursor: text;
}

.slide-fade-enter-active >>> {
  transition: all 0.3s ease;
}
.slide-fade-leave-active >>> {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to >>>
/* .slide-fade-leave-active for <2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>
