<script>
import Vue from "vue";
import ModalItemSelection from "../utils/ModalItemSelection";
import ModalTramiteExistente from "../utils/ModalTramiteExistente";
import ModalLimiteTramites from "../utils/ModalLimiteTramites";
import ModalBloqueoCompletados from "../utils/ModalBloqueoCompletados";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";

export default Vue.extend({
  name: "ProcedureSelection",
  components: {
    ModalItemSelection,
    ModalTramiteExistente,
    ModalLimiteTramites,
    ModalBloqueoCompletados,
  },
  props: {
    clasificacion: String,
  },
  data: () => ({
    page: 1,
    cantidadAMostrar: 10,
    search: "",
    opciones: [5, 10, 20, 50],
    filterProc: "",
    filterOptions: [
      { text: "Emisión de Formulario de Tasas", value: "Tasa" },
      {
        text: "Emisión de Formulario de Tasas para Publicidad directa",
        value: "Publicidad Directa",
      },
    ],
    snackbar: false,
    selectCantidad: [],
    switchMark: false,
    loading: false,
    filteredData: [],
    agregado: false,
    renderizado: false,
    cantidadMaxima: 5,
  }),
  computed: {
    ...mapState("Listado-tramites", ["tramites", "errorConexion"]),
    ...mapState("Tramites-seleccionados", ["warehouse", "tasas_seleccionadas"]),
    ...mapState("Tramites-completados", ["completados", "reimprimirEfectivo"]),
    ...mapGetters("Listado-tramites", ["existenTramites"]),
    ...mapGetters("Tramites-seleccionados", [
      "existeTramiteYTasaSeleccionado",
      "existeTasa",
    ]),
    ...mapGetters("Tramites-completados", ["existeTramiteYTasaCompletado"]),
    pagination() {
      const items = [...this.filteredData];
      const result = new Array(Math.ceil(items.length / this.cantidadAMostrar))
        .fill()
        .map((_) => items.splice(0, this.cantidadAMostrar));
      return result;
    },
  },
  created() {
    if (this.existenTramites) {
      this.loading = true;
      setTimeout(() => {
        this.extraerTramites().then(() => {
          this.loading = false;
          this.mostrarErrorConexion();
        });
      }, 1500);
    }
  },
  mounted() {
    this.getfilteredData();
  },
  methods: {
    ...mapActions("Listado-tramites", ["extraerTramites"]),
    ...mapMutations("Listado-tramites", ["ADD_MARKED", "REMOVE_MARKED"]),

    ...mapActions("Tramites-seleccionados", [
      "reqAddProcedure",
      "validarTramite",
    ]),
    ...mapActions("Tramites-completados", ["agregarTramiteCompledo"]),
    ...mapActions("Modal", [
      "mostrarModal",
      "mostrarModalTramiteExistente",
      "mostrarModalLimiteTramites",
      "mostrarModalCompletados",
    ]),

    next(page) {},

    reqaddproc(procedure) {
      if (this.reimprimirEfectivo !== null) {
        return this.mostrarModalCompletados();
      }

      if (!this.existeTasa(procedure.id_item) && this.warehouse.length == 10) {
        return this.mostrarModalLimiteTramites();
      } else if (
        this.existeTramiteYTasaSeleccionado(procedure.id, procedure.id_item) ||
        this.existeTramiteYTasaCompletado(procedure.id, procedure.id_item)
      ) {
        return this.mostrarModalTramiteExistente();
      }
      this.reqAddProcedure(JSON.parse(JSON.stringify(procedure))).then(
        (resp) => {
          if (resp === false) {
            this.mostrarModal(procedure);
          }
          if (resp == undefined) {
            if (
              procedure.tipo == "Tasa" &&
              procedure.formulario.campos.length == 0
            ) {
              let tramite = this.warehouse.find(
                (element) => element.id == procedure.id
              );
              let posicion = this.warehouse.indexOf(tramite);
              this.validarTramite(posicion);
              this.agregarTramiteCompledo(
                JSON.parse(JSON.stringify(procedure))
              );
            }
          }
        }
      );
    },
    mostrarErrorConexion() {
      if (this.errorConexion == true) {
        this.renderizado = true;
        this.snackbar = true;
      } else {
        this.snackbar = false;
      }
    },

    resetPage() {
      this.page = 1;
    },
    // Esto deberia de ser una propiedad computada.
    text: (procedures) => procedures.id_item + " — " + procedures.titulo,
    // nuevo
    getfilteredData() {
      let tramitesFiltrados = this.tramites.filter(
        (element) => element.clasificacion == this.clasificacion
      );
      this.filteredData = tramitesFiltrados;
      let filteredDataByfilters = [];
      let filteredDataBySearch = [];

      if (this.switchMark === true) {
        filteredDataByfilters = this.filteredData.filter(
          (obj) => obj.marked === true
        );
        this.filteredData = filteredDataByfilters;
      }

      if (this.filterProc) {
        filteredDataByfilters = this.filteredData.filter(
          (obj) => obj.tipo === this.filterProc
        );
        this.filteredData = filteredDataByfilters;
      }

      if (this.search) {
        filteredDataBySearch = this.filteredData.filter(
          (obj) => obj.id === this.search
        );
        this.filteredData = filteredDataBySearch;
      }

      if (this.filteredData.length !== 0) {
        this.renderizado = true;
      }
      this.page = 1;
    },
  },
  watch: {
    tramites: function() {
      this.getfilteredData();
    },
  },
});
</script>

