import axios from "axios";
import { rgpWebUrl, pagosUrl } from "../../services/paths.js";
import { getPagosOnlinePorTramiteService } from "../../components/consultas/services/consulta.service.js";

const initialState = () => ({
  completados: [],
  reimprimirEfectivo: null,
  reimprimirEfectivoClickeado: null,
  liquidacion: null,
  spinnerMedioDePago: false,
  errorConexion: false,
  modalMedioDePago: false,
  complementaria: null,
  faltaReponer: null,
  reimprimiendo: false,
  consultaPagoOnline: null,
  faltaReponerCargada: null,
  esExento: false,
  esDiferido: false,
  consultaPagoOnlinePorFormulario: [],
  consultaPagoOnlinePorTramite: [],
  token: null
});

const state = initialState();

const getters = {
  validarComplementaria: (state) => {
    return state.complementaria.tasaAbonada[0].tipo == "Tasa";
  },
  calcularTotal: (state) => {
    return state.completados.reduce((resultado, tramite) => {
      resultado += tramite.formulario.resultado;
      return resultado;
    }, 0);
  },
  indiceTramite: (state) => (id) => {
    return state.completados.map((tramite) => tramite.id).indexOf(id);
  },
  reimprimirEfectivo: (state) => {
    return state.reimprimirEfectivo;
  },
  reimprimirEfectivoClickeado: (state) => {
    return state.reimprimirEfectivoClickeado;
  },
  liquidacion: (state) => {
    return state.liquidacion;
  },
  formatearTramitesCompletados: (state) => {
    return state.completados.map((tramite) => {
      if (tramite.tipo == "Publicidad Directa") {
        return {
          id: tramite.id,
          anio: tramite.anio,
          serie: tramite.serie,
          tipo: "",
          formulario: "",
          codigo: tramite.id_item,
          tramite: tramite.leyenda,
          conceptos:
            "Monto fijo (" +
            tramite.formulario.resultado.toFixed(2) +
            ") 0 --1 --",
          //tasa: calcularTotal()
          tasa: `${tramite.formulario.resultado.toFixed(2)}`,
        };
      } else {
        return {
          id: tramite.id,
          anio: tramite.anio,
          serie: tramite.serie,
          tipo: "",
          formulario: "",
          codigo: tramite.id_item,
          tramite: tramite.leyenda,
          conceptos: tramite.formulario.detalle,
          //tasa: calcularTotal()
          tasa: `${tramite.formulario.resultado.toFixed(2).replace(".", ",")}`,
          // tasa: `${tramite.formulario.resultado}`,
        };
      }
    });
  },
  existeTramiteYTasaCompletado: (state) => (idTramite, idTasa) => {
    return state.completados.some(
      (tramite) => tramite.id != idTramite && tramite.id_item == idTasa
    );
  },
};

const mutations = {
  REIMPRIMIR_EFECTIVO(state, formulario) {
    state.reimprimirEfectivo = formulario;
  },
  REIMPRIMIR_EFECTIVO_CLICKEADO(state, clickeado) {
    state.reimprimirEfectivoClickeado = clickeado;
  },
  LIQUIDACION(state, n_liquidacion) {
    state.liquidacion = n_liquidacion;
  },
  AGREGAR_TRAMITE_COMPLETADO(state, tramite) {
    state.completados.push(tramite);
  },
  ELIMINAR_TRAMITE_COMPLETADO(state, indice) {
    state.completados.splice(indice, 1);
  },
  LIMPIAR_TRAMITES_COMPLETADOS(state) {
    state.completados = [];
  },
  MOSTRAR_SPINNER_MEDIO_DE_PAGO(state) {
    state.spinnerMedioDePago = true;
  },
  OCULTAR_SPINNER_MEDIO_DE_PAGO(state) {
    state.spinnerMedioDePago = false;
  },
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach((key) => {
      state[key] = newState[key];
    });
  },
  MOSTRAR_ERROR_CONEXION(state) {
    state.errorConexion = true;
  },
  OCULTAR_ERROR_CONEXION(state) {
    state.errorConexion = false;
  },
  MOSTRAR_MODAL_MEDIODEPAGO(state) {
    state.modalMedioDePago = true;
  },
  OCULTAR_MODAL_MEDIODEPAGO(state) {
    state.modalMedioDePago = false;
  },
  BUSCAR_COMPLEMENTARIA(state, respuesta) {
    state.complementaria = respuesta;
  },
  RESET_COMPLEMENTARIA(state) {
    state.complementaria = null;
  },
  CONSULTAR_FALTA_REPONER(state, respuesta) {
    state.faltaReponer = respuesta;
  },
  RESET_FALTA_REPONER(state) {
    state.faltaReponer = null;
  },
  SET_REIMPRIMIENDO(state, valor) {
    state.reimprimiendo = valor;
  },
  SET_CONSULTA_PAGO_ONLINE(state, valor) {
    state.consultaPagoOnline = valor;
  },
  SET_CONSULTA_PAGO_ONLINE_POR_FORMULARIO(state, valor) {
    state.consultaPagoOnlinePorFormulario = valor
  },
  SET_CONSULTA_PAGO_ONLINE_POR_TRAMITE(state, val) {
    state.consultaPagoOnlinePorTramite = val
  },
  SET_FALTA_REPONER_CARGADA(state, valor) {
    state.faltaReponerCargada = valor;
  },
  ES_EXENTO(state) {
    state.esExento = true;
  },
  NO_EXENTO(state) {
    state.esExento = false;
  },
  ES_DIFERIDO(state) {
    state.esDiferido = true;
  },
  NO_DIFERIDO(state) {
    state.esDiferido = false;
  },
};

const actions = {
  consultarComplementaria({ commit }, comprobante) {
    axios
      .get(`${rgpWebUrl}/complementaria?comprobante=${comprobante}`)
      .then((response) => {
        commit("BUSCAR_COMPLEMENTARIA", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarComplementaria({ commit, dispatch }, datos_y_tasas) {
    axios
      .post(`${rgpWebUrl}/complementaria`, datos_y_tasas)
      .then((response) => {
        commit("REIMPRIMIR_EFECTIVO", response.data.tasas[0]);
        dispatch("obtenerPDF", response.data.tasas[0]);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarTramiteCompledo({ commit }, tramite) {
    if (tramite.formulario.campos.length != 0) {
      tramite.formulario.campos.forEach((renglon, index) => {
        const valorFormula = tramite.formulario.formula
          ? tramite.formulario.formula
          : tramite.formulario.monto_fijo;
        const valorDetalle = tramite.formulario.monto_fijo
          ? `Monto fijo: ${tramite.formulario.monto_fijo}`
          : "";
        tramite.formulario.campos[index].formula_aplicada = valorFormula;
        tramite.formulario.campos[index].detalle_parcial = valorDetalle;
        renglon.campos.forEach((campo) => {
          const formula_aplicada = tramite.formulario.campos[
            index
          ].formula_aplicada.replace(
            new RegExp(campo.nombre_variable, "g"),
            campo.valor
          );
          tramite.formulario.campos[index].formula_aplicada = formula_aplicada;
          if (!tramite.formulario.monto_fijo) {
            tramite.formulario.campos[
              index
            ].detalle_parcial += `${campo.nombre_variable} (${campo.valor}) 0--1 -- `;
          }
        });
      });

      // Resultado parcial
      tramite.formulario.campos.forEach((renglon, index) => {
        const resultado_parcial = eval(
          tramite.formulario.campos[index].formula_aplicada
        );
        tramite.formulario.campos[index].resultado_parcial = resultado_parcial;
        if (
          tramite.formulario.monto_minimo &&
          tramite.formulario.monto_minimo > resultado_parcial
        ) {
          tramite.formulario.campos[index].resultado_parcial =
            tramite.formulario.monto_minimo;
        }
        if (
          tramite.formulario.monto_maximo &&
          tramite.formulario.monto_maximo < resultado_parcial
        ) {
          tramite.formulario.campos[index].resultado_parcial =
            tramite.formulario.monto_maximo;
        }
      });

      // Resultado total y detalle
      tramite.formulario.resultado = 0;
      tramite.formulario.detalle = "";

      // Sumatoria de resultados
      tramite.formulario.campos.forEach((renglon, index) => {
        tramite.formulario.resultado +=
          tramite.formulario.campos[index].resultado_parcial;
        tramite.formulario.detalle +=
          tramite.formulario.campos[index].detalle_parcial;
      });
    } else {
      const valorFormula = tramite.formulario.formula;
      tramite.formulario.resultado = 1 * valorFormula;
      if (
        tramite.formulario.monto_minimo &&
        tramite.formulario.monto_minimo > tramite.formulario.resultado
      ) {
        tramite.formulario.resultado = tramite.formulario.monto_minimo;
      }
      if (
        tramite.formulario.monto_maximo &&
        tramite.formulario.monto_maximo < tramite.formulario.resultado
      ) {
        tramite.formulario.resultado = tramite.formulario.monto_maximo;
      }
    }
    commit("AGREGAR_TRAMITE_COMPLETADO", tramite);
  },
  agregarTramiteCompledoTasa({ commit }, tramite) {
    const valorFormula = tramite.formulario.formula;
    tramite.formulario.resultado = 1 * valorFormula;
    // console.log(tramite.formulario.resultado.toFixed(2));
    commit("AGREGAR_TRAMITE_COMPLETADO", tramite);
  },
  persistirPublicidadDirecta({ commit, dispatch }, datos_y_publicidad) {
    axios
      .post(`${rgpWebUrl}/ingresar-publicidad`, datos_y_publicidad)
      .then((response) => {
        commit("REIMPRIMIR_EFECTIVO", response.data.tasas[0]);
        if (datos_y_publicidad.medioPago == 20) {
          dispatch("obtenerPDF", response.data.tasas[0]);
        } else {
          const tasasMasPago = {
            tasas: [response.data.tasas[0]],
            medioPago: datos_y_publicidad.medioPago,
          };
          dispatch("URLpagoOnline", tasasMasPago);
        }
      })
      .catch((err) => {
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  persistirTasas({ commit, dispatch }, datos_y_tasas) {
    const cidiUser = sessionStorage.getItem("cidiResponse")
    ? JSON.parse(sessionStorage.getItem("cidiResponse"))
    : null
    datos_y_tasas.cuitCidi = cidiUser ? cidiUser.cuit : null
    axios
      .post(`${rgpWebUrl}/ingresarTasa`, datos_y_tasas)
      .then((response) => {
        commit("REIMPRIMIR_EFECTIVO", response.data.tasas[0]);
        if (datos_y_tasas.medioPago == 20) {
          dispatch("obtenerPDF", response.data.tasas[0]);
        } else {
          const tasasMasPago = {
            tasas: [response.data.tasas[0]],
            medioPago: datos_y_tasas.medioPago,
          };
          dispatch("URLpagoOnline", tasasMasPago);
        }
      })
      .catch((err) => {
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  // persistirTasas({commit}, listado_tasas){
  obtenerPDF({ commit }, listado_tasas) {
    let listado = {
      tasas: [listado_tasas],
      idUsuario: 0,
      idServicio: 0,
    };

    const config = {
      responseType: "blob",
    };

    axios
      .post(`${pagosUrl}/portal/pagoPresencial`, listado, config)
      .then((response) => {
        let blob = new Blob([response.data], { type: "application/pdf" });
        let data = URL.createObjectURL(blob);
        window.open(data, "_blank");
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("OCULTAR_MODAL_MEDIODEPAGO");
      })
      .catch((err) => {
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  reimprimirPDFefectivo({ commit }, formulario) {
    let listado = {
      tasas: [formulario],
    };

    const config = {
      responseType: "blob",
    };

    axios
      .post(`${pagosUrl}/portal/pagoPresencial/reimprimir`, listado, config)
      .then((response) => {
        let blob = new Blob([response.data], { type: "application/pdf" });
        let data = URL.createObjectURL(blob);
        window.open(data, "_blank");
      })
      .catch((err) => {
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  reimprimirPDFpagoOnline({ commit }, n_liquida) {
    const config = {
      responseType: "blob",
    };

    axios
      .post(
        `${pagosUrl}/portal/pagoOnline/reimprimir?nroLiquidacion=${n_liquida}`,
        null,
        config
      )
      .then((response) => {
        let blob = new Blob([response.data], { type: "application/pdf" });
        let data = URL.createObjectURL(blob);
        window.open(data, "_blank");
      })
      .catch((err) => {
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  //FUNCION PARA COMPONENTE REIMPRESIONES
  reimprimirPDFOnline({ commit }, n_liquida) {
    const config = {
      responseType: "blob",
    };
    return new Promise((resolve, reject) => {
      axios
        .post(
          `${pagosUrl}/portal/pagoOnline/reimprimir?nroLiquidacion=${n_liquida}`,
          null,
          config
        )
        .then((response) => {
          commit("SET_REIMPRIMIENDO", false);
          let blob = new Blob([response.data], { type: "application/pdf" });
          let data = URL.createObjectURL(blob);
          window.open(data, "_blank");
          resolve();
        })
        .catch((err) => {
          commit("SET_REIMPRIMIENDO", false);
          console.log(state.reimprimiendo);
          commit("MOSTRAR_ERROR_CONEXION");
          console.log(err);
          reject(err.response.data.error);
        });
    })
  },
  URLpagoOnline({ commit }, listado_tasas) {
    let listado = {
      tasas: listado_tasas.tasas,
      medioPago: listado_tasas.medioPago,
      idUsuario: 0,
      idServicio: 0,
    };
    axios
      .post(`${pagosUrl}/portal/pagar`, listado)
      .then((response) => {
        console.log(response.data);
        commit("LIQUIDACION", response.data.liquidacion);
        if (response.data.codigoRespuesta === "00") {
          const urlPago = response.data.urlPago;
          var paramsScreen = [
            "height=" + screen.height,
            "width=" + screen.width,
          ].join(",");
          var popup = window.open(urlPago, "popup_window", paramsScreen);
          popup.moveTo(0, 0);
        } else if (response.data.codigoRespuesta === "99") {
          console.log(response.data.urlPago);
        }
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("OCULTAR_MODAL_MEDIODEPAGO");
      })
      .catch((err) => {
        commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
        commit("MOSTRAR_ERROR_CONEXION");
        console.log(err);
      });
  },
  eliminarTramiteCompledo({ commit, getters }, id_tramite) {
    const indice_tramite = getters.indiceTramite(id_tramite);
    commit("ELIMINAR_TRAMITE_COMPLETADO", indice_tramite);
  },
  limpiarTramitesCompletados({ commit }) {
    commit("LIMPIAR_TRAMITES_COMPLETADOS");
    commit("REIMPRIMIR_EFECTIVO", null);
    commit("REIMPRIMIR_EFECTIVO_CLICKEADO", null);
    commit("LIQUIDACION", null);
  },
  reimprimirClickeado({ commit }) {
    commit("REIMPRIMIR_EFECTIVO_CLICKEADO", 1);
  },
  reset({ commit }) {
    commit("RESET");
  },
  mostrarSpinnerMedioDePago({ commit }) {
    commit("MOSTRAR_SPINNER_MEDIO_DE_PAGO");
  },
  ocultarSpinnerMedioDePago({ commit }) {
    commit("OCULTAR_SPINNER_MEDIO_DE_PAGO");
  },
  mostrarErrorConexion({ commit }) {
    commit("MOSTRAR_ERROR_CONEXION");
  },
  ocultarErrorConexion({ commit }) {
    commit("OCULTAR_ERROR_CONEXION");
  },
  mostrarModalMedioDePago({ commit }) {
    commit("MOSTRAR_MODAL_MEDIODEPAGO");
  },
  ocultarModalMedioDePago({ commit }) {
    commit("OCULTAR_MODAL_MEDIODEPAGO");
  },
  buscarComplementaria({ commit }, respuesta) {
    commit("BUSCAR_COMPLEMENTARIA", respuesta);
  },
  resetComplementaria({ commit }) {
    commit("RESET_COMPLEMENTARIA");
  },
  agregarTramiteComplementaria({ commit }, tramite) {
    commit("AGREGAR_TRAMITE_COMPLEMENTARIA", tramite);
  },
  resetFaltaReponer({ commit }) {
    commit("RESET_FALTA_REPONER");
  },
  siExento({ commit }) {
    commit("ES_EXENTO");
  },
  noExento({ commit }) {
    commit("NO_EXENTO");
  },
  siDiferido({ commit }) {
    commit("ES_DIFERIDO");
  },
  noDiferido({ commit }) {
    commit("NO_DIFERIDO");
  },
  consultarFaltaReponer({ commit, state }, comprobante) {
    axios
      .get(`${rgpWebUrl}/faltaReponer?comprobante=${comprobante}`)
      .then((response) => {
        if (response.data != null && response.data.length != 0) {
          commit("SET_FALTA_REPONER_CARGADA", true);
        }
        commit("CONSULTAR_FALTA_REPONER", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarFaltaReponer({ commit, dispatch }, datos_y_tasas) {
    axios
      .post(`${rgpWebUrl}/faltaReponer`, datos_y_tasas)
      .then((response) => {
        commit("REIMPRIMIR_EFECTIVO", response.data.tasas[0]);
        dispatch("obtenerPDF", response.data.tasas[0]);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  consultarPagoOnline({ commit, dispatch }, datos) {
    commit("SET_REIMPRIMIENDO", true);
    commit("SET_CONSULTA_PAGO_ONLINE", null);
    axios
      .get(
        `${pagosUrl}/consultaPagoOnline?nroLiquidacion=${datos.nroLiquidacion}&nroCuit=${datos.nroCuit}`
      )
      .then((response) => {
        if (response.data != 0) {
          dispatch("reimprimirPDFOnline", datos.nroLiquidacion);
          commit("SET_CONSULTA_PAGO_ONLINE", 1);
        } else {
          commit("SET_CONSULTA_PAGO_ONLINE", 0);
          commit("SET_REIMPRIMIENDO", false);
          commit("OCULTAR_ERROR_CONEXION");
        }
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  consultarPagoOnlinePorFormulario({ commit, dispatch }, datos) {
    commit("SET_REIMPRIMIENDO", true);
    return new Promise((resolve, reject) => {
      axios
        .get(
          `${pagosUrl}/consultaPagoOnlinePorFormulario?nroFormulario=${datos.nroFormulario}&nroCuit=${datos.nroCuit}`
        )
        .then(response => {
          // Verificar si response.data es un string o number (cambia el tipo de dato por ahora inexplicablemente).
          if (typeof response.data === 'string' && response.data.includes(',')) {
            commit('SET_CONSULTA_PAGO_ONLINE_POR_FORMULARIO', response.data.split(',').map(numero => numero.trim()));
          }
          if (typeof response.data === 'number') {
            // Si es un número, convertir a cadena y hacer el dispatch
            // dispatch("reimprimirPDFOnline", nroString);
            resolve(response.data);
          }
          commit("SET_REIMPRIMIENDO", false);
          resolve(response.data);
        })
        .catch((err) => {
          // commit("MOSTRAR_ERROR_CONEXION");
          reject(err.response.data.error);
        });
    })
  },
  async consultarPagoOnlinePorTramite({ commit }, datos) {
    try {
      commit("SET_REIMPRIMIENDO", true);
      const res = await getPagosOnlinePorTramiteService(datos)
      if (res.length > 1) {
        commit("SET_CONSULTA_PAGO_ONLINE_POR_TRAMITE", res)
        return res
      } else {
        commit("SET_REIMPRIMIENDO", false);
        return Number(res[0])
      }
    } catch (error) {
      console.log(error)
    }
  },
  setFaltaReponerCargada({ commit }, valor) {
    commit("SET_FALTA_REPONER_CARGADA", valor);
  },
  setConsultarPagoOnlinePorFormulario({ commit }, valor) {
    commit("SET_CONSULTA_PAGO_ONLINE_POR_FORMULARIO", valor);
  },
  setConsultarPagoOnlinePorTramite({ commit }, val) {
    commit("SET_CONSULTA_PAGO_ONLINE_POR_TRAMITE", val)
  }
};

function elmayorde(x, y) {
  let valor1 = parseInt(x);
  let valor2 = parseInt(y);
  return valor1 > valor2 ? valor1 : valor2;
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
