const initialState = () => ({
  modal: {
    visible: false,
    payload: {},
  },
  modalTramite: false,
  modalLimiteTramites: false,
  modalSeleccionTramites: false,
  modalBloqueoCompletados: false,
  modalSeleccionTramitesComplementaria: false,
  modalSeleccionTramitesFaltaReponer: false,
});

const state = initialState();

const getters = {};

const mutations = {
  SHOW_MODAL(state, payload) {
    state.modal.visible = true;
    state.modal.payload = { ...payload };
  },
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach((key) => {
      state[key] = newState[key];
    });
  },
  SHOW_MODAL_TRAMITE_EXISTENTE(state) {
    state.modalTramite = true;
  },
  SHOW_MODAL_LIMITE_TRAMITES(state) {
    state.modalLimiteTramites = true;
  },
  SHOW_MODAL_SELECCION_TRAMITES(state) {
    state.modalSeleccionTramites = true;
  },
  SHOW_MODAL_SELECCION_TRAMITES_COMPLEMENTARIA(state) {
    state.modalSeleccionTramitesComplementaria = true;
  },
  SHOW_MODAL_SELECCION_TRAMITES_FR(state) {
    state.modalSeleccionTramitesFaltaReponer = true;
  },
  SHOW_MODAL_COMPLETADOS(state) {
    state.modalBloqueoCompletados = true;
  },
};

const actions = {
  mostrarModal({ commit }, tramite) {
    commit("SHOW_MODAL", tramite);
  },
  resetearModal({ commit }) {
    commit("RESET");
  },
  mostrarModalTramiteExistente({ commit }) {
    commit("SHOW_MODAL_TRAMITE_EXISTENTE");
  },
  mostrarModalLimiteTramites({ commit }) {
    commit("SHOW_MODAL_LIMITE_TRAMITES");
  },
  mostrarModalSeleccionTramites({ commit }) {
    commit("SHOW_MODAL_SELECCION_TRAMITES");
  },
  mostrarModalSeleccionTramitesFR({ commit }) {
    commit("SHOW_MODAL_SELECCION_TRAMITES_FR");
  },
  mostrarModalCompletados({ commit }) {
    commit("SHOW_MODAL_COMPLETADOS");
  },
  mostrarModalSeleccionTramitesComplementaria({ commit }) {
    commit("SHOW_MODAL_SELECCION_TRAMITES_COMPLEMENTARIA");
  },
  resetModalTramiteExistente({ commit }) {
    commit("RESET");
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
