<template>
  <v-card outlined class="item-busqueda">
    <v-list-item three-line>
      <v-list-item-content>
        <div class="overline mb-4 textoTitulo">
          {{ titulo }}
        </div>
        <div class="d-flex justify-space-between align-center">
          <!-- <v-list-item-subtitle> -->
          <div class="textoDescripcion">
            {{ descripcion }}
          </div>
          <!-- </v-list-item-subtitle> -->
        </div>
        <div class="mt-3 d-flex justify-center">
          <router-link
            :to="{
              name: `${this.tipo.charAt(0)}tramites`,
              params: { id: this.enlace },
            }"
          >
            <v-btn outlined small class="ml-1" justify="end" color="primary">
              ver más
            </v-btn>
          </router-link>
        </div>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
        class="d-none d-sm-flex marco-img"
        color="primary"
      >
        <img
          :src="require(`../../../assets/img/${this.tipo}.svg`)"
          class="pa-2"
          alt=""
        />
      </v-list-item-avatar>
    </v-list-item>
  </v-card>
</template>
<script>
import Vue from "vue";

export default Vue.extend({
  name: "TramiteCoincidente",
  props: {
    titulo: {
      type: String,
      default: "",
    },
    busqueda: {
      type: String,
      default: "",
    },
    descripcion: {
      type: String,
      default: "",
    },
    tipo: {
      type: String,
      default: "",
    },
    enlace: {
      type: String,
      default: "",
    },
  },
  computed: {
    busquedaRealizada() {
      return this.busqueda;
    },
  },
  data: () => ({
    palabras_omitidas: [
      " a ",
      " o ",
      " de ",
      " del ",
      " un ",
      " una ",
      " uno ",
      " el ",
      " la ",
      " los ",
      " las ",
      " es ",
      " son ",
      " con ",
      " por ",
      " para ",
      " y ",
      " A ",
      " O ",
      " DE ",
      " DEL ",
      " UN ",
      " UNA ",
      " UNO ",
      " EL ",
      " LA ",
      " LOS ",
      " LAS ",
      " ES ",
      " SON ",
      " CON ",
      " POR ",
      " PARA ",
      " Y ",
      " - "
    ],
  }),
  methods: {
    remarcarTextoBuscado() {
      let busquedaResumida = this.busqueda;
      this.palabras_omitidas.forEach((element) => {
        busquedaResumida = busquedaResumida.replace(element, " ");
      });
      const titulos = document.querySelectorAll(".textoTitulo");
      let arrayBusqueda = busquedaResumida.split(" ");
      titulos.forEach((el) => {
        let titulo = el.innerText;
        arrayBusqueda.forEach((element) => {
          titulo = titulo.replace(
            new RegExp(`(${element})`, "i"),
            "<span style='background-color:yellow'>$1</span>"
          );
        });
        el.innerHTML = titulo;
      });

      const descripciones = document.querySelectorAll(".textoDescripcion");
      descripciones.forEach((el) => {
        let descripcion = el.innerText;
        arrayBusqueda.forEach((element) => {
          descripcion = descripcion.replace(
            new RegExp(`(${element})`, "i"),
            "<span style='background-color:yellow'>$1</span>"
          );
        });
        el.innerHTML = descripcion;
      });
    },
  },
  mounted() {
    this.remarcarTextoBuscado();
  },
  watch: {
    busquedaRealizada() {
      this.remarcarTextoBuscado();
    },
  },
});
</script>

<style scoped>
.item-busqueda .overline {
  line-height: 1.2rem;
}
.marco-img {
  border-radius: 5px !important;
}
.tituloRemarcado {
  font-weight: bolder;
  color: red !important;
}
</style>
