import axios from "axios";
import { rgpWebUrl } from "../../services/paths.js";

const initialState = () => ({
  listadoNovedades: null,
  errorConexion: false,
});

const state = initialState();

const getters = {
  existenNovedades: (state) => {
    return state.listadoNovedades != null;
  },
};

const mutations = {
  AGREGAR_NOVEDADES(state, procedure) {
    state.listadoNovedades = [...procedure];
  },
  MOSTRAR_ERROR_CONEXION(state) {
    state.errorConexion = true;
  },
  OCULTAR_ERROR_CONEXION(state) {
    state.errorConexion = false;
  },
  CREAR_NOVEDAD(state, novedad) {
    state.listadoNovedades.push(novedad);
  },
  MODIFICAR_NOVEDAD(state, parametros) {
    let posicion = parametros.posicion;
    let novedad = parametros.novedad;
    state.listadoNovedades.splice(posicion, 1, { ...novedad });
  },
};

const actions = {
  extraerNovedades({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/novedades?id  `)
      .then((response) => {
        commit("AGREGAR_NOVEDADES", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarNovedad({ commit, dispatch }, novedad) {
    axios
      .post(`${rgpWebUrl}/novedades`, novedad)
      .then((response) => {
        dispatch("extraerNovedades");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarNovedad({ commit, dispatch }, id) {
    axios
      .delete(`${rgpWebUrl}/novedad/${id}`)
      .then((response) => {
        console.log(response.data);
        dispatch("extraerNovedades");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarNovedad({ commit, dispatch }, novedad) {
    axios
      .put(`${rgpWebUrl}/novedades`, novedad)
      .then((response) => {
        if (response.data != "") {
          commit("MOSTRAR_ERROR_CONEXION");
        } else {
          commit("OCULTAR_ERROR_CONEXION");
        }
        //console.log("agregar")
        dispatch("extraerNovedades");
      })
      .catch(() => {
        console.log("error");
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
