<template>
  <div class="container">
    <div class="informes">
      <TitleSection title="Edición de requisitos y pasos"></TitleSection>
    </div>
    <!-- FORMULARIOS CREACION/EDICION ITEMS GENERICOS-->
    <div class="panelsheader  usrfrm" id="formularioListado">
      <v-expansion-panels v-model="panelGenericos">
        <v-expansion-panel>
          <v-expansion-panel-header>
            PASOS/REQUERIMIENTOS Genéricos
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <ItemsGenericos />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
    <!-- FORMULARIO INICIAL SELECCION TRAMITE-->
    <div class="panelsheader panelInferior usrfrm">
      <v-expansion-panels v-model="panelEleccion">
        <v-expansion-panel>
          <v-expansion-panel-header>
            elección del tramite
          </v-expansion-panel-header>
          <v-expansion-panel-content>
             <EleccionTramite />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
    <!-- FORMULARIOS EDICION TRAMITE-->
    <div class="panelsheader panelInferior usrfrm pl-0" id="formularioListado">
      <v-expansion-panels v-model="panelEdicion" :readonly="anularExpansionPanel">
        <v-expansion-panel @click="openEditionPanel()" >
          <v-expansion-panel-header>
            edición del tramite
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <EdicionTramite />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
    <!-- SNACKBAR -->
    <v-snackbar
      v-model="snackbar"
      centered
      color="red"
      timeout="5000"
      outlined
      text
      height="60"
    >
      <v-row class="snackbar">
        <v-col>
          {{mensajeSnackbar}}
        </v-col>
      </v-row>
      <v-row class="snackbar">
        <v-col>
          <v-btn class="botonSnackbar" text @click="snackbar = false">
            ACEPTAR
          </v-btn>
        </v-col>
      </v-row>
    </v-snackbar>
  </div>
</template>

<script>
import Vue from "vue";
import { mapActions, mapState } from "vuex";
import TitleSection from "../shared/body/TitleSection.vue";
import { EventBus } from "../../eventBus.js";
import ItemsGenericos from "./componentesRequisitosAdmin/ItemsGenericos.vue";
import EleccionTramite from "./componentesRequisitosAdmin/EleccionTramite.vue";
import EdicionTramite from "./componentesRequisitosAdmin/EdicionTramite.vue";


export default Vue.extend({
  name: "adminRequisitos",
  components: {
    TitleSection,
    ItemsGenericos,
    EleccionTramite,
    EdicionTramite 
  },
  data: () => ({
    panelEleccion: 0,
    snackbar : false,
    panelEdicion: false,
    mensajeSnackbar: "",
    anularExpansion: true,
    panelGenericos: 0,
    datosAModificar: ["Paso a paso", "Requerimientos"],
    idTramiteSeleccionado: null,
  }),
  created() {
    try {
      this.extraerRequerimientosGenericos()
      this.extraerPasosGenericos()
    } catch (error) {
      this.snackbar = true
      this.mensajeSnackbar = "Hubo un error en la conexión. Por favor intente nuevamente."
      return;
    }

    EventBus.$on("abrirPanelEdicion", () => {
      this.abrirPanel();
    });
     EventBus.$on("cerrarPanelEdicion", () => {
      this.cerrarPanel();
    });

  },
  mounted(){
    this.anularFormularioEleccion()
  },
 
  computed: {
    ...mapState("Listado-tramites", ["tramites", "anularExpansionPanel", 
            "formularioEleccionValidado"]),
  },

  methods: {
    ...mapActions("Listado-tramites", ["extraerRequerimientosGenericos","habilitarPanelEdicionTramite", 
              "anularFormularioEleccion", "extraerPasosGenericos"]),
    abrirPanel(){
      this.panelEdicion = 0
    },
    cerrarPanel(){
      this.panelEdicion = 1
    },
    openEditionPanel(){
        if (this.formularioEleccionValidado != true) {     
          this.snackbar = true
          this.mensajeSnackbar = "Debe completar el formulario."
          return
        }else{
          this.habilitarPanelEdicionTramite()
      
        }
    }
    
    
  },
});
</script>
