<template>
  <v-dialog v-model="modal.visible" persistent max-width="450">
    <v-card>
      <v-card-title class="headline justify-center">
        <span>Reemplazo de formularios</span>
      </v-card-title>

      <v-divider></v-divider>

      <v-card-text class="pt-5 font-weight-medium subtitle-2">
        Los formularios y campos cargados en la sección de tasas se perderán.
      </v-card-text>

      <v-card-actions>
        <v-btn color="red darken-1" text @click="cerrar">
          Cancelar
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="aplicar(modal.payload)">
          Aceptar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import { mapState, mapActions } from "vuex";

export default Vue.extend({
  name: "ModalItemSelection",
  computed: {
    ...mapState("Modal", {
      modal: "modal",
    }),
  },
  methods: {
    ...mapActions("Tramites-seleccionados", [
      "limpiarAgregarTramiteWH",
      "eliminarDatosIngresadosPorUsuario",
    ]),
    ...mapActions("Tramites-completados", ["limpiarTramitesCompletados"]),
    ...mapActions("Modal", {
      cerrar: "resetearModal",
    }),
    aplicar(tramite) {
      this.cerrar();
      this.limpiarTramitesCompletados();
      this.eliminarDatosIngresadosPorUsuario();
      this.limpiarAgregarTramiteWH(tramite);
    },
  },
});
</script>

<style scoped>
.v-application .headline {
  font-size: 1.3rem !important;
}

.headline {
  font-weight: 400;
}

.icon-alert {
  font-size: 32px;
}
</style>
