<script>
import Vue from "vue";
import { mapActions, mapState } from "vuex";

export default Vue.extend({
  name: "InformacionUsuarioCidi",
  methods: {
    ...mapActions("Logincidi", ["logout"]),
    logOut(){
      if(this.$route.name.includes("admin")){
        this.$router.push("/")
      }
      this.logout()
    }
  },
  computed: {
    ...mapState("Logincidi", ["usuarioCidi", "usuarioInterno"]),
    sexo() {
      if (this.usuarioCidi.cidiResponseLogin.Id_Sexo === "02") {
        return "https://cidi.cba.gov.ar/portal/assets/img/avatars/perfil-femenino.svg";
      } else {
        return "https://cidi.cba.gov.ar/portal/assets/img/avatars/perfil-masculino.svg";
      }
    },
  },
});
</script>

