<template>
  <div>
    <v-dialog
      v-model="modalMedioDePago"
      persistent
      max-width="700"
      min-height="400"
      color="primary"
      class="modal"
    >
      <v-card class="modal" v-if="!spinnerMedioDePago">
        <v-card-title class="headline justify-center">
          <span>Seleccione el medio de pago</span>
        </v-card-title>
        <v-row v-if="errorConexion">
          <v-alert
            text
            outlined
            color="red"
            class="mx-1 py-2 ml-3 mr-3 mb-0 text-center textAlert"
            width="100%"
            close-icon="close"
            border="right"
          >
            Hubo un error en la conexión. Por favor intente nuevamente.
          </v-alert>
        </v-row>
        <div class="panelsheader  usrfrm">
          <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header
                class="textoOpcion"
                @click="seleccionarMedioDePago(20)"
                expand-icon=""
              >
                EFECTIVO
              </v-expansion-panel-header>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
        <div class="panelsheader  usrfrm">
          <v-expansion-panels v-model="panel">
            <v-expansion-panel>
              <v-expansion-panel-header>
                ELECTRÓNICO
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-card elevation="2" class="mt-4 pl-6 pt-3 pb-3 pr-6">
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                      v-for="opcion in mediosDePago"
                      :key="opcion.nombre"
                    >
                      <v-btn
                        @click="seleccionarMedioDePago(opcion.idEntidad)"
                        class="botonOpcionPago"
                      >
                        <v-row class="contenidoBotonOpcionPago">
                          <div>{{ opcion.nombre }}</div>
                          <img
                            class="imgOpcionPago"
                            :alt="opcion.alt"
                            src="../../assets/img/icono_visa_debito.png"
                            v-if="opcion.nombre == 'PAGO VISA DEBITO'"
                          />
                          <img
                            class="imgOpcionPago"
                            :alt="opcion.alt"
                            src="../../assets/img/boton_masterdebit.png"
                            v-if="opcion.nombre == 'PAGO MASTER DEBITO'"
                          />
                          <img
                            class="imgOpcionPago"
                            :alt="opcion.alt"
                            src="../../assets/img/interbanking.jpg"
                            v-if="opcion.nombre == 'INTERBANKING'"
                          />
                          <img
                            class="imgOpcionPago"
                            :alt="opcion.alt"
                            src="../../assets/img/redLink.jpg"
                            v-if="opcion.nombre == 'RED LINK'"
                          />
                        </v-row>
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-card>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
        <v-row class="d-flex align-end mt-4 justify-center">
          <v-btn @click="ocultarModal()" class="botonModal">Cancelar</v-btn>
        </v-row>
      </v-card>
      <div
        v-if="spinnerMedioDePago"
        class="final d-flex align-center justify-center"
      >
        <v-card
          outlined
          height="100%"
          min-height="200px"
          width="100%"
          class="d-flex align-center justify-center"
        >
          <div>
            <v-progress-circular
              :size="40"
              :width="5"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </div>
        </v-card>
      </div>
    </v-dialog>
  </div>
</template>

<script>
import Vue from "vue";
import { mapActions, mapState, mapGetters } from "vuex";

export default Vue.extend({
  name: "ModalMedioDePago",
  computed: {
    ...mapState("Modal", ["modalMedioDePago"]),
    ...mapState("Tramites-seleccionados", [
      "datosIngresadosPorUsuario",
      "mediosDePago",
    ]),
    ...mapState("Tramites-completados", [
      "spinnerMedioDePago",
      "errorConexion",
      "modalMedioDePago",
    ]),
    ...mapGetters("Tramites-seleccionados", ["existenMediosDePago"]),
    opcionesDePago() {
      return this.mediosDePago;
    },
  },
  created() {
    this.mostrarSpinnerMedioDePago();
    try {
      this.extraerMediosDePago();
    } catch (error) {
      this.mostrarErrorConexion();
      this.ocultarSpinnerMedioDePago();
      return;
    }
  },
  data: () => ({
    showAlert: false,
    panel: 1,
    loading: false,
  }),
  watch: {
    opcionesDePago() {
      this.ocultarSpinner();
    },
  },
  methods: {
    ...mapActions("Tramites-completados", ["ocultarModalMedioDePago"]),
    ...mapActions("Tramites-completados", [
      "persistirPublicidadDirecta",
      "persistirTasas",
      "mostrarSpinnerMedioDePago",
      "ocultarSpinnerMedioDePago",
      "mostrarErrorConexion",
      "ocultarErrorConexion",
    ]),
    ...mapActions("Tramites-seleccionados", ["extraerMediosDePago"]),
    seleccionarMedioDePago(param) {
      this.mostrarSpinnerMedioDePago();
      this.ocultarErrorConexion();
      let datosPorConsola = this.datosIngresadosPorUsuario;
      datosPorConsola.medioPago = param;
      try {
        if (Array.isArray(datosPorConsola.items)) {
          this.persistirTasas(datosPorConsola);
        } else {
          this.persistirPublicidadDirecta(datosPorConsola);
        }
      } catch (error) {
        this.mostrarErrorConexion();
        this.ocultarSpinnerMedioDePago();
        return;
      }
      this.panel = 1;
    },
    ocultarSpinner() {
      if (this.mediosDePago != {}) {
        this.ocultarSpinnerMedioDePago();
      }
    },
    ocultarModal() {
      this.ocultarModalMedioDePago();
      this.ocultarErrorConexion();
      this.panel = 1;
    },
  },
});
</script>
