<template>
  <v-container>
    <div class="container informes px-5">
      <TitleSection title="Falta reponer"></TitleSection>
      <div class="panelsheader pt-3">
        <v-expansion-panels class="pb-0" v-model="panelBusqueda">
          <v-expansion-panel>
            <v-expansion-panel-header
              >Búsqueda por Número Único</v-expansion-panel-header
            >
            <v-expansion-panel-content>
              <BusquedaFaltaReponer />
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
      <div class="panelsheader pt-3">
        <v-expansion-panels
          class="pb-0"
          v-model="panelRespuesta"
          :readonly="anularPanelRespuesta"
        >
          <v-expansion-panel @click="abrirPanelRespuesta()">
            <v-expansion-panel-header
              >Datos del usuario</v-expansion-panel-header
            >
            <v-expansion-panel-content>
              <UsuarioFaltaReponer />
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
      <div class="panelsheader pt-3">
        <v-expansion-panels
          class="pb-5"
          v-model="panelTasas"
          :readonly="anularPanelTasas"
        >
          <v-expansion-panel @click="abrirPanelRespuesta()">
            <v-expansion-panel-header
              >Listado de tasas</v-expansion-panel-header
            >
            <v-expansion-panel-content>
              <div class="d-flex justify-end">
                <v-divider color="primary" class="linea ml-1 mt-8"></v-divider>
                <v-btn
                  tile
                  depressed
                  color="primary"
                  class="form-inmueble px-3 mt-4"
                  @click="mostrarModalSeleccionTramitesFR()"
                >
                  Agregar items
                </v-btn>
              </div>
              <div v-if="this.existeFaltaReponer('Tasa')">
                <ValuationSection />
              </div>
              <TasasFaltaReponer />
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
      <v-snackbar
        v-model="snackbar"
        centered
        color="red"
        timeout="0"
        outlined
        text
        height="60"
      >
        <v-row class="snackbar">
          <v-col>
            {{ mensajeSnackbar }}
          </v-col>
        </v-row>
        <v-row class="snackbar">
          <v-col>
            <v-btn class="botonSnackbar" text @click="aceptarSnackbar">
              ACEPTAR
            </v-btn>
          </v-col>
        </v-row>
      </v-snackbar>
    </div>
    <ModalSeleccionTramitesFaltaReponer />
  </v-container>
</template>

<script>
import Vue from "vue";
import { mapGetters, mapState, mapActions } from "vuex";
import TitleSection from "../components/shared/body/TitleSection.vue";
import BusquedaFaltaReponer from "../components/admin/componentesFaltaReponer/BusquedaFaltaReponer";
import UsuarioFaltaReponer from "../components/admin/componentesFaltaReponer/UsuarioFaltaReponer";
import TasasFaltaReponer from "../components/admin/componentesFaltaReponer/TasasFaltaReponer";
import ModalSeleccionTramitesFaltaReponer from "../components/utils/ModalSeleccionTramitesFaltaReponer";
import ValuationSection from "../components/emision-tasas/ValuationSection";

export default {
  name: "faltaReponer",
  components: {
    TitleSection,
    BusquedaFaltaReponer,
    UsuarioFaltaReponer,
    TasasFaltaReponer,
    ModalSeleccionTramitesFaltaReponer,
    
    ValuationSection
  },
  computed: {
    ...mapState("Tramites-completados", ["faltaReponer", "errorConexion"]),
    ...mapGetters("Tramites-completados", ["validarComplementaria"]),
    ...mapGetters("Tramites-seleccionados", {
      existeTipoTramiteWH: "existeTipoTramiteWH",
    }),
    busquedaValidada() {
      return this.faltaReponer;
    },
      errorDeConexion(){
      return this.errorConexion;
    }
  },
  created() {
    this.extraerTiposDeUsuario();
    this.extraerLeyes();
    this.resetearWarehouse();
    this.limpiarTramitesCompletados();
  },

  data: () => ({
    panelBusqueda: 0,
    panelRespuesta: 1,
    anularPanelRespuesta: true,
    anularPanelTasas: true,
    panelTasas: 1,
    mensajeSnackbar: "",
    snackbar: false,
  }),
  methods: {
    ...mapActions("Tramites-seleccionados", [
      "extraerTiposDeUsuario",
      "extraerLeyes",
      "resetearWarehouse"
    ]),
    ...mapActions("Tramites-completados", ["limpiarTramitesCompletados", "ocultarErrorConexion"]),
    ...mapActions("Modal", ["mostrarModalSeleccionTramitesFR"]),
    existeFaltaReponer(tipo) {
      return this.existeTipoTramiteWH(tipo);
    },
    habilitarPanel() {
      if ( this.faltaReponer == null || this.faltaReponer.length == 0 ) {
        this.anularPanelRespuesta = true;
        this.anularPanelTasas = true;
        this.panelTasas = 1;
        this.panelRespuesta = 1;
      } else {
        let ultimo = this.faltaReponer.length - 1;
        if(this.faltaReponer[ultimo].tasa_total_exenta != 0){
          this.anularPanelRespuesta = true;
          this.anularPanelTasas = true;
          this.panelTasas = 1;
          this.panelRespuesta = 1;
        } else {
          this.anularPanelRespuesta = false;
          this.anularPanelTasas = false;
          this.panelTasas = 0;
          this.panelRespuesta = 0;
        }
      }
    },
    snackbarConexion(){
      if(this.errorConexion == true){
        this.snackbar = true;
        this.mensajeSnackbar = "Hubo un error en la conexión. Por favor intente nuevamente."
      }else{
        this.snackbar = false;
        this.mensajeSnackbar = "";
      }
    },
    aceptarSnackbar(){
      this.mensajeSnackbar = "";
      this.snackbar= false;
      this.ocultarErrorConexion();
    },
    abrirPanelRespuesta() {
      if ( this.faltaReponer == null || this.faltaReponer.length == 0) {
        this.snackbar = true;
        this.anularPanelRespuesta = true;
        this.mensajeSnackbar = "Debe realizar una búsqueda primero.";
        return;
      } else {
        this.anularPanelRespuesta = false;
      }
    },
  },
  watch: {
    busquedaValidada() {
      this.habilitarPanel();
    },
    errorDeConexion(){
      this.snackbarConexion();
    }
  },
};
</script>
<style scoped>
.snackbar {
  text-align: center;
  display: flex;
  align-items: center;
}
.botonSnackbar {
  color: red !important;
}
</style>
