<template>
  <div class="panelsheader">
    <v-expansion-panels v-model="panel">
      <v-expansion-panel>
        <v-expansion-panel-header
          >Prórrogas del documento</v-expansion-panel-header
        >
        <v-expansion-panel-content>
          <v-data-table
            :headers="headers"
            :items="prorrogas"
            :header-props="{ sortIcon: null }"
            :items-per-page="5"
            item-key="name"
            class="elevation-1 mt-3 tablaMedida"
          >
            <template v-slot:[`item.fechaProcesamiento`]="{ item }">
              {{ item.fechaProcesamiento | fecha }}
            </template>

            <template v-slot:[`item.fechaResolucion`]="{ item }">
              {{ item.fechaResolucion | fecha }}
            </template>

            <template v-slot:[`item.numeroResolucion`]="{ item }">
              {{ item.numeroResolucion | numResolucion }}
            </template>

            <template v-slot:footer>
              <v-divider></v-divider>
              <div class="py-3 cantDias">
                Cantidad total de días de prórroga:

                <v-chip color="error ml-1" dark>
                  {{ cantidadProrrogas }}
                </v-chip>
              </div>
              <v-divider></v-divider>
            </template>
          </v-data-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import Vue from "vue";
import moment from "moment";
import { mapGetters } from "vuex";

export default Vue.extend({
  name: "ProrrogasDocumento",
  data: () => ({
    panel: 0,
    headers: [
      {
        text: "Fecha observación",
        align: "center",
        value: "fechaProcesamiento",
        sortable: false,
      },
      {
        text: "Días",
        align: "center",
        value: "cantidadDias",
      },
      {
        text: "Tipo",
        align: "center",
        value: "prorroga",
      },
      {
        text: "Resolución",
        align: "center",
        value: "numeroResolucion",
        sortable: false,
      },
      {
        text: "Fecha resolución",
        align: "center",
        value: "fechaResolucion",
        sortable: false,
      },
      {
        text: "Causas",
        align: "center",
        value: "causa",
        sortable: false,
      },
    ],
  }),
  computed: {
    ...mapGetters("Consulta-documentos", ["prorrogas", "cantidadProrrogas"]),
  },
  filters: {
    fecha: function(value) {
      if (!value) return "";
      return moment(String(value)).format("DD/MM/YYYY");
    },
    numResolucion: function(value) {
      if (value <= 0) return null;
      return value;
    },
  },
});
</script>

<style scoped>
.v-data-table >>> .v-data-table__wrapper > table > tbody > tr > th,
.v-data-table >>> .v-data-table__wrapper > table > thead > tr > th,
.v-data-table >>> .v-data-table__wrapper > table > tfoot > tr > th {
  font-size: 0.85rem;
}
.theme--light.v-divider >>> {
  border-color: rgba(0, 0, 0, 0.5);
}
.cantDias {
  font-size: 0.875rem;
}
</style>
