<template>
  <div>
    <v-row class=" mx">
      <v-col cols="12" :md="columnasListadoItemsTramite" class="pb-0 pt-0">
        <div></div>
        <v-row class="mx-titulo">
          <v-col cols="auto" class="titulo-margen ml-4 px-0">
            <v-card outlined>
              <div class="titulo py-2 px-3">
                <div v-if="tramiteEditable.tipo == 'Paso a paso'">
                  Pasos del trámite
                </div>
                <div v-else>
                  Requerimientos del trámite
                </div>
              </div>
            </v-card>
