        </v-row>
        <v-card elevation="2" class="text-xs-center pt-5 mb-3 vCardResultados">
          <v-form class="px-3">
            <v-list
              dense
              v-if="
                itemsTramiteEditable.length != 0 ||
                  tramiteEditable.tipo == 'Paso a paso'
              "
            >
              <v-list-item
                v-for="(opcion, index) in itemsTramiteEditable"
                :key="opcion.id"
              >
                <v-row class="rowListItem">
                  <v-col cols="1" class="d-flex align-center">
                    <v-list-item-icon>
                      <div class="iconoListado">
                        {{ (indice = index + 1) }}
