                  <v-col cols="8" md="9" class="pt-2">
                    <v-row width="100%">
                      <v-col
                        cols="12"
                        md="3"
                        v-if="tramiteEditable.tipo == 'Paso a paso'"
                        >[Orden: {{ opcion.orden }}]</v-col
                      >
                      <v-col cols="12" md="8">
                        {{ opcion.descripcion }}
                      </v-col>
                    </v-row>
                  </v-col>
                  <v-col cols="3" md="2" class="pt-4">
                    <v-row class="position">
                      <v-btn
                        @click="mostrarEditarItem(opcion, index)"
