                        dark
                        x-small
                        fab
                        class="mx-2"
                      >
                        <v-icon>delete</v-icon>
                      </v-btn>
                    </v-row>
                  </v-col>
                </v-row>
              </v-list-item>
              <v-list-item
                v-if="
                  tramiteEditable.tipo == 'Paso a paso' &&
                    tramiteEditable.solicitud != 'Presencial'
                "
              >
                <v-row class="rowListItem">
                  <v-col cols="1" class="d-flex align-center">
                    <v-list-item-icon color="blue-grey">
                      <v-icon>mdi-link-variant</v-icon>
