                  <v-col cols="3" sm="2">
                    <v-row class="position d-flex justify-content-center pr-0">
                      <v-btn
                        tile
                        width="72px"
                        class="mr-2 botonLink"
                        color="primary"
                        @click="editarLinkTramite"
                      >
                        <v-icon>
                          mdi-pencil
                        </v-icon>
                      </v-btn>
                    </v-row>
                  </v-col>
                </v-row>
              </v-list-item>
            </v-list>
            <v-row class="d-flex justify-center vCardResultados" v-else>
              <div class="d-flex align-self-center">
                No se encontraron resultados
              </div>
            </v-row>
            <v-row class="d-flex justify-center">
              <v-btn
                @click="mostrarCrearItem()"
                color="primary"
                outlined
                class="mb-5"
              >
                Crear opción
              </v-btn>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
      <v-col cols="12" md="6" class="pb-0 pt-0">
        <!-- EDICION ITEM EN TRAMITE -->
        <v-col cols="12" v-if="edicionItemTramite" class="pb-0 pl-0 pr-0 pt-0">
          <v-row class="mx-titulo">
            <v-col cols="auto" class="titulo-margen ml-4 px-0">
              <v-card outlined>
                <div class="titulo py-2 px-3">
                  {{ textoTitleEdition }}
                </div>
              </v-card>
            </v-col>
          </v-row>
          <v-card
            elevation="2"
            class="text-xs-center pt-5 mb-3 vCardResultados"
          >
            <v-form ref="formularioEdicion" class="px-3">
              <v-row class="d-flex justify-space-around rowEdition">
                <v-alert
                  text
                  outlined
                  color="red"
                  class="mx-1 mt-2 mb-2 py-2 text-center"
                  width="100%"
                  close-icon="close"
                  border="right"
                  v-if="showAlertEdicion"
                >
                  Debe completar el campo "orden".
                </v-alert>
                <v-col
                  cols="4"
                  sm="3"
                  class="px-3 pt-1 pb-0"
                  v-if="tramiteEditable.tipo == 'Paso a paso'"
                >
                  <v-col
                    cols="12"
                    md="12"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Orden n°
                    </label>
                  </v-col>
                  <v-text-field
                    v-model="orden"
                    outlined
                    dense
                    :placeholder="ordenPlaceholder"
                    autocomplete="off"
                    :rules="requiredRule"
                    required
                    @keypress="onlyNumber($event)"
                    @keyup="validarNumeros(orden, 'orden')"
                    class="pt-1"
                  >
                  </v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  :sm="columnasDescripcionItem"
                  class="px-3 pt-1 pb-0"
                >
                  <v-col
                    cols="12"
                    md="12"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      DESCRIPCIÓN
                    </label>
                  </v-col>
                  <v-autocomplete
                    v-model="nuevaDescripcion"
                    :placeholder="placeholderDescripcion"
                    item-text="descripcion"
                    item-value="id"
                    :items="listadoItems"
                    dense
                    outlined
                    :disabled="habilitarEleccionDescripcion"
                    prepend-icon="search"
                    class="sm-6 pt-1"
                    color="primary"
                    clearable
                    @change="seleccionItem"
                    append-icon
                  >
                  </v-autocomplete>
                </v-col>
              </v-row>
              <v-row class="rowBotones d-flex justify-space-between">
                <v-btn
                  @click="cancelarEditarItemTramite()"
                  color="primary"
                  outlined
                  class="mb-5"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  @click="confirmarEditarTramite()"
                  color="primary"
                  outlined
                  class="mb-5"
                >
                  Editar
                </v-btn>
              </v-row>
            </v-form>
          </v-card>
        </v-col>
        <!-- CREACION ITEM EN TRAMITE -->
        <v-col cols="12" v-if="creacionItemTramite" class="pb-0 pl-0 pr-0 pt-0">
          <v-row class="mx-titulo">
            <v-col cols="auto" class="titulo-margen ml-4 px-0">
              <v-card outlined>
                <div class="titulo py-2 px-3">
                  {{ textoTitleEdition }}
                </div>
              </v-card>
            </v-col>
          </v-row>
          <v-card
            elevation="2"
            class="text-xs-center pt-5 mb-3 vCardResultados"
          >
            <v-form ref="formularioEdicion" class="px-3">
              <v-row class="d-flex justify-space-around rowEdition">
                <v-alert
                  text
                  outlined
                  color="red"
                  class="mx-1 mt-2 mb-2 py-2 text-center"
                  width="100%"
                  close-icon="close"
                  border="right"
                  v-if="showAlertEdicion"
                >
                  Debe completar todos los campos.
                </v-alert>
                <v-col
                  cols="4"
                  sm="3"
                  class="px-3 pt-1 pb-0"
                  v-if="tramiteEditable.tipo == 'Paso a paso'"
                >
                  <v-col
                    cols="12"
                    md="12"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Orden n°
                    </label>
                  </v-col>
                  <v-text-field
                    v-model="orden"
                    outlined
                    dense
                    :placeholder="ordenPlaceholder"
                    autocomplete="off"
                    :rules="requiredRule"
                    required
                    @keypress="onlyNumber($event)"
                    @keyup="validarNumeros(orden, 'orden')"
                    class="pt-1"
                  >
                  </v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  :sm="columnasDescripcionItem"
                  class="px-3 pt-1 pb-0"
                >
                  <v-col
                    cols="12"
                    md="12"
                    class="px-1 py-0 label-height d-flex flex-align-column align-end pb-0"
                  >
                    <label class="v-label v-label--active theme--light">
                      Seleccionar descripción
                    </label>
                  </v-col>
                  <v-autocomplete
                    v-model="nuevaDescripcion"
                    :placeholder="placeholderDescripcion"
                    item-text="descripcion"
                    item-value="id"
                    :items="listadoItems"
                    dense
                    outlined
                    :disabled="habilitarEleccionDescripcion"
                    prepend-icon="search"
                    class="sm-6 pt-1"
                    color="primary"
                    clearable
                    @change="seleccionItem"
                    required
                    :rules="requiredRule"
                    append-icon
                  >
                  </v-autocomplete>
                </v-col>
              </v-row>
              <v-row class="rowBotones d-flex justify-space-between">
                <v-btn
                  @click="cancelarCrearItemTramite()"
                  color="primary"
                  outlined
                  class="mb-5"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  @click="confirmarEditarTramite()"
                  color="primary"
                  outlined
                  class="mb-5"
                >
                  Crear
                </v-btn>
              </v-row>
            </v-form>
          </v-card>
        </v-col>
        <!-- EDICION LINK EN TRAMITE -->
        <v-col
          cols="12"
          v-if="mostrarCajaEdicionLink"
          class="pb-0 pl-0 pr-0 pt-0"
        >
          <EdicionLink
            v-if="tramiteEditable.solicitud != 'Presencial'"
            :link="editarUrl"
            :tramite="tramiteSeleccionado"
            @closeLink="cerrarEdicionLink()"
            @confirmacionSnackbar="
              mostrarSnackbar(
                true,
                false,
                false,
                'success',
                '30',
                '1000',
                'La acción se realizó con éxito'
              )
            "
          ></EdicionLink>
        </v-col>
      </v-col>
    </v-row>
    <v-snackbar
      v-model="snackbar"
      centered
      :color="colorSnackbar"
      :timeout="timeSnackbar"
      outlined
      text
      :height="heightSnackbar"
    >
      <v-row class="snackbar">
        <v-col>
          {{ mensajeSnackbar }}
        </v-col>
      </v-row>
      <v-row v-if="botonesErrorSnackbar" class="snackbar">
        <v-col>
          <v-btn class="botonSnackbar" text @click="snackbar = false">
            ACEPTAR
          </v-btn>
        </v-col>
      </v-row>
      <v-row v-if="botonesEleccionSnackbar" class="snackbar">
        <v-col>
          <v-btn
            class="botonSnackbar"
            text
            color="primary"
            @click="confirmarEliminarItem()"
          >
            SI
          </v-btn>

          <v-btn
            class="botonSnackbar"
            text
            color="primary"
            @click="cancelarEliminarItem()"
          >
            NO
          </v-btn>
        </v-col>
      </v-row>
    </v-snackbar>
  </div>
</template>
<script>
import Vue from "vue";
import { mapActions, mapGetters, mapState } from "vuex";
import { EventBus } from "../../../eventBus.js";
import EdicionLink from "./EdicionLink.vue";

export default Vue.extend({
  name: "EdicionTramite",
  components: {
    EdicionLink,
  },
  data: () => ({
    columnasListadoItemsTramite: "12",
    listadoItems: [],
    botonesErrorSnackbar: false,
    botonesEleccionSnackbar: false,
    colorSnackbar: "",
    snackbar: false,
    heightSnackbar: null,
    timeSnackbar: -1,
    habilitarEleccionDescripcion: false,
    itemAEliminar: null,
    showPasos: false,
    showReq: false,
    mensajeSnackbar: "",
    idItemSeleccionado: null,
    placeholderDescripcion: null,
    idTramiteSeleccionado: null,
    nuevaDescripcion: null,
    orden: null,
    ordenPlaceholder: "",
    solicitudSeleccionada: null,
    showAlertEdicion: false,
    textoTitleEdition: "",
    requiredRule: [(v) => !!v || "Campo requerido"],
    columnasDescripcionItem: 9,
    linkSolicitud: "",
  }),
  created() {
    this.columnasListadoItemsTramite = 12;
  },
  beforeUpdate() {
    this.filtrarItemsPorTramite();
    if (this.tramiteEditable.tipo == "Paso a paso") {
      this.showPasos = true;
      this.showReq = false;
      this.columnasDescripcionItem = 9;
    } else {
      this.showPasos = false;
      this.showReq = true;
      this.columnasDescripcionItem = 12;
    }
    if (
      this.edicionItemTramite == false &&
      this.creacionItemTramite == false &&
      this.mostrarCajaEdicionLink == false
    ) {
      this.columnasListadoItemsTramite = 12;
    }
  },
  mounted() {
    this.filtrarItemsPorTramite();
  },
  computed: {
    ...mapState("Listado-tramites", [
      "tramites",
      "itemsTramiteEditable",
      "tramiteEditable",
      "tramiteAEditar",
      "edicionItemTramite",
      "listadoRequerimientos",
      "listadoRequerimientosGenericos",
      "listadoPasos",
      "listadoPasosGenericos",
      "mostrarCajaEdicionLink",
      "creacionItemTramite",
    ]),

    ...mapGetters("Listado-tramites", ["obtenerTramitePorId"]),

    editarUrl() {
      console.log(this.tramiteSeleccionado);
      if (this.tramiteEditable.solicitud == "CVD") {
        if (this.tramiteSeleccionado.url_cvd) {
          return this.tramiteSeleccionado.url_cvd;
        }
        return "";
      } else {
        if (this.tramiteSeleccionado.url_web) {
          return this.tramiteSeleccionado.url_web;
        }
        return "";
      }
    },

    tramiteSeleccionado() {
      let tram = this.obtenerTramitePorId(this.tramiteEditable.id);
      return tram;
    },
    listadoPasosPorTramite() {
      return this.listadoPasos;
    },
    listadoRequerimientosPorTramite() {
      return this.listadoRequerimientos;
    },
    pasosGenericos() {
      return this.listadoPasosGenericos;
    },
    requerimientosGenericos() {
      return this.listadoRequerimientosGenericos;
    },
  },
  watch: {
    listadoPasosPorTramite() {
      this.filtrarItemsPorTramite();
    },
    listadoRequerimientosPorTramite() {
      this.filtrarItemsPorTramite();
    },
    pasosGenericos() {
      this.filtrarOpcionesGenericas();
    },
    requerimientosGenericos() {
      this.filtrarOpcionesGenericas();
    },
  },
  methods: {
    ...mapActions("Listado-tramites", [
      "mostrarEdicionItemTramite",
      "modificarPasoEnTramite",
      "modificarRequerimientoEnTramite",
      "eliminarPasoEnTramite",
      "eliminarRequerimientoEnTramite",
      "ocultarEdicionItemTramite",
      "agregarNuevoTramite",
      "extraerRequerimientos",
      "extraerPasos",
      "filtrarItemsTramiteEditable",
      "validarFormularioEdicion",
      "mostrarEdicionLink",
      "ocultarEdicionLink",
      "mostrarCreacionItemTramite",
      "ocultarCreacionItemTramite",
    ]),

    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    validarNumeros(param, param2) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        let campo = eval("this." + param2 + "= null");
      }
    },
    anularEdicionItem() {
      this.columnasListadoItemsTramite = 12;
    },
    mostrarEditarItem(param, index) {
      this.columnasListadoItemsTramite = "6";
      this.showAlertEdicion = false;
      this.mostrarEdicionItemTramite();
      this.ocultarCreacionItemTramite();
      this.ocultarEdicionLink();
      this.habilitarEleccionDescripcion = true;
      this.filtrarOpcionesGenericas();

      if (this.$refs.formularioEdicion != undefined) {
        this.$refs.formularioEdicion.reset();
      }

      this.idItemSeleccionado = param.id;
      if (this.tramiteEditable.tipo == "Paso a paso") {
        this.ordenPlaceholder = param.orden.toString();
      }
      this.placeholderDescripcion = param.descripcion;

      this.textoTitleEdition =
        "Editar trámite: " +
        this.tramiteEditable.id_item +
        "- item: " +
        (index + 1);
    },
    mostrarCrearItem() {
      this.columnasListadoItemsTramite = "6";
      this.showAlertEdicion = false;
      this.mostrarCreacionItemTramite();
      this.ocultarEdicionItemTramite();
      this.ocultarEdicionLink();
      this.ordenPlaceholder = "";
      (this.habilitarEleccionDescripcion = false),
        (this.textoTitleEdition = "Crear item");
      this.placeholderDescripcion = null;
      if (this.$refs.formularioEdicion != undefined) {
        this.orden = null;
        this.$refs.formularioEdicion.reset();
      }
      this.filtrarOpcionesGenericas();
    },
    seleccionItem() {
      this.idItemSeleccionado = this.nuevaDescripcion;
    },
    filtrarOpcionesGenericas() {
      if (this.tramiteEditable.tipo == "Requerimientos") {
        this.listadoItems = this.listadoRequerimientosGenericos;
      } else {
        this.listadoItems = this.listadoPasosGenericos;
      }
      this.listadoItems = this.listadoItems.filter(
        (element) =>
          element.tipo.toUpperCase() ==
          this.tramiteEditable.solicitud.toUpperCase()
      );
    },
    cancelarEditarItemTramite() {
      this.ocultarEdicionItemTramite();
      this.columnasListadoItemsTramite = "12";
      this.showAlertEdicion = false;
    },
    cancelarCrearItemTramite() {
      this.ocultarCreacionItemTramite();
      this.columnasListadoItemsTramite = "12";
      this.showAlertEdicion = false;
    },
    confirmarEditarTramite() {
      this.$refs.formularioEdicion.validate();
      if (this.$refs.formularioEdicion.validate() == true) {
        this.showAlertEdicion = false;
        let item = {};
        if (this.tramiteEditable.tipo == "Paso a paso") {
          item.idpaso = this.idItemSeleccionado;
          item.idtramite = this.tramiteEditable.id;
          item.orden = parseInt(this.orden);

          this.validarOrdenExistente();
          if (this.validarOrdenExistente() == false) {
            try {
              this.modificarPasoEnTramite(item);
            } catch (error) {
              this.mostrarSnackbar(
                true,
                true,
                false,
                "red",
                "60",
                "5000",
                "Hubo un error en la conexión. Por favor intente nuevamente."
              );
              return;
            }
            this.orden = null;
            this.nuevaDescripcion = null;
            this.mostrarSnackbar(
              true,
              false,
              false,
              "success",
              "30",
              "1000",
              "La acción se realizó con éxito"
            );
            this.ocultarEdicionItemTramite();
            this.ocultarCreacionItemTramite();
            this.columnasDescripcionItem = "12";
          } else {
            this.mostrarSnackbar(
              true,
              false,
              false,
              "red",
              "60",
              "5000",
              "Ya existe un paso con ese número de orden asignado"
            );
          }
        } else {
          item.idrequerimiento = this.idItemSeleccionado;
          item.idtramite = this.tramiteEditable.id;

          this.validarRequerimientoExistente();
          if (this.validarRequerimientoExistente() == false) {
            try {
              this.modificarRequerimientoEnTramite(item);
            } catch (error) {
              this.mostrarSnackbar(
                true,
                true,
                false,
                "red",
                "60",
                "5000",
                "Hubo un error en la conexión. Por favor intente nuevamente."
              );
              return;
            }
            this.nuevaDescripcion = null;
            this.mostrarSnackbar(
              true,
              false,
              false,
              "success",
              "30",
              "1000",
              "La modificación se realizó con éxito."
            );
            this.ocultarEdicionItemTramite();
            this.ocultarCreacionItemTramite();
            this.columnasDescripcionItem = "12";
          } else {
            this.mostrarSnackbar(
              true,
              false,
              false,
              "red",
              "60",
              "5000",
              "Ya existe el item seleccionado en el trámite."
            );
          }
        }
      } else {
        this.showAlertEdicion = true;
      }
    },
    validarOrdenExistente() {
      let coincidencia = {};
      coincidencia = this.itemsTramiteEditable.find(
        (element) => element.orden == this.orden
      );
      if (coincidencia == undefined) {
        return false;
      } else {
        return true;
      }
    },
    validarRequerimientoExistente() {
      let coincidencia = {};
      coincidencia = this.itemsTramiteEditable.find(
        (element) => element.id == this.idItemSeleccionado
      );
      if (coincidencia == undefined) {
        return false;
      } else {
        return true;
      }
    },
    filtrarItemsPorTramite() {
      if (this.tramiteEditable.tipo == "Requerimientos") {
        let items = [];
        items = this.listadoRequerimientos.filter(
          (element) => element.descripcion != null
        );
        items = items.filter(
          (element) =>
            element.tipo.toUpperCase() ==
            this.tramiteEditable.solicitud.toUpperCase()
        );
        items = items.sort(function(a, b) {
          return a.orden - b.orden;
        });
        this.filtrarItemsTramiteEditable(items);
      }
      if (this.tramiteEditable.tipo == "Paso a paso") {
        let items = [];
        items = this.listadoPasos.filter(
          (element) => element.descripcion != null
        );
        items = items.filter(
          (element) =>
            element.tipo.toUpperCase() ==
            this.tramiteEditable.solicitud.toUpperCase()
        );
        items = items.sort(function(a, b) {
          return a.orden - b.orden;
        });
        this.filtrarItemsTramiteEditable(items);
      }
    },
    eliminarItemEnTramite(param) {
      this.itemAEliminar = param;
      this.mostrarSnackbar(
        true,
        false,
        true,
        "primary",
        "60",
        "5000",
        "¿Confirma que elimina el ítem del trámite?"
      );
    },
    confirmarEliminarItem() {
      this.snackbar = false;
      let eliminar = true;
      this.confirmarEliminarItemEnTramite();
      this.resetItemAEditar();
    },
    confirmarEliminarItemEnTramite() {
      let item = {};
      if (this.tramiteEditable.tipo == "Paso a paso") {
        item.idpaso = this.itemAEliminar.id;
        item.idtramite = this.tramiteEditable.id;
        item.orden = this.itemAEliminar.orden;
      } else {
        item.idrequerimiento = this.itemAEliminar.id;
        item.idtramite = this.tramiteEditable.id;
      }
      try {
        if (this.tramiteEditable.tipo == "Paso a paso") {
          this.eliminarPasoEnTramite(item);
        } else {
          this.eliminarRequerimientoEnTramite(item);
        }
      } catch (error) {
        this.mostrarSnackbar(
          true,
          true,
          false,
          "red",
          "60",
          "5000",
          "Hubo un error en la conexión. Por favor intente nuevamente."
        );
        return;
      }
      this.mostrarSnackbar(
        true,
        false,
        false,
        "success",
        "30",
        "1000",
        "La eliminación se realizó con éxito"
      );
    },
    cancelarEliminarItem() {
      this.snackbar = false;
      this.ocultarEdicionLink();
      this.ocultarEdicionItemTramite();
      this.columnasListadoItemsTramite = "12";
    },
    mostrarSnackbar(
      snackbar,
      botonesError,
      botonesEleccion,
      color,
      height,
      time,
      mensaje
    ) {
      this.snackbar = snackbar;
      this.botonesErrorSnackbar = botonesError;
      this.botonesEleccionSnackbar = botonesEleccion;
      this.colorSnackbar = color;
      this.heightSnackbar = height;
      this.timeSnackbar = time;
      this.mensajeSnackbar = mensaje;
    },
    editarLinkTramite() {
      this.columnasListadoItemsTramite = "6";
      this.ocultarCreacionItemTramite();
      this.ocultarEdicionItemTramite();
      this.mostrarEdicionLink();
    },
    cerrarEdicionLink() {
      this.columnasListadoItemsTramite = "12";
      this.ocultarEdicionItemTramite();
    },
    resetItemAEditar() {
      this.textoTitleEdition = "Editar trámite: " + this.idTramiteSeleccionado;
    },
  },
});
</script>
