import axios from "axios";
import { portalUrl, rgpWebUrl } from "../../services/paths.js";

function existeCookie() {
  if (document.cookie.indexOf("CiDi") != -1) {
    return true;
  } else {
    return false;
  }
}

function delay() {
  return new Promise((resolve) => setTimeout(resolve, 1000));
}

const initialState = () => ({
  usuarioCidi: sessionStorage.getItem("cidiResponse")
    ? JSON.parse(sessionStorage.getItem("cidiResponse"))
    : null,
  usuarioInterno: false,
});

const state = initialState();

const getters = {
  ingresadoEnCidi: (state) => {
    return state.usuarioCidi !== null;
  },
};

const mutations = {
  LOGIN_CIDI(state, datosCidi) {
    state.usuarioCidi = { ...datosCidi };
  },
  VALIDAR_USUARIO_INTERNO(state) {
    state.usuarioInterno = true;
  },
  LOGOUT_CIDI(state) {
    state.usuarioCidi = null;
    state.usuarioInterno = false;
  },
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach((key) => {
      state[key] = newState[key];
    });
  },
};

const actions = {
  login({ commit, dispatch }) {
    axios
      .get(`${portalUrl}/getCredentials`)
      .then((response) => {
        dispatch("validarUsuarioInterno", response.data.cidiResponseLogin.CUIL);
        sessionStorage.setItem("cidiResponse", JSON.stringify(response.data));
        commit("LOGIN_CIDI", response.data);
      })
      .catch((error) => {});
  },
  validarUsuarioInterno({ commit }, cuit) {
    axios
      .get(`${rgpWebUrl}/usuario-interno?cuit=${cuit}`)
      .then((response) => {
        if (response.data == "1") {
          commit("VALIDAR_USUARIO_INTERNO", response.data);
        }
      })
      .catch((error) => {});
  },
  async comprobarCookie({ dispatch }) {
    for (let index = 0; index < 30; index++) {
      if (existeCookie()) {
        dispatch("login");
        break;
      } else {
        await delay();
      }
    }
  },
  logout({ commit }) {
    axios
      .post(`${portalUrl}/externalLogout`, {}, {
        withCredentials: true
      })
      .then(response => {
        sessionStorage.removeItem("cidiResponse");
        document.cookie.split(';').forEach(function (c) {
          document.cookie = c
          .replace(/^ +/, '')
          .replace(/=.*/, '=;expires=' + new Date().toUTCString() + ';path=/')
      })
    }).catch(error => {
      sessionStorage.removeItem("cidiResponse");
      console.log(error)
    })

    commit("LOGOUT_CIDI");
  },
  reset({ commit }) {
    commit("RESET");
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
