<template>
  <div v-if="this.$route.name.includes('admin') == false">
    <v-badge :content="counter" :value="counter" color="red" overlap left>
      <router-link :to="{ name: this.nombreRuta }">
        <v-btn tile outlined color="primary" class="no-text-transforms shadow">
          mis tasas
        </v-btn>
      </router-link>
    </v-badge>
  </div>
</template>

<script>
import Vue from "vue";
import { mapGetters, mapState } from "vuex";

export default Vue.extend({
  name: "MyValuations",
  data: () => ({
    redireccion: "informes",
  }),
  computed: {
    ...mapGetters("Tramites-seleccionados", ["contadorGenerico"]),
    ...mapState("Logincidi", ["usuarioInterno"]),
    counter() {
      return this.contadorGenerico("warehouse");
    },
    nombreRuta() {
      let posiblesRutas = ["r", "i"];
      if (posiblesRutas.includes(this.$route.name.charAt(0)))
        return `${this.$route.name.charAt(0)}tasas`;
      else return "rtasas";
    },
  },
});
</script>

<style scoped>
.shadow {
  background-color: white;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 1px 5px 0 rgba(0, 0, 0, 0.12) !important;
}
</style>
