<script>
import Vue from "vue";
import Alert from "../utils/Alert";
import { mapGetters, mapActions, mapState } from "vuex";
import { EventBus } from "../../eventBus";

export default Vue.extend({
  name: "UserForm",
  props: {
    showSnackbar: Boolean,
  },
  components: {
    Alert,
  },
  data: () => ({
    modalVisible: false,
    valid: true,
    lazy: false,
    marker: false,
    guardadoLocal: false,
    mensaje: "",
    numeroLiquidacion: [],
    requiredRuleCuit: [
      (v) => !!v || "Campo requerido.",
      (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
      (v) => (v || "").length == 11 || "Debe estar compuesto por 11 dígitos",
    ],
    requiredRuleDni: [
      (v) => !!v || "Campo requerido.",
      (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
      (v) =>
        (v || "").length == 6 ||
        (v || "").length == 7 ||
        (v || "").length == 8 ||
        "Debe contener entre 6 y 8 dígitos",
    ],
    requiredRule: [(v) => !!v || "Campo requerido."],
    reqMaxLengthRule: [
      (v) => !!v || "Campo requerido.",
      // (v) => /\D/.test(v) || "Solo de la A-Z",
      (v) => (v || "").length < 50 || "Máximo de 50 caracteres",
    ],
    snackbar: false,
    loading: true,
    fullName: null,
    // typeUser: [],
    typeUserSelected: {
      id: 8,
      nombre: "Agrimensor",
      exento: false,
      pago_diferido: false,
      numero_liquidacion: false,
    },
    datosDelUsuario: [],
    profEnrollment: null,
    prof: null,
    cuit: null,
    dni: null,
    numero_liquidacion: false,
    numero_liquidacion_value: null,
    exento: false,
    pago_diferido: false,
    exento_value: false,
    pago_diferido_value: false,
    //idley: null,
  }),
  created() {
    // esto deberia ser un action.
    if (this.contadorTypeUser == 0) {
      this.extraerTiposDeUsuario().then(() => {
        this.loading = false;
      });
    } else {
      this.loading = false;
    }

    // if (this.contadorLeyes == 0) {
    //   this.extraerLeyes().then(() => {
    //     this.loading = false;
    //   });
    // } else {
    //   this.loading = false;
    // }
  },
  beforeUpdate() {
    if (this.numero_liquidacion_value == null) {
      this.borrarNumeroLiquidacion();
    }
  },
  mounted() {
    if (this.contadorUserForm !== 0) {
      this.fullName = this.userForm.fullName;
      this.typeUserSelected = this.userForm.typeUserSelected;
      this.profEnrollment = this.userForm.profEnrollment;
      this.prof = this.userForm.prof;
      this.cuit = this.userForm.cuit;
      this.dni = this.userForm.dni;
      this.numero_liquidacion_value = this.userForm.numero_liquidacion_value;
      this.exento_value = this.userForm.exento_value;
      this.pago_diferido_value = this.userForm.pago_diferido_value;
      // this.pago_diferido = false;
      // this.exento = false;
      // this.idley = this.userForm.idley;
      this.guardadoLocal = true;
    } else {
      this.fullName = null;
      this.typeUserSelected = {
        id: 8,
        nombre: "Agrimensor",
        exento: false,
        pago_diferido: false,
        numero_liquidacion: false,
      };
      this.profEnrollment = null;
      this.prof = null;
      this.cuit = null;
      this.dni = null;
      this.numero_liquidacion_value = null;
      this.exento_value = false;
      this.pago_diferido_value = false;
      //this.idley = null;
    }
    if (this.showSnackbar == true) {
      this.snackbar = true;
    }
  },
  computed: {
    ...mapState("Logincidi", ["usuarioCidi"]),
    ...mapGetters("Logincidi", ["ingresadoEnCidi"]),
    ...mapState("Tramites-seleccionados", [
      "userForm",
      "tipo_usuario",
      "leyes",
      "showSnackbarUserForm",
      "errorConexion",
      "publicidad_seleccionada",
      "tasas_seleccionadas",
      "datosUsuarioYTasas",
      "warehouse",
      "validarFormularioUsuario",
      "generarPago",
    ]),
    ...mapState("Tramites-completados", [
      "completados",
      "esExento",
      "esDiferido",
    ]),
    ...mapGetters("Tramites-seleccionados", [
      "contadorGenerico",
      "objetoVacio",
      "extraerLiquidacion",
    ]),
    contadorTypeUser() {
      return this.contadorGenerico("tipo_usuario");
    },
    // contadorLeyes() {
    //   return this.contadorGenerico("leyes");
    // },
    contadorUserForm() {
      return this.objetoVacio("userForm");
    },
    checkUserForm() {
      return this.validarFormularioUsuario;
    },
    checkGenerarPago() {
      return this.generarPago;
    },
  },

  methods: {
    ...mapActions("Tramites-seleccionados", [
      "extraerTiposDeUsuario",
      "extraerLeyes",
      "agregarFormularioDeUsuario",
      "borrarNumeroLiquidacion",
      "validarUserform",
      "anularUserform",
      "ocultarSnackbarUserForm",
      "guardarDatosIngresadosPorUsuario",
      "eliminarDatosIngresadosPorUsuario",
      "borrarFormularioDeUsuario",
      "almacenarTasaSeleccionada",
      "cancelUserForm",
      "cancelGenerarPago",
      "validateFormularioTramite",
      "validateFormularioPulicidad",
    ]),
    // "guardarNumeroLiquidacion",
    // "seleccionarLiquidacion",
    // "buscarLiquidaciones",
    ...mapActions("Tramites-completados", [
      "mostrarModalMedioDePago",
      "ocultarModalMedioDePago",
      "siExento",
      "noExento",
      "siDiferido",
      "noDiferido",
    ]),
    toggleMarker() {
      this.marker = !this.marker;
    },
    scroll(id) {
      document.getElementById(id).scrollIntoView({
        behavior: "smooth",
      });
    },
    verificarUserForm() {
      if (this.$refs.form != undefined) {
        this.validateUserForm();
        if (this.validateUserForm()) {
          this.validarUserform();
          this.guardarDatosDelUsuario();
          if (this.completados.length == 0 && this.warehouse.length == 0) {
            (this.mensaje = "Debe tener un trámite seleccionado"),
              (this.snackbar = true);
          } else {
            if (this.warehouse[0].tipo == "Tasa") {
              this.validateFormularioTramite();
            } else {
              this.validateFormularioPulicidad();
            }
          }
        }
      }
    },
    validateUserForm() {
      if (this.$refs.form.validate() != true) {
        this.scroll("userForm");
        (this.mensaje = "Debe completar el formulario DATOS DEL USUARIO"),
          (this.snackbar = true);
        this.anularUserform();
        this.borrarDatosDelUsuario();
        return false;
      } else {
        return true;
      }
    },

    borrarDatosDelUsuario() {
      this.eliminarDatosIngresadosPorUsuario();
    },
    guardarDatosDelUsuario() {
      //let constley = null;
      // if (this.exento_value === true) {
      //   constley = this.idley.idley;
      // }
      this.datosDelUsuario = {
        profesion: this.prof,
        solicitante: this.fullName,
        nro_doc: this.dni,
        cuit: this.cuit,
        mp: this.profEnrollment,
        tipo_usuario: this.typeUserSelected.id,
        pago_diferido: this.pago_diferido_value,
        exento: this.exento_value,
        // pago_diferido: false,
        // exento: false,
        nro_liquidacion: this.numero_liquidacion_value,
        // idley: constley,
      };
    },
    guardarDatosIngresados() {
      if (this.completados.length != 0) {
        if (this.warehouse[0].tipo == "Tasa") {
          this.guardarDatosAImprimir();
        }
        this.datosDelUsuario.items = this.tasas_seleccionadas;
      }
      if (Object.keys(this.publicidad_seleccionada).length != 0) {
        this.datosDelUsuario.items = this.publicidad_seleccionada;
      }
      let datosAGuardar = {};
      datosAGuardar = this.datosDelUsuario;
      this.guardarDatosIngresadosPorUsuario(datosAGuardar);
      if (!this.esExento && !this.esDiferido) {
        this.mostrarModalMedioDePago();
      } else {
        this.ocultarModalMedioDePago();
      }
    },
    guardarDatosAImprimir() {
      const listadoTramitesSeleccionados = [];
      this.completados.forEach((element) => {
        let tramiteGuardado = {};
        tramiteGuardado.id = element.id;
        tramiteGuardado.id_item = element.id_item;
        tramiteGuardado.formula = element.formulario.formula;
        tramiteGuardado.monto_fijo = element.formulario.monto_fijo;
        tramiteGuardado.monto_minimo = element.formulario.monto_minimo;
        tramiteGuardado.monto_maximo = element.formulario.monto_maximo;
        tramiteGuardado.serie = element.serie;
        tramiteGuardado.anio = element.anio;
        let nuevaFila = [];
        if (element.formulario.campos.length != 0) {
          element.formulario.campos.forEach((elemento) => {
            let nuevoIngreso = [];
            elemento.campos.forEach((elementoInterno) => {
              let valor = elementoInterno.valor;
              if (elementoInterno.valor != undefined) {
                if (typeof valor == "string") {
                  valor = valor.replace(",", ".");
                }
                valor = parseFloat(valor);
                valor.toFixed(2);
              }
              let campo = {};
              (campo.nombre_variable = elementoInterno.nombre_variable),
                (campo.valor = valor);
              nuevoIngreso.push(campo);
            });
            nuevaFila.push(nuevoIngreso);
          });
        } else {
          nuevaFila = [[]];
        }
        tramiteGuardado.campos = nuevaFila;

        listadoTramitesSeleccionados.push(tramiteGuardado);
      });
      this.almacenarTasaSeleccionada(listadoTramitesSeleccionados);
    },
    saveLS() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (this.guardadoLocal === true) {
        this.guardadoLocal = false;

        localStorage.removeItem("datos_usuario");
        this.borrarFormularioDeUsuario();

        return;
      }

      const userFrm = {
        fullName: this.fullName,
        typeUserSelected: this.typeUserSelected,
        profEnrollment: this.profEnrollment,
        prof: this.prof,
        cuit: this.cuit,
        dni: this.dni,
        numero_liquidacion_value: this.numero_liquidacion_value,
        exento_value: this.exento_value,
        pago_diferido_value: this.pago_diferido_value,
        // pago_diferido: false,
        // exento: false,
        // idley: this.idley,
      };
      this.guardadoLocal = true;
      this.agregarFormularioDeUsuario(userFrm);
    },
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    validarNumeros(param, param2) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        let campo = eval("this." + param2 + "= null");
      }
    },
    resetFormUser() {
      this.$refs.form.reset;
    },
    //TODO: Ver de refactorizar esta parte, de forma que solo se utilice el reset.
    initialFormUserValue() {
      this.$refs.form.resetValidation();
      this.fullName = null;
      this.typeUserSelected = {
        id: 8,
        nombre: "Agrimensor",
        exento: false,
        pago_diferido: false,
        numero_liquidacion: false,
      };
      this.profEnrollment = null;
      this.prof = null;
      this.cuit = null;
      this.dni = null;
      this.numero_liquidacion = false;
      this.exento = false;
      this.pago_diferido = false;
      this.numero_liquidacion_value = null;
      this.exento_value = false;
      this.pago_diferido_value = false;
      //this.idley = null;
      this.guardadoLocal = false;
      this.borrarFormularioDeUsuario();
      // this.$refs.form.reset
    },
    // cambioExento(valor) {
    //   if (valor) {
    //     this.idley = null;
    //   }
    // },
    changeExentoPagoDiferido(checkboxNumber) {
      if (checkboxNumber === 1 && this.exento_value) {
        this.exento_value = false;
      } else if (checkboxNumber === 2 && this.pago_diferido_value) {
        this.pago_diferido_value = false;
      }
    },
    changeSelected(objSelected) {
      this.numero_liquidacion_value = null;
      this.exento_value = false;
      this.pago_diferido_value = false;
      this.typeUserSelected = objSelected;
      if (this.typeUserSelected.id == 20) {
        // this.buscarLiquidaciones();
        this.exento_value = true;
      }
    },
    modalToGetUserCidi() {
      this.modalVisible = true;
    },
    getUserCidi() {
      this.fullName = `${this.usuarioCidi.cidiResponseLogin.Nombre} ${this.usuarioCidi.cidiResponseLogin.Apellido}`;
      this.cuit = this.usuarioCidi.cuit;
      this.dni = this.usuarioCidi.cidiResponseLogin.NroDocumento;
      this.modalVisible = false;
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },

    specialCharacters(e) {
      if (/(?![a-zA-ZÀ-ÿ\u00f1\u00d1]|\s)./g.test(e.key)) {
        e.preventDefault();
      } else {
        return true;
      }
    },

    checkOnlyWorld(param) {
      const regexpTexto = /(?![a-zA-ZÀ-ÿ\u00f1\u00d1]|\s)./g;
      if (param.target.value.match(regexpTexto)) {
        param.target.value = null;
      }
    },

    // checkOnlyNum(param, campo) {
    //   const regexpNumeros = /\D/;
    //   if (param.target.value.match(regexpNumeros)) {
    //     param.target.value = null;
    //     if(campo == "cuit"){
    //       this.cuit = null
    //     }else{
    //       this.dni = null
    //     }
    //   }
    // },
  },
  watch: {
    checkUserForm() {
      if (this.validarFormularioUsuario == true) {
        this.verificarUserForm();
        this.cancelUserForm();
      }
    },
    checkGenerarPago() {
      if (this.generarPago == true) {
        this.guardarDatosIngresados();
        this.cancelGenerarPago();
      }
    },
    exento_value() {
      if (this.exento_value) {
        this.siExento();
        EventBus.$emit("esExento", this.esExento);
      } else {
        this.noExento();
        EventBus.$emit("esExento", this.esExento);
      }
    },
    pago_diferido_value() {
      if (this.pago_diferido_value) {
        this.siDiferido();
        EventBus.$emit("esDiferido", this.esDiferido);
      } else {
        this.noDiferido();
        EventBus.$emit("esDiferido", this.esDiferido);
      }
    },
    // $route()
    //   this.typeusers();
    // }
    fullName(val) {
      if (this.fullName !== null) {
        this.fullName = val.trim().replace(/\s{2,}/g, " ");
      }
    },
  },
});
</script>
