<template>
  <div>
    <v-row>
      <v-col cols="12" xs="12" sm="12" md="3" class="pb-0">
        <v-card outlined elevation="2" class="d-flex align-center justify-center">
          <v-radio-group :row="is_screen_small" v-model="radios">
            <v-radio v-for="item of sectionArray" :key="item.value" :value="item.value">
              <template v-slot:label>
                <div>Por <strong class="primary--text">{{ item.text }}</strong></div>
              </template>
            </v-radio>
          </v-radio-group>
        </v-card>
      </v-col>
      <v-col cols="12" xs="12" sm="12" md="9" class="pb-0 mb-5">
        <v-card outlined elevation="2" height="100%">
          <v-form ref="formu">
            <v-row>
              <v-col cols="12">
                <v-row class="d-flex justify-space-between align-center mx-4">
                  <!-- ALERT ERROR  -->
                  <v-col cols="12" class="pa-0">
                    <v-alert text outlined color="red" v-model="alert" class="py-2 mb-2 mt-1 text-center body-2"
                      width="100%" close-icon="close" dismissible v-if="alert">
                      <template v-slot:close="{ }">
                        <v-icon size="18" color="error" @click="resetearError">close</v-icon>
                      </template>
                      {{ mensajeAlert }}
                    </v-alert>
                  </v-col>
                  <v-col cols="12" sm="6" class="pb-0 pt-4 pr-sm-5 px-0 completo" v-if="radios == 'nroliquidacion'">
                    <v-text-field v-model="liquidacion" @keypress="onlyNumber($event)"
                      @keyup="validarNumero(liquidacion, 'liquidacion')" :rules="requiredLiquidacion"
                      label="Número de liquidación" maxlength="16" outlined dense
                      class="centered-input shrink unico"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" class="pb-0 pt-4 pr-sm-5 px-0 completo" v-if="radios == 'nroformulario'">
                    <v-text-field v-model="formulario" @keypress="onlyNumber($event)"
                      @keyup="validarNumero(formulario, 'formulario')" :rules="requiredFormulario"
                      label="Número de formulario" maxlength="12" outlined dense
                      class="centered-input shrink unico"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" class="pb-0 pt-4 pr-sm-5 px-0 completo" v-if="radios == 'nrotramite'">
                    <v-text-field v-model="tramite" @keypress="onlyNumber($event)"
                      @keyup="validarNumero(tramite, 'tramite')" :rules="requiredTramite" label="Número de trámite"
                      minlength="1" maxlength="12" outlined dense class="centered-input shrink unico" />
                  </v-col>
                  <v-col cols="12" sm="6" class="pb-0 pt-4 pl-sm-5 px-0 completo">
                    <v-text-field v-model="cuit" @keypress="onlyNumber($event)" @keyup="validarNumero(cuit, 'cuit')"
                      :rules="requiredRuleCuit" label="Número de cuit/cuil" maxlength="11" outlined dense
                      class="centered-input shrink unico"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="8" class="d-flex justify-center justify-sm-start pl-0">
                    <vue-recaptcha ref="myRecaptcha" sitekey="6Lc0SSMeAAAAAIIktay2F6fvlxR7FYYX_e2mKfil"
                      @verify="verificarMethod" @expired="expiredMethod"></vue-recaptcha>
                    <small v-if="errorCaptcha">Debe validar este campo</small>
                  </v-col>
                  <v-col cols="12" sm="4" class="d-flex justify-sm-end justify-center py-2 px-0">
                    <v-btn outlined color="primary" class="no-text-transforms boton" @click="imprimir()"
                      :disabled="buscando == true" :loading="buscando == true">
                      Imprimir
                      <v-icon color="primary" size="14" />
                    </v-btn>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
    <!-- Modal liquidaciones -->
    <v-row justify="center">
      <v-dialog v-model="mostrarDialog" persistent max-width="400px">
        <v-card>
          <v-card-title class="headline justify-center"><strong>Liquidaciones</strong></v-card-title>
          <v-divider />
          <v-card-text :style="{ height: calculateAlturaModal() }">
            <v-radio-group column v-model="liquidacion">
              <template v-if="consultaPagoOnlinePorFormulario">
                <v-radio v-for="(nro, idx) in this.consultaPagoOnlinePorFormulario" :key="idx" :label="`Numero: ${nro}`"
                  :value="nro" />
              </template>
              <template v-if="consultaPagoOnlinePorTramite">
                <v-radio v-for="(nro, idx) in this.consultaPagoOnlinePorTramite" :key="idx" :label="`Numero: ${nro}`"
                  :value="nro" />
              </template>
            </v-radio-group>
          </v-card-text>
          <v-divider />
          <div class="d-flex" v-if="pdfError">
            <v-alert text outlined color="red" class="py-2 mb-2 mt-1 text-center body-2" width="100%" close-icon="close"
              dismissible>
              <template v-slot:close="{ }">
                <v-icon size="18" color="error" @click="pdfError = false">close</v-icon>
              </template>
              No se pudo generar el comprobante solicitado.
            </v-alert>
          </div>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="disabledDialog" class="mr-auto">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text class="no-text-transforms boton ml-auto" @click="imprimirDesdeDialog()"
              :disabled="loadingDialog == true || !liquidacion" :loading="loadingDialog == true">
              Seleccionar
              <v-icon color="primary" size="14" />
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import Vue from "vue";
import { mapActions, mapState } from "vuex";
import { VueRecaptcha } from 'vue-recaptcha';

export default Vue.extend({
  name: "BusquedaReimpresiones",
  components: {
    VueRecaptcha
  },
  data() {
    return {
      pdfError: false,
      radios: "nroliquidacion",
      errorBusqueda: false,
      liquidacion: "",
      cuit: "",
      formulario: "",
      tramite: '',
      buscando: false,
      loadingDialog: false,
      alert: false,
      mensajeAlert: "",
      recaptcha: false,
      errorCaptcha: false,
      mostrarDialog: false,
      requiredLiquidacion: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
        (v) => (v || "").length == 16 || "Debe estar compuesto por 16 dígitos",
      ],
      requiredRuleCuit: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
        (v) => (v || "").length == 11 || "Debe estar compuesto por 11 dígitos",
      ],
      requiredFormulario: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
        (v) => (v || "").length == 12 || "Debe estar compuesto por 12 dígitos",
      ],
      requiredTramite: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
        (v) => (v || "").length >= 1 || "Debe estar compuesto por 1 dígitos",
      ],
      sectionArray: [
        {
          value: 'nroliquidacion',
          text: 'Nro Liquidación'
        },
        {
          value: 'nroformulario',
          text: 'Nro Formulario'
        },
        {
          value: 'nrotramite',
          text: 'Nro Trámite'
        }
      ]
    };
  },
  computed: {
    ...mapState("Tramites-completados", [
      "reimprimiendo", "errorConexion", "consultaPagoOnline", "consultaPagoOnlinePorFormulario", "consultaPagoOnlinePorTramite"
    ]),
    reimpresion() {
      return this.reimprimiendo;
    },
    errorDeConexion() {
      return this.errorConexion
    },
    resultadoBusqueda() {
      return this.consultaPagoOnline
    },
    resultadoBusquedaPorFormulario() {
      return this.consultaPagoOnlinePorFormulario
    },
    resultadoBusquedaPorTramite() {
      return this.consultaPagoOnlinePorTramite
    },
    is_screen_small() {
      return this.$vuetify.breakpoint.smOnly;
    },
    camposCompletos() {
      return this.liquidacion !== "" && this.cuit !== "" && this.liquidacion !== undefined && this.cuit !== undefined
        || this.formulario !== "" && this.cuit !== "" && this.formulario !== undefined && this.cuit !== undefined
        || this.tramite !== "" && this.cuit !== "" && typeof this.tramite !== "undefined" && typeof this.cuit !== "undefined";
    }
  },
  methods: {
    ...mapActions("Tramites-completados", [
      "reimprimirPDFOnline", "setReimprimiendo", "ocultarErrorConexion", "consultarPagoOnline", "consultarPagoOnlinePorFormulario", "consultarPagoOnlinePorTramite"
      , "setConsultarPagoOnlinePorFormulario", "setConsultarPagoOnlinePorTramite"
    ]),
    onlyNumber: function (evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    verificarMethod() {
      this.recaptcha = true
      this.errorCaptcha = false
    },
    expiredMethod() {
      this.recaptcha = false
    },
    validarNumero(param, param2) {
      const regexpNumeros = /\D/;
      if (param != null && param.match(regexpNumeros)) {
        let campo = eval("this." + param2 + "= null");
      }
    },
    resetearError() {
      this.alert = false
      this.mensajeAlert = ""
    },
    disabledDialog() {
      this.mostrarDialog = false;
      this.$refs.formu.reset();
      this.$refs.myRecaptcha.reset();
      this.recaptcha = false;
      this.setConsultarPagoOnlinePorFormulario([]);
      this.setConsultarPagoOnlinePorTramite([])
    },
    imprimir() {
      this.$refs.formu.validate();

      if (!this.recaptcha) {
        return this.errorCaptcha = true
      }

      if (this.$refs.formu.validate()) {
        this.buscando = true
        let datos = null

        if (this.liquidacion) {
          datos = {
            nroLiquidacion: this.liquidacion,
            nroCuit: this.cuit
          }
          this.formulario = "";
          this.consultarPagoOnline(datos);
          this.liquidacion = "";
        }

        if (this.formulario) {
          datos = {
            nroFormulario: this.formulario,
            nroCuit: this.cuit
          }
          this.consultarPagoOnlinePorFormulario(datos).then(res => {
            if (res === '') {
              this.alert = true
              this.mensajeAlert = "No se encontró ningún formulario abonado con los datos ingresados."
              this.$refs.myRecaptcha.reset();
              this.recaptcha = false;
              setTimeout(() => {
                this.resetearError();
                return
              }, 4000);
            }
            if (typeof res === 'number') {
              let nroString = res.toString();
              this.reimprimirPDFOnline(nroString).then(() => {
                this.$refs.formu.resetValidation();
                this.$refs.formu.reset();
                this.$refs.myRecaptcha.reset();
                this.recaptcha = false;
              }).catch(() => {
                this.$refs.myRecaptcha.reset();
                this.recaptcha = false;
                this.alert = true
                this.mensajeAlert = "No se encontró ningún formulario abonado con los datos ingresados."
                this.$refs.formu.reset();
                this.$refs.formu.resetValidation();
              }).finally(() => {
                this.buscando = false;
              });
            }
          });
        }

        if (this.tramite) {
          datos = {
            nroTramite: this.tramite,
            nroCuit: this.cuit
          }
          this.consultarPagoOnlinePorTramite(datos)
            .then((res) => {
              if (res.length === 0) {
                this.mostrarAlerta("No se encontró ningún formulario abonado con los datos ingresados.");
                this.resetearRecaptcha();
                setTimeout(() => {
                  this.resetearError();
                }, 4000);
                return;
              }

              if (typeof res === 'number') {
                const nroString = res.toString();
                this.reimprimirPDFOnline(nroString)
                  .then(this.resetFormulario)
                  .catch(() => {
                    this.mostrarAlerta("No se encontró ningún formulario abonado con los datos ingresados.");
                    this.resetFormulario();
                  }).finally(() => {
                    this.buscando = false;
                  });
              }
            })
            .catch((error) => {
              console.error("Error en la consulta:", error);
              this.mostrarAlerta("Ocurrió un error al procesar la solicitud.");
              this.resetearRecaptcha();
              this.buscando = false;
            });
        }
      }
    },
    imprimirDesdeDialog() {
      this.loadingDialog = true;
      this.reimprimirPDFOnline(this.liquidacion).then(() => {
        this.loadingDialog = false;
        this.mostrarDialog = false;
        this.$refs.myRecaptcha.reset();
        this.recaptcha = false;
        this.$refs.formu.reset();
        this.$refs.formu.resetValidation();
        this.setConsultarPagoOnlinePorFormulario([])
        this.setConsultarPagoOnlinePorTramite([])
      }).catch(() => {
        this.loadingDialog = false;
        this.$refs.myRecaptcha.reset();
        this.recaptcha = false;
        this.pdfError = true
      })
    },
    mostrarAlerta(mensaje) {
      this.alert = true;
      this.mensajeAlert = mensaje;
    },

    resetearRecaptcha() {
      this.$refs.myRecaptcha.reset();
      this.recaptcha = false;
    },

    resetFormulario() {
      this.$refs.formu.resetValidation();
      this.$refs.formu.reset();
      this.resetearRecaptcha();
    },
    calculateAlturaModal() {
      let height = "";
      if (this.consultaPagoOnlinePorFormulario.length) {
        height = this.consultaPagoOnlinePorFormulario.length * 40 + "px"
      } else {
        height = this.consultaPagoOnlinePorTramite.length * 40 + "px"
      }

      return height;
    },
  },
  watch: {
    reimpresion() {
      this.buscando = this.reimprimiendo;
    },
    errorDeConexion() {
      if (this.errorConexion) {
        this.alert = true
        this.mensajeAlert = "Hubo un error de conexión. Por favor intente nuevamente."
        setTimeout(() => {
          this.alert = false
          this.mensajeAlert = ""
          this.$refs.formu.reset();
        }, 5000);
      }
    },
    resultadoBusqueda() {
      if (this.consultaPagoOnline == 0) {
        this.alert = true
        this.mensajeAlert = "No se encontró ningún formulario abonado con los datos ingresados."
        this.$refs.formu.reset();
        setTimeout(() => {
          this.alert = false
          this.mensajeAlert = ""
        }, 4000);
      } else {
        this.alert = false
        this.mensajeAlert = ""
      }
    },
    resultadoBusquedaPorFormulario() {
      if (this.consultaPagoOnlinePorFormulario.length > 1) {
        this.alert = false
        this.mensajeAlert = ""
        this.mostrarDialog = true;
        this.buscando = false;
      }
    },
    resultadoBusquedaPorTramite() {
      if (this.consultaPagoOnlinePorTramite.length > 1) {
        this.alert = false
        this.mensajeAlert = ""
        this.mostrarDialog = true;
        this.buscando = false;
      }
    }
  }
});
</script>
<style scoped>
small {
  color: #FF5252;
  font-size: 12px !important;
  padding-left: 12px;
}

.v-input--radio-group--column .v-input--radio-group__input>>> {
  background-color: tomato;
}

.v-application .headline {
  font-size: 1.3rem !important;
  color: #009eed;
}
</style>