<template>
  <v-row>
    <v-col cols="12" class="pb-2 pt-3">
      <v-card elevation="2" class="text-xs-center">
        <h3 class="tituloTramite" v-if="tramite_seleccionado.id != undefined">
          {{ tramite_seleccionado.titulo }}
        </h3>
        <h3 class="tituloTramite" v-else>
          <v-row>
            <v-col cols="11" class="py-0 px-3" v-if="showTitle">
              {{ title }}
            </v-col>
            <v-col
              cols="1"
              class="editarTextoBoton"
              v-if="usuarioInterno && perfilAdmin"
            >
              <v-btn
                class="px-0 primary"
                @click="editarTexto()"
                small
                v-if="editTextButton"
              >
                <v-icon small>
                  edit
                </v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </h3>

        <v-card-text
          :class="[
            title == '' ? 'pb-4' : 'pb-4',
            alignText == '' ? 'text-left' : 'text-center',
          ]"
        >
          <v-row>
            <v-col cols="12" md="12">
              <v-card-text v-if="showDescriptionText" class="pt-0 pb-0">
                <div id="textoDescripcion">{{ content }}</div>
              </v-card-text>
              <div v-if="edicionTexto" class="editionTextarea">
                <tinymce id="tinymce" v-model="texto" toolbar2="false">
                </tinymce>
              </div>
              <div class="contenidoInformes"><span v-html="content"></span></div>
            </v-col>
            <v-col cols="12" v-if="edicionTexto">
              <v-row class="rowBotonesEdicion">
                <button
                  class="editionButton boton"
                  @click="cancelarEditarTexto()"
                >
                  Cancelar
                </button>
                <button
                  class="editionButton boton"
                  @click="confirmarEditarTexto(texto)"
                >
                  Confirmar
                </button>
              </v-row>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import Vue from "vue";
import { mapState, mapActions } from "vuex";

import tinymce from "vue-tinymce-editor";
Vue.component("tinymce", tinymce);

export default Vue.extend({
  /* TODO: ///  El card-title debe tomar como parametro un texto. De no obtenerse tendrá como valor predeterminado: "".
           ///  El cuerpo de la descripción debe pasarse como parametro.
           ///  La disposicion del card-text (center, left) debe tomarse como parametro. De no obtenerse tendrá como valor predeterminado: "" en otras palabras left.
   */
  name: "DescriptionSection",
  computed: {
    ...mapState("Tramites-seleccionados", [
      "tramite_seleccionado",
      "textoDescripcionFormularios",
    ]),
    ...mapState("Logincidi", ["usuarioInterno"]),
  },
  data: () => ({
    texto: "",
    showTitle: true,
    showDescriptionText: false,
    edicionTexto: false,
    editTextButton: true,
    perfilAdmin: false,
    tinymceActivo: false,
  }),
  created() {
    if (this.contenidoFormularios == true) {
      this.showDescriptionText = true;
    }
    if (this.$route.name.includes("admin")) {
      this.perfilAdmin = true;
    } else {
      this.perfilAdmin = false;
    }
  },
  mounted() {
    let cajaTexto = document.getElementById("textoDescripcion");
    if (cajaTexto != null) {
      cajaTexto.innerHTML = this.textoDescripcionFormularios;
    }
  },
  updated() {
    let cajaTexto = document.getElementById("textoDescripcion");
    if (cajaTexto != null) {
      cajaTexto.innerHTML = this.textoDescripcionFormularios;
    }

    if (this.tinymceActivo == true) {
      tinyMCE.activeEditor.setContent(this.textoDescripcionFormularios);
      this.tinymceActivo = false;
    }
  },

  methods: {
    ...mapActions("Tramites-seleccionados", ["modificarTextoFormularios"]),
    editarTexto() {
      this.showDescriptionText = false;
      this.editTextButton = false;
      this.edicionTexto = true;
      this.tinymceActivo = true;
    },
    cancelarEditarTexto() {
      this.showTitle = true;
      this.editTextButton = true;
      this.showDescriptionText = true;
      this.edicionTexto = false;
    },
    confirmarEditarTexto(param) {
      this.texto = param;
      this.modificarTextoFormularios(this.texto);
      this.editTextButton = true;
      this.showDescriptionText = true;
      this.edicionTexto = false;
    },
  },
  props: {
    contenidoFormularios: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: "",
    },
    alignText: {
      type: String,
      default: "",
    },
    content: {
      type: String,
      default: "",
    },
  },
});
</script>
