router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.mostrarEditable)) {
    if (store.state.Logincidi.usuarioInterno) {
      next();
    } else {
      next({ name: "home" });
    }
  } else {
    next();
  }
});

export default router;
