<template>
  <v-form ref="formuarioMedida" class="contenedorBuscador" v-model="valid" :lazy-validation="lazy">
    <v-row class="contenedorBuscador">
      <v-col cols="12">
        
        <v-row class="d-flex contenedorBuscador justify-space-between align-center mx-4">
          <v-col cols="12" class="pa-0" v-if="errores !== ''">
            <v-alert
              text
              outlined
              color="red"
              class="py-2 mb-2 mt-1 text-center body-2"
              width="100%"
              close-icon="close"
              dismissible
              v-if="errores !== ''"
            >
              <template v-slot:close="{}">
                <v-icon size="18" color="error" @click="resetearError"
                  >close</v-icon
                >
              </template>
              {{ errores }}
            </v-alert>
          </v-col>
          <v-col xs="12" sm="10" md="9" class="pa-0">
            <v-row>
              <v-col cols="6" xs="6" sm="3" md="3" class="pb-0 pt-4">
                <v-text-field
                  v-model="medida"
                  :rules="requiredMedida"
                  label="Código"
                  outlined
                  @keypress="soloLetras($event)"
                  @keyup="validarLetras(medida)"
                  maxlength="3"
                  dense
                  class="centered-input shrink"
                ></v-text-field>
              </v-col>
              <v-col cols="6" xs="6" sm="4" md="5" class="pb-0 pt-4">
                <v-text-field
                  v-model="diario"
                  :rules="requiredDiario"
                  maxlength="6"
                  @keypress="onlyNumber($event)"
                  label="N° diario"
                  outlined
                  dense
                  class="centered-input shrink"
                ></v-text-field>
              </v-col>
              <v-col cols="6" xs="6" sm="3" md="3" class="pb-0 pt-4">
                <v-text-field
                  v-model="anio"
                  :rules="requiredAnio"
                  @keypress="onlyNumber($event)"
                  label="Año"
                  maxlength="4"
                  outlined
                  dense
                  class="centered-input shrink"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-col>
          <v-col
            cols="12"
            xs="12"
            sm="2"
            md="3"
            class="d-flex justify-end py-2 px-0"
          >
            <v-btn
              outlined
              color="primary"
              class="no-text-transforms boton"
              @click="buscar()"
              :disabled="buscando == true"
              :loading="buscando == true"
            >
              buscar
              <v-icon color="primary" size="14"> </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
import Vue from "vue";
import { mapActions, mapGetters } from "vuex";

export default Vue.extend({
  name: "BusquedaPorMedida",
  data() {
    return {
      diario: null,
      anio: null,
      medida: null,
      valid: true,
      lazy: false,
      requiredMedida: [
        (v) => !!v || "Campo requerido.",
        (v) => (v || "").length != 1 || "Ingresá al menos 2 caracteres",
      ],
      requiredAnio: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
        (v) => (v || "").length == 4 || "Ingresá al menos 4 dígitos",
      ],
      requiredDiario: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
      ],
    };
  },
  computed: {
    ...mapGetters("Consulta-documentos", ["buscando", "errores"]),
  },
  methods: {
    ...mapActions("Consulta-documentos", [
      "busquedaPorMedida",
      "resetearError",
    ]),
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    buscar() {
      if (!this.$refs.formuarioMedida.validate()) {
        return;
      }
      console.log(this.medida.toUpperCase());
      this.busquedaPorMedida({
        medida: this.medida.toUpperCase(),
        diario: Number(this.diario),
        anio: Number(this.anio),
      });
    },
    soloLetras: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 65 || (charCode > 90 && charCode < 97) || charCode > 122) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    validarLetras(param) {
      const regexpNumeros = /[^A-Za-z]/;
      if (param != null && param.match(regexpNumeros)) {
        this.medida = null;
      }
    },
  },
});
</script>
<style scoped>
.contenedorBuscador {
  height:100%;
}
</style>
