import axios from "axios";
import { rgpWebUrl } from "../../services/paths.js";

const initialState = () => ({
  documento: {},
  medidas: [],
  prorrogas: [],
  buscando: false,
  documento_encontrado: null,
  error: "",
});

const state = initialState();

const getters = {
  buscando(state) {
    return state.buscando;
  },
  documento(state) {
    return state.documento;
  },
  medidas(state) {
    return state.medidas;
  },
  prorrogas(state) {
    return state.prorrogas;
  },
  cantidadProrrogas(state) {
    return state.prorrogas.reduce((result, prorroga) => {
      result += Number(prorroga.cantidadDias);
      return result;
    }, 0);
  },
  errores(state) {
    return state.error;
  },
  documentoEncontrado(state) {
    return state.documento_encontrado;
  },
};

const mutations = {
  RESPUESTA_DOCUMENTO(state, documento) {
    state.documento = { ...documento };
  },
  RESPUESTA_MEDIDAS(state, medidas) {
    state.medidas = [...medidas];
  },
  RESPUESTA_PRORROGAS(state, prorrogas) {
    state.prorrogas = [...prorrogas];
  },
  DOCUMENTO_ENCONTRADO(state, seEncontro) {
    state.documento_encontrado = seEncontro;
  },
  BUSCANDO(state, estaBuscando) {
    state.buscando = estaBuscando;
  },
  SETEAR_ERROR(state, errores) {
    state.error = errores;
  },
  RESET_ERROR(state) {
    state.error = "";
  },
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach((key) => {
      state[key] = newState[key];
    });
  },
};

const actions = {
  busquedaPorUnico({ commit, dispatch }, n_unico) {
    commit("RESET");
    commit("BUSCANDO", true);
    axios
      .get(`${rgpWebUrl}/getDatosDocumento?nroUnico=${n_unico}`)
      .then((response) => {
        if (response.data.nroUnico == n_unico) {
          commit("RESPUESTA_DOCUMENTO", response.data);
          commit("DOCUMENTO_ENCONTRADO", true);
          dispatch("busquedaMedidasPorUnico", n_unico);
          dispatch("busquedaProrrogasPorUnico", n_unico);
        } else {
          commit("DOCUMENTO_ENCONTRADO", false);
        }
        commit("BUSCANDO", false);
      })
      .catch((error) => {
        commit(
          "SETEAR_ERROR",
          "Algo salió mal. Por favor, intentá nuevamente más tarde."
        );
        commit("BUSCANDO", false);
      });
  },
  busquedaMedidasPorUnico({ commit }, n_unico) {
    axios
      .get(`${rgpWebUrl}/obtenerMedidasPorUnico?nroUnico=${n_unico}`)
      .then((response) => {
        commit("RESPUESTA_MEDIDAS", response.data);
      })
      .catch((error) => {
        commit(
          "SETEAR_ERROR",
          "Algo salió mal. Por favor, intentá nuevamente más tarde."
        );
        commit("BUSCANDO", false);
      });
  },
  busquedaProrrogasPorUnico({ commit }, n_unico) {
    axios
      .get(`${rgpWebUrl}/prorrogasDocumento?nroUnico=${n_unico}`)
      .then((response) => {
        commit("RESPUESTA_PRORROGAS", response.data);
      })
      .catch((error) => {
        commit(
          "SETEAR_ERROR",
          "Algo salió mal. Por favor, intentá nuevamente más tarde."
        );
        commit("BUSCANDO", false);
      });
  },
  busquedaPorMedida({ commit, dispatch }, obj_medida) {
    let params = Object.assign(
      {},
      {
        medida: obj_medida.medida,
        diario: Number(obj_medida.diario),
        anio: Number(obj_medida.anio),
      }
    );
    commit("RESET");
    commit("BUSCANDO", true);
    axios
      .get(`${rgpWebUrl}/getDocPorMedida`, { params: params })
      .then((response) => {
        if (!response.data.error) {
          dispatch("busquedaPorUnico", response.data);
        } else {
          commit("DOCUMENTO_ENCONTRADO", false);
        }
        commit("BUSCANDO", false);
      })
      .catch((error) => {
        commit(
          "SETEAR_ERROR",
          "Algo salió mal. Por favor, intentá nuevamente más tarde."
        );
        commit("BUSCANDO", false);
      });
  },
  busquedaPorTramite({ commit, dispatch }, n_tramite) {
    commit("RESET");
    commit("BUSCANDO", true);
    axios
      .get(`${rgpWebUrl}/busquedaPorTramite?n_tramite=${n_tramite}`)
      .then((response) => {
        if (response.data.trmId == n_tramite) {
          commit("RESPUESTA_DOCUMENTO", response.data);
          commit("DOCUMENTO_ENCONTRADO", true);
          dispatch("busquedaMedidasPorUnico", response.data.nroUnico);
          dispatch("busquedaProrrogasPorUnico", response.data.nroUnico);
        } else {
          commit("DOCUMENTO_ENCONTRADO", false);
        }
        commit("BUSCANDO", false);
      })
      .catch((error) => {
        commit(
          "SETEAR_ERROR",
          "Algo salió mal. Por favor, intentá nuevamente más tarde."
        );
        commit("BUSCANDO", false);
      });
  },
  resetearError({ commit }) {
    commit("RESET_ERROR");
  },
  reset({ commit }) {
    commit("RESET");
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
