<template>
  <v-container fluid>
    <TitleSection :title="tituloSeccion" section="B"></TitleSection>
    <div v-if="loading" class="final d-flex align-center justify-center pb-3">
      <v-progress-circular
        :size="40"
        :width="5"
        color="primary"
        indeterminate
      ></v-progress-circular>
    </div>
    <div class="pb-3" v-if="!loading">
      <DescriptionSection
        alignText=""
        :content="tramite_seleccionado.descripcion"
      />
      <v-row>
        <ReqTimeline
          title="Requisitos"
          subtitle="Requerimientos"
          :requires="requerimientos('PRESENCIAL')"
          :timeline="arrayVacio"
          :presencial="true"
          :id="tramite_seleccionado.id"
        />
        <ReqTimeline
          title="Solicitud por carga virtual"
          subtitle="Paso a paso"
          :timeline="paso_a_paso('CVD')"
          :requires="arrayVacio"
          :presencial="false"
          v-if="tramite_seleccionado.habilita_cvd"
          :id="tramite_seleccionado.id"
        />

        <ReqTimeline
          title="Solicitud web"
          subtitle="Paso a paso"
          :timeline="paso_a_paso('WEB')"
          :requires="arrayVacio"
          :presencial="false"
          v-if="tramite_seleccionado.habilita_web"
          :id="tramite_seleccionado.id"
        />

        <ReqTimeline
          title="Solicitud presencial"
          subtitle="Paso a paso"
          :timeline="paso_a_paso('PRESENCIAL')"
          :requires="arrayVacio"
          :presencial="false"
          v-if="tramite_seleccionado.habilita_presencial"
          :id="tramite_seleccionado.id"
        />
      </v-row>
    </div>
  </v-container>
</template>

<script>
import Vue from "vue";
import TitleSection from "../components/shared/body/TitleSection.vue";
import DescriptionSection from "../components/requisitos-tramite/DescriptionSection.vue";
import ReqTimeline from "../components/requisitos-tramite/ReqTimeline.vue";
import { mapGetters, mapMutations, mapState, mapActions } from "vuex";

export default {
  /* TODO: * Esta vista deberia tener predeterminado el componente que muestra el listado de tramites.
   * Deberia tener el fondo de la imagen correspondiente.
   * Ver si poner el loader en el header o en este componente.
   */
  name: "reports",
  components: {
    TitleSection,
    DescriptionSection,
    ReqTimeline,
  },
  data: () => ({
    tituloSeccion: "Informes",
    loading: false,
    arrayVacio: [],
  }),
  // beforeUpdate() {

  // },
  mounted() {
    this.seleccion();
    if (this.tramite_seleccionado != {}) {
      this.tituloSeccion = "Descripción";
    }
  },
  beforeDestroy() {
    this.LIMPIAR_TRAMITE_SELECCIONADO();
  },
  computed: {
    ...mapGetters("Listado-tramites", ["obtenerTramitePorId"]),
    ...mapState("Tramites-seleccionados", ["tramite_seleccionado"]),
    //Si se elimina esta linea deja de funcionar la actualización de página
    ...mapState("Listado-tramites", ["tramites"]),
    listadoTramites() {
      return this.tramites;
    },
  },
  methods: {
    ...mapMutations("Tramites-seleccionados", ["LIMPIAR_TRAMITE_SELECCIONADO"]),
    ...mapActions("Tramites-seleccionados", ["seleccionarTramite"]),

    seleccion() {
      if (!this.tramite_seleccionado.hasOwnProperty("id")) {
        this.seleccionarTramite(
          this.obtenerTramitePorId(this.$route.params.id)
        );
        this.loading = true;
      }
      if (this.tramite_seleccionado.hasOwnProperty("id")) {
        this.loading = false;
      }
    },
    //refactorizar
    requerimientos(tipo) {
      if (this.tramite_seleccionado.requerimientos) {
        let items = this.tramite_seleccionado.requerimientos.filter(
          (element) => element.descripcion != null
        );
        const requerimiento_tipado = items.filter(
          (obj) => obj.tipo.toLowerCase() === tipo.toLowerCase()
        );
        return requerimiento_tipado;
      }
    },
    paso_a_paso(tipo) {
      if (this.tramite_seleccionado.paso_a_paso) {
        let items = this.tramite_seleccionado.paso_a_paso.filter(
          (element) => element.descripcion != null
        );
        const paso_a_paso_tipado = items.filter(
          (obj) => obj.tipo.toLowerCase() === tipo.toLowerCase()
        );

        // si la bd lo trae ordenado esto es innecesario.
        const paso_a_paso_ordenado = paso_a_paso_tipado.sort((a, b) =>
          a.orden > b.orden ? 1 : b.orden > a.orden ? -1 : 0
        );
        return paso_a_paso_ordenado;
      }
    },
    cambioEstadoTramites() {
      this.seleccion();
    },
  },
  watch: {
    listadoTramites() {
      this.cambioEstadoTramites();
    },
  },
};
</script>
<style scoped>
.contenedor {
  width: 100%;
  margin: auto;
}
</style>
