import axios from "axios"
import { pagosUrl } from '../../../services/paths'

export const getPagosOnlinePorTramiteService = async (dataParam) => {
    try {
        const { data } = await axios.get(`${pagosUrl}/consultaPagoOnlinePorTramite`, {
            params: dataParam
        })

        return data
    } catch (error) {
        console.error(error)
    }
}