import axios from "axios";
import { rgpWebUrl } from "../../services/paths.js";

const initialState = () => ({
  tramites: [],
  listadoFormulariosGenericos: [],
  marked: localStorage.getItem("marcados")
    ? [...JSON.parse(localStorage.getItem("marcados"))]
    : [],
  errorConexion: false,
  listadoRequerimientos: [],
  listadoRequerimientosGenericos: [],
  listadoPasosGenericos: [],
  listadoPasos: [],
  anularExpansionPanel: true,
  //tramite seleccionado en eleccion tramite
  tramiteEditable: {},
  itemsTramiteEditable: [],
  formularioEleccionValidado: false,
  mostrarCajaEdicionLink: false,
  edicionItemTramite: false,
  creacionItemTramite: false,
  tramitesPorIdItem: [],
  tramitesPorAnio: []
});

const state = initialState();

const getters = {
  obtenerTramitePorId: (state) => (id) => {
    return state.tramites.find((tramite) => {
      return tramite.id == id;
    });
  },
  existenTramites: (state) => {
    return state.tramites.length === 0;
  },
  existenFormularios: (state) => {
    return state.listadoFormulariosGenericos.length === 0;
  },
  tramitesPorIdItem: (state) => {
    return state.tramites.filter(
      (v, i, a) => a.findIndex((t) => t.id_item === v.id_item) === i
    );
  },
  tramitesTipoTasa: (state) => {
    return state.tramites.filter(
      (v, i, a) => a.findIndex((t) => t.id_item === v.id_item) === i && v.tipo == "Tasa"
    );
  },
  tramitesTipoTasaPorAnio: (state) => {
    return state.tramitesPorAnio.filter(
      (v, i, a) => a.findIndex((t) => t.id_item === v.id_item) === i && v.tipo == "Tasa"
    );
  },
};

const mutations = {
  AGREGAR_TRAMITES(state, procedure) {
    state.tramites = [...procedure];
  },
  AGREGAR_REQUERIMIENTOS(state, procedure) {
    state.listadoRequerimientos = [...procedure];
  },
  AGREGAR_REQUERIMIENTOS_GENERICOS(state, procedure) {
    state.listadoRequerimientosGenericos = [...procedure];
  },
  AGREGAR_PASOS(state, procedure) {
    state.listadoPasos = [...procedure];
  },
  AGREGAR_PASOS_GENERICOS(state, procedure) {
    state.listadoPasosGenericos = [...procedure];
  },
  GUARDAR_TRAMITE_EDITABLE(state, tramite) {
    state.tramiteEditable = { ...tramite };
  },
  VALIDAR_FORMULARIO_ELECCION(state) {
    state.formularioEleccionValidado = true;
  },
  ANULAR_FORMULARIO_ELECCION(state) {
    state.formularioEleccionValidado = false;
  },
  ADD_MARKED(state, id) {
    // Dividir en procesos con menos responsabilidad y llamar por action.
    let exist = state.marked.findIndex((key) => key === id);
    let location = state.tramites.findIndex((key) => key.id === id);
    if (exist === -1) {
      state.tramites[location].marked = true;
      state.marked.push(id);
      localStorage.setItem("marcados", JSON.stringify([...state.marked]));
    }
  },
  AGREGAR_FORMULARIOS_GENERICOS(state, procedure) {
    state.listadoFormulariosGenericos = [...procedure];
  },
  GUARDAR_LISTADO_FORMULARIOS(state, listado) {
    state.listadoFormularios = [...listado];
  },
  MOSTRAR_ERROR_CONEXION(state) {
    state.errorConexion = true;
  },
  OCULTAR_ERROR_CONEXION(state) {
    state.errorConexion = false;
  },

  HABILITAR_PANEL_EDICION_TRAMITE(state) {
    state.anularExpansionPanel = false;
  },
  ANULAR_PANEL_EDICION_TRAMITE(state) {
    state.anularExpansionPanel = true;
  },
  FILTRAR_ITEMS_TRAMITE_EDITABLE(state, items) {
    state.itemsTramiteEditable = [...items];
  },

  REMOVE_MARKED(state, id) {
    // Dividir en procesos con menos responsabilidad y llamar por action.
    let exist = state.marked.indexOf(id);
    state.marked.splice(exist, 1);
    let location = state.tramites.findIndex((key) => key.id === id);
    state.tramites[location].marked = false;
    localStorage.setItem("marcados", JSON.stringify([...state.marked]));
  },
  RESET(state) {
    const newState = initialState();
    Object.keys(newState).forEach((key) => {
      state[key] = newState[key];
    });
  },
  MOSTRAR_EDICION_LINK(state) {
    state.mostrarCajaEdicionLink = true;
  },
  OCULTAR_EDICION_LINK(state) {
    state.mostrarCajaEdicionLink = false;
  },
  MOSTRAR_EDICION_ITEM_TRAMITE(state) {
    state.edicionItemTramite = true;
  },
  OCULTAR_EDICION_ITEM_TRAMITE(state) {
    state.edicionItemTramite = false;
  },
  MOSTRAR_CREACION_ITEM_TRAMITE(state) {
    state.creacionItemTramite = true;
  },
  OCULTAR_CREACION_ITEM_TRAMITE(state) {
    state.creacionItemTramite = false;
  },
  AGREGAR_TRAMITES_POR_ANIO(state, procedure) {
    state.tramitesPorAnio.push(procedure)
  },
  SET_TRAMITES_POR_ANIO(state, procedure) {
    state.tramitesPorAnio = procedure
  }
};

const actions = {
  extraerFormulariosGenericos({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/formularios-adjuntos`)
      .then((response) => {
        let array = response.data;
        commit("AGREGAR_FORMULARIOS_GENERICOS", array);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarFormularioATramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/agregar-formulario-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarFormularioATramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/eliminar-formulario-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  extraerRequerimientos({ commit, state }, id) {
    axios
      .get(`${rgpWebUrl}/requerimientos?idtramite=${id}`)
      .then((response) => {
        let array = response.data;
        commit("AGREGAR_REQUERIMIENTOS", array);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  extraerPasos({ commit, state }, id) {
    axios
      .get(`${rgpWebUrl}/pasos?idtramite=${id}`)
      .then((response) => {
        let array = response.data;
        commit("AGREGAR_PASOS", array);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  extraerPasosGenericos({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/pasos?idtramite=0`)
      .then((response) => {
        let array = response.data;
        commit("AGREGAR_PASOS_GENERICOS", array);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  extraerRequerimientosGenericos({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/requerimientos?idtramite=0`)
      .then((response) => {
        let array = response.data;
        commit("AGREGAR_REQUERIMIENTOS_GENERICOS", array);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarRequerimientoGenerico({ commit, dispatch }, requerimiento) {
    axios
      .post(`${rgpWebUrl}/requerimientos`, requerimiento)
      .then((response) => {
        dispatch("extraerRequerimientosGenericos");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarPasoGenerico({ commit, dispatch }, paso) {
    axios
      .post(`${rgpWebUrl}/pasos`, paso)
      .then((response) => {
        dispatch("extraerPasosGenericos");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarRequerimientoGenerico({ commit, dispatch }, id) {
    axios
      .delete(`${rgpWebUrl}/requerimientos/${id}`)
      .then((response) => {
        dispatch("extraerRequerimientosGenericos");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarPasoGenerico({ commit, dispatch }, id) {
    axios
      .delete(`${rgpWebUrl}/pasos/${id}`)
      .then((response) => {
        dispatch("extraerPasosGenericos");
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarPasoGenerico({ commit, dispatch }, paso) {
    axios
      .put(`${rgpWebUrl}/pasos`, paso)
      .then((response) => {
        dispatch("extraerPasosGenericos");
        dispatch("extraerTramites");
        if (state.tramiteEditable != {}) {
          dispatch("extraerPasos", state.tramiteEditable.id);
        }
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarRequerimientoGenerico({ commit, dispatch }, requerimiento) {
    axios
      .put(`${rgpWebUrl}/requerimientos`, requerimiento)
      .then((response) => {
        dispatch("extraerRequerimientosGenericos");
        dispatch("extraerTramites");
        if (state.tramiteEditable != {}) {
          dispatch("extraerRequerimientos", state.tramiteEditable.id);
        }
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarPasoEnTramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/paso-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites", item.idtramite);
        dispatch("extraerPasos", item.idtramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  modificarRequerimientoEnTramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/requerimiento-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites", item.idtramite);
        dispatch("extraerRequerimientos", item.idtramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarPasoEnTramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/eliminar-paso-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites", item.idtramite);
        dispatch("extraerPasos", item.idtramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  eliminarRequerimientoEnTramite({ commit, dispatch }, item) {
    axios
      .post(`${rgpWebUrl}/eliminar-requerimiento-por-tramite`, item)
      .then((response) => {
        dispatch("extraerTramites", item.idtramite);
        dispatch("extraerRequerimientos", item.idtramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  editarLink({ commit, dispatch }, item) {
    let params = Object.assign(
      {},
      {
        id: item.id,
        tipo: item.tipo.charAt(0),
        url: item.url,
      }
    );
    console.log(
      `${rgpWebUrl}/url-tramite?id=${params.id}&tipo=${
        params.tipo
      }&url=${encodeURIComponent(params.url)}`
    );
    axios
      .put(
        `${rgpWebUrl}/url-tramite?id=${params.id}&tipo=${
          params.tipo
        }&url=${encodeURIComponent(params.url)}`
      )
      .then((response) => {
        dispatch("extraerTramites", item.idtramite);
        // dispatch("extraerPasos", item.idtramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  anularPanelEdicionTramite({ commit }) {
    commit("ANULAR_PANEL_EDICION_TRAMITE");
  },
  habilitarPanelEdicionTramite({ commit }) {
    commit("HABILITAR_PANEL_EDICION_TRAMITE");
  },
  extraerTramites({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/itemsTasas`)
      .then((response) => {
        const tramites = JSON.parse(JSON.stringify(response.data))
        tramites.map((tramite, index) => {
          if (
            tramites[index].descripcion == null ||
            tramites[index].descripcion.trim() == ""
          ) {
            tramites[index].descripcion =
              "No se ha generado una descripción para este tramite.";
          }
          state.marked.includes(tramite.id)
            ? (tramites[index].marked = true)
            : (tramites[index].marked = false);
        });
        commit("AGREGAR_TRAMITES", tramites);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  extraerFormularios({ commit, state }) {
    axios
      .get(`${rgpWebUrl}/formularios`)
      .then((response) => {
        commit("GUARDAR_LISTADO_FORMULARIOS", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  guardarTramiteEditable({ commit }, tramite) {
    commit("GUARDAR_TRAMITE_EDITABLE", tramite);
  },
  validarFormularioEdicion({ commit }, state) {
    commit("VALIDAR_FORMULARIO_EDICION");
  },
  validarFormularioEleccion({ commit }, state) {
    commit("VALIDAR_FORMULARIO_ELECCION");
  },
  anularFormularioEleccion({ commit }, state) {
    commit("ANULAR_FORMULARIO_ELECCION");
  },
  filtrarItemsTramiteEditable({ commit }, items) {
    commit("FILTRAR_ITEMS_TRAMITE_EDITABLE", items);
  },
  guardarListadoFormularios({ commit }, listado) {
    commit("GUARDAR_LISTADO_FORMULARIOS", listado);
  },
  reset({ commit }) {
    commit("RESET");
  },
  mostrarEdicionLink({ commit }) {
    commit("MOSTRAR_EDICION_LINK");
  },
  ocultarEdicionLink({ commit }) {
    commit("OCULTAR_EDICION_LINK");
  },
  mostrarEdicionItemTramite({ commit }) {
    commit("MOSTRAR_EDICION_ITEM_TRAMITE");
  },
  ocultarEdicionItemTramite({ commit }) {
    commit("OCULTAR_EDICION_ITEM_TRAMITE");
  },
  mostrarCreacionItemTramite({ commit }) {
    commit("MOSTRAR_CREACION_ITEM_TRAMITE");
  },
  ocultarCreacionItemTramite({ commit }) {
    commit("OCULTAR_CREACION_ITEM_TRAMITE");
  },
  extraerTramitesPorAnio({ commit, state }, anio) {
    axios
      .get(`${rgpWebUrl}/itemsTasasPorAnio?anio=${anio}`)
      .then((response) => {
        response.data.map((tramite, index) => {
          if (
            response.data[index].descripcion == null ||
            response.data[index].descripcion.trim() == ""
          ) {
            response.data[index].descripcion =
              "No se ha generado una descripción para este tramite.";
          }
          // state.marked.includes(tramite.id)
          //   ? (response.data[index].marked = true)
          //   : (response.data[index].marked = false);
        });
        commit("SET_TRAMITES_POR_ANIO", response.data);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  cargarTramitesPorAnio({ commit, state }, item) {
    let anio = item.anio
    axios
      .get(`${rgpWebUrl}/itemsTasasPorAnio?anio=${anio}`)
      .then((response) => {
        let tramite = response.data.find(element => element.id_item.trim() == item.id_item.trim())
        commit("AGREGAR_TRAMITES_POR_ANIO", tramite);
        commit("OCULTAR_ERROR_CONEXION");
      })
      .catch(() => {
        commit("MOSTRAR_ERROR_CONEXION");
      });
  },
  agregarTramitePorAnio({commit}, array) {
    commit("AGREGAR_TRAMITES_POR_ANIO", array)
  },
  setTramitesAnioAnterior({commit}, array) {
    commit("SET_TRAMITES_POR_ANIO", array)
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
