    ...mapGetters("Listado-resoluciones", ["existenManuales"]),
  },
  created() {
    this.conjuntoPorAnios();

    // if(this.existenManuales) {
    //     this.loading = true;
    //     setTimeout(() => {
    //       try{
    //         this.extraerManuales()
    //         this.conjuntoPorAnios();      
    //       }catch(error){
    //         this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intente nuevamente.", true, false);
    //         this.loading = false;
    //         return
    //       }  
    //       this.loading = false;
    //     }, 1500);
    // }
    if(this.$route.name.includes("admin")){
        this.perfilAdmin = true;
        this.newManual = true;
    }
  },
  
  beforeUpdate() {
    this.conjuntoPorAnios(); 
  },

  mounted() {
    this.actualizarPagina();
  },
  methods: {
    ...mapActions("Listado-resoluciones", [
      "extraerManuales", "agregarManual", "eliminarManual", 
      "modificarManual",
    ]),
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    scroll(id) {  
      document.getElementById(id).scrollIntoView({
        behavior: "smooth"
      });
    },
    mostrarSnackbar(color, time, height, mensaje, botonAceptar, botonesConfirmar){
      this.snackbar = true;
      this.colorSnackbar = color;
      this.timeSnackbar = time;
      this.heightSnackbar = height;
      this.mensajeSnackbar = mensaje;
      this.botonAceptarSnackbar= botonAceptar;
      this.botonesConfirmarSnackbar= botonesConfirmar;
    },
    mostrarCrearManual() {
      this.editionBox = true;
      this.titleEdition = "Crear Manual";
      this.textBoton = "Crear";
      this.showAlert = false;
      this.newManual = false;
      this.listarCategorias()  
    },
    cancelarEdicion() {
      this.editionBox = false;
      this.newManual = true;
      this.$refs.formulario.reset();
      this.showAlert = false;
    },
    confirmar() {
      this.$refs.formulario.validate();
      if(this.$refs.formulario.validate()== true){
        this.showAlert = false;
        if (this.titleEdition == "Crear Manual") {
          this.crearManual();
        } else {
          this.editarManual();
        }
      }else{
        this.showAlert = true;
      }
    },
    crearManual(){
      let manual = {}
        manual.categoria = this.categoriaManual;
        manual.anio = this.anioManual;
        manual.pdf = this.linkPdf;
        manual.descripcion = this.nuevoManual;

      try {
        this.agregarManua(manual);
      } catch (error) {
        this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intente nuevamente.", true, false)
        return;
      }
      this.mostrarSnackbar("success", 3000, 30, "La creación se realizó con éxito.", false, false)
      this.actualizarPagina();
      this.newManual = true;
      this.$refs.formulario.reset();
      this.cancelarEdicion();
    },
    mostrarEliminarManual(param) {
      this.idManualSeleccionado = param.id
      this.mostrarSnackbar("primary", 5000, 60, "¿Confirma que desea eliminar la resolución: " + param.manual + "?", false, true)
    },
    deleteManual() {
      try {
        this.eliminarManual(this.idManualSeleccionado);
      } catch (error) {
        this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intente nuevamente.", true, false)
        return
      }
      this.mostrarSnackbar("success", 3000, 30, "La eliminación se realizó con éxito.", false, false)
      this.actualizarPagina();
      this.idManualSeleccionado = null;
    },
    conjuntoPorAnios() {
      this.manualesAnios = [];
      this.manualesCate=[];
      this.listadoDeCategorias = [];
      this.manuales.forEach((el) => {
        if (this.manualesAnios.find((ele) => ele === el.anio) == undefined) {
          this.manualesAnios.push(el.anio);
        }
      });
      this.manualesAnios = this.manualesAnios.sort(function (a, b) { return b - a })
      this.manualesAnios.forEach((elemento, index) => {
        this.manualesCate = this.manuales.reduce((accu, i) => {
          if (elemento === i.anio)
            (accu[i.categoria] = accu[i.categoria] || []).push({
              manual: i.descripcion,
              pdf: i.pdf,
              anio: i.anio,
              id:i.id,
              categoria: i.categoria
            });
          return accu;
        }, {});
        this.listadoDeCategorias.push({
          anio: elemento,
          categorias: this.manualesCate,
        });
      });
    },
    listarCategorias() {
      this.manuales.forEach((el) => {
        if (
          this.itemCategorias.find((ele) => ele === el.categoria) == undefined
        ) {
          this.itemCategorias.push(el.categoria);
        }
      });
      this.itemCategorias = this.itemCategorias.sort()
    },
    actualizarPagina() {
      this.conjuntoPorAnios();
    },
    anularSnackbar() {
      this.snackbar = false;
      this.colorSnackbar = "";
      this.timeSnackbar = -1;
      this.heightSnackbar = "";
      this.mensajeSnackbar = "";
      this.botonAceptarSnackbar= false;
      this.botonesConfirmarSnackbar= false;
      this.idManualSeleccionado = null;
    },
    mostrarEditarManual(param) {
      this.editionBox = true;
      this.showAlert = false;
      this.newManual = false;
      this.scroll("inicioPagina");
      this.titleEdition = "Editar Manual";
      this.textBoton = "Modificar";
      this.categoriaManual = param.categoria;
      this.nuevoManual = param.manual;
      this.anioManual = param.anio;
      this.linkPdf = param.pdf;
      this.idManualSeleccionado = param.id;
      this.listarCategorias() 
    },
    editarManual() {
      let manual = {};
        manual.id = this.idManualSeleccionado;
        manual.descripcion = this.nuevoManual;
        manual.categoria = this.categoriaManual;
        manual.pdf = this.linkPdf;
        manual.anio = this.anioManual;
      try {
        this.modificarManual(manual);
      } catch (error) {
        this.mostrarSnackbar("red", 5000, 60, "Hubo un error en la conexión. Por favor intente nuevamente.", true, false)
        return;
      }
      this.mostrarSnackbar("success", 3000, 30, "La modificación se realizó con éxito.", false, false)
      this.actualizarPagina();
      this.editionBox = false;
      this.nuevaResolucion = true;
      this.$refs.formulario.reset()
      this.idManualSeleccionado = null;

    },
  },
});
</script>

