<template>
  <v-dialog
    v-model="modalSeleccionTramitesFaltaReponer"
    persistent
    max-width="1240"
  >
    <v-card>
      <v-card-title class=" titleModal">
        <span>Selección de trámites</span>
        <v-col cols="8" xs="4" sm="3" md="2" class="px-2 py-0 ">
          <v-row class="mx-1">
            <v-col cols="12" class="px-0 pt-1 pb-0">
              <v-select
                v-model="anioTramites"
                :items="this.aniosTramites"
                item-text="anio"
                item-value="id"
                outlined
                dense
                return-object
                @change="changeYear($event)"
              ></v-select>
            </v-col>
          </v-row>
        </v-col>
      </v-card-title>

      <v-divider></v-divider>

      <v-card-text class="font-weight-medium subtitle-2 pa-0">
        <!-- v-if="!ingresadoEnCidi" -->
        <!-- <v-alert
          outlined
          text
          border="left"
          color="primary"
          dense
          prominent
          hide-on-leave
          class="ma-2"
          transition="scale-transition"
        >
          Recordá que solo podés ingresar un formulario de publicidad directa
          por tasa.
        </v-alert> -->
        <v-alert
          outlined
          text
          border="left"
          color="deep-orange"
          dense
          prominent
          hide-on-leave
          class="ma-2 text-center"
          transition="scale-transition"
          v-if="reimprimirEfectivo != null"
        >
          Para agregar items tenés que generar una nueva tasa.
        </v-alert>
        <v-data-table
          v-model="selected"
          :headers="headers"
          :items="tramitesFiltrados()"
          item-key="id_item"
          :disabled-select-all="true"
          class="tabla-tramite"
          :some-items="false"
          @click:row="selectRow"
          show-select
          v-if="!loading"
        >
          <!-- :item-class="itemDeshabilitado" -->
          <template v-slot:[`header.data-table-select`]="{}"> </template>

          <template
            v-slot:[`item.data-table-select`]="{ item, isSelected, select }"
          >
            <v-simple-checkbox
              color="primary"
              v-ripple
              :readonly="item.deshabilitado"
              :disabled="
                item.deshabilitado || estalistaDeshabilitado(item.id_item)
              "
              :value="isSelected"
              @input="select($event)"
            ></v-simple-checkbox>
          </template>

          <template
            v-slot:footer
            v-if="seleccionados.length > 0 || agregadosEnCarrito.length > 0"
          >
            <v-divider></v-divider>
            <v-row no-gutters class="cantDias">
              <v-col
                v-if="seleccionados.length > 0"
                class="d-flex flex-wrap justify-center align-center py-1 px-3"
              >
                Seleccionados:
                <v-chip
                  color="primary ml-1 my-1 minimo d-flex justify-center"
                  dark
                  v-for="tramiteSeleccionado in seleccionados"
                  :key="tramiteSeleccionado"
                >
                  {{ tramiteSeleccionado }}
                </v-chip>
              </v-col>
              <v-col
                v-if="agregadosEnCarrito.length > 0"
                class="d-flex flex-wrap justify-center align-center py-1 px-3"
              >
                Agregados:
                <v-chip
                  color="error ml-1 my-1 minimo d-flex justify-center"
                  dark
                  v-for="tramiteAgregado in agregadosEnCarrito"
                  :key="tramiteAgregado"
                >
                  {{ tramiteAgregado }}
                </v-chip>
              </v-col>
            </v-row>
          </template>
        </v-data-table>
        <div
          v-if="loading"
          class="final d-flex align-center justify-center pb-3"
        >
          <v-card
            outlined
            height="100%"
            min-height="200px"
            width="100%"
            class="d-flex align-center justify-center"
          >
            <div>
              <v-progress-circular
                :size="40"
                :width="5"
                color="primary"
                indeterminate
              ></v-progress-circular>
            </div>
          </v-card>
        </div>
      </v-card-text>

      <v-card-actions>
        <v-btn color="red darken-1" text @click="cerrar()">
          Cancelar
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="aplicar()">
          Aplicar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import { mapState, mapActions, mapGetters } from "vuex";

export default Vue.extend({
  name: "ModalSeleccionTramitesFaltaReponer",
  data: () => ({
    selected: [],
    deshabilitarTodo: false,
    loading: false,
    anioTramites: 1,
    aniosTramites: [],
    headers: [
      {
        sortable: false,
        text: "ITEM TASA",
        align: "start",
        value: "id_item",
        width: "100",
      },
      { sortable: false, text: "DESCRIPCIÓN LIA", value: "leyenda" },
    ],
  }),
  computed: {
    ...mapState("Modal", ["modalSeleccionTramitesFaltaReponer"]),
    ...mapState("Tramites-seleccionados", ["warehouse"]),
    ...mapState("Listado-tramites", ["tramitesPorAnio"]),
    ...mapGetters("Tramites-seleccionados", ["tramitesActivos"]),
    ...mapGetters("Listado-tramites", ["tramitesTipoTasaPorAnio"]),
    ...mapGetters("Tramites-completados", ["reimprimirEfectivo"]),

    seleccionados() {
      return this.selected.map((value) => {
        return value.id_item;
      });
    },
    agregadosEnCarrito() {
      return this.tramitesActivos.map((value) => {
        return value.id_item;
      });
    },
    controladorCantidad() {
      if (this.selected.length + this.tramitesActivos.length >= 10) return true;
      else return false;
    },
    busquedaTramites() {
      return this.tramitesPorAnio;
    },
    abrirModal() {
      return this.modalSeleccionTramitesFaltaReponer;
    },
  },
  created() {
    let year = new Date().getFullYear();
    for (let i = year; i >= 2007; i--) {
      this.aniosTramites.push(i);
    }
    this.loading = true;
    this.extraerTramitesPorAnio(year);
  },
  mounted() {
    this.anioTramites = new Date().getFullYear();
    this.loading = true;
    this.extraerTramitesPorAnio(this.anioTramites);
  },
  methods: {
    ...mapActions("Modal", ["resetearModal"]),
    ...mapActions("Listado-tramites", ["extraerTramitesPorAnio"]),
    ...mapActions("Tramites-seleccionados", [
      "reqAddProcedure",
      "validarTramite",
    ]),
    ...mapActions("Tramites-completados", ["agregarTramiteCompledo"]),

    cerrar() {
      this.selected = [];
      this.resetearModal();
    },
    changeYear(value) {
      this.loading = true;
      this.extraerTramitesPorAnio(value);
    },
    tramitesFiltrados() {
      this.tramitesTipoTasaPorAnio.forEach((item) => {
        item.deshabilitado = false;
        this.tramitesActivos.forEach((itemActivo) => {
          if (
            itemActivo.id_item == item.id_item ||
            item.tipo == "Publicidad Directa" ||
            itemActivo.tipo == "Publicidad Directa"
          ) {
            item.deshabilitado = true;
          }
        });

        if (this.selected[0]) {
          if (
            (this.selected[0].tipo == "Tasa" &&
              item.tipo == "Publicidad Directa") ||
            (this.selected[0].tipo == "Publicidad Directa" &&
              item.id_item != this.selected[0].id_item)
          ) {
            item.deshabilitado = true;
          }
        }

        if (this.reimprimirEfectivo != null) {
          item.deshabilitado = true;
        }
      });
      return this.customSort(this.tramitesTipoTasaPorAnio, "asc");
    },
    aplicar() {
      this.selected.map((value, key) => {
        value.formulario.campos.forEach((el) => (el.valor = null));
        this.reqAddProcedure(JSON.parse(JSON.stringify(value)));
      });
      this.selected.forEach((element) => {
        if (element.tipo == "Tasa" && element.formulario.campos.length == 0) {
          let tramite = this.warehouse.find((el) => el.id == element.id);
          let posicion = this.warehouse.indexOf(tramite);
          this.validarTramite(posicion);
          this.agregarTramiteCompledo(JSON.parse(JSON.stringify(element)));
        }
      });
      this.selected = [];
      this.cerrar();
      this.anioTramites = new Date().getFullYear();
    },
    selectRow(value, row) {
      if (!value.deshabilitado) {
        if (!this.estalistaDeshabilitado(value.id_item)) {
          if (row.isSelected) {
            row.select(false);
          } else {
            row.select(true);
          }
        }
      }
    },
    existeEnSeleccionados(item) {
      return this.selected.some(function(el) {
        return el.id_item === item;
      });
    },

    estalistaDeshabilitado(item) {
      if (this.controladorCantidad) {
        if (this.existeEnSeleccionados(item)) return false;
        else return true;
      }
      return false;
    },
    customSort(d, order) {
      let sort = {
          asc: function(a, b) {
            let l = 0,
              m = Math.min(a.value.length, b.value.length);
            while (l < m && a.value[l] === b.value[l]) {
              l++;
            }
            return l === m
              ? a.value.length - b.value.length
              : a.value[l] - b.value[l];
          },
          desc: function(a, b) {
            return sort.asc(b, a);
          },
        },
        mapped = d.map(function(el, i) {
          return { index: i, value: el.id_item.split(".").map(Number) };
        });

      mapped.sort(sort[order] || sort.asc);

      return mapped.map(function(el) {
        return d[el.index];
      });
    },
  },
  watch: {
    tramitesActivos() {
      this.tramitesFiltrados();
    },
    busquedaTramites() {
      this.tramitesFiltrados();
      this.loading = false;
    },
    abrirModal() {
      if (this.modalSeleccionTramitesFaltaReponer) {
        this.loading = true;
        this.extraerTramitesPorAnio(this.anioTramites);
      }
    },
  },
});
</script>

