<script>
import Vue from "vue";

import { mapGetters, mapActions, mapState } from "vuex";

export default Vue.extend({
  name: "UsuarioComplementaria",

  data: () => ({
    fullName: null,
    typeUserSelected: null,
    profEnrollment: null,
    prof: null,
    cuit: null,
    dni: null,
    ley: null,
    numero_liquidacion: null,
    exento: false,
    pago_diferido: false,
    usuarioSeleccionado: [],
    mostrarPagoDiferido: false,
    mostrarExento: false,
  }),
  computed: {
    ...mapState("Tramites-seleccionados", ["tipo_usuario", "leyes"]),
    ...mapState("Tramites-completados", ["complementaria"]),
    busquedaValidada() {
      return this.complementaria;
    },
  },

  mounted() {
    this.cargarDatosUsuario();
  },

  methods: {
    cargarDatosUsuario() {
      this.fullName = this.complementaria[0].solicitante;
      this.cuit = this.complementaria[0].cuit;
      this.usuarioSeleccionado = this.tipo_usuario.filter(
        (element) =>
          element.id == this.complementaria[0].tipo_usuario
      );
      this.pago_diferido = this.complementaria[0].pago_diferido == "N" ? false : true;
      this.profEnrollment = this.complementaria[0].mp;
      this.prof = this.complementaria[0].profesion;
      this.dni = this.complementaria[0].nro_doc;
      this.numero_liquidacion = this.complementaria[0].nro_liquidacion;
      this.typeUserSelected = this.usuarioSeleccionado[0].nombre;
      this.mostrarPagoDiferido = this.usuarioSeleccionado[0].pago_diferido;
      this.mostrarExento = this.usuarioSeleccionado[0].exento;
      if(this.complementaria[0].tasa_total_exenta != 0){
        this.exento = true;
      }else{
        this.exento = false;
      }
      // this.exento = this.complementaria[0].exento;
      if (this.exento) {
        if(this.complementaria[0].idley != null && this.complementaria[0].idley != ""){
          this.ley = this.leyes.filter(
            (element) => element.idley == this.complementaria[0].idley
          )[0].concepto;
        }
      }
    },
  },
  watch: {
    busquedaValidada() {
      if( this.complementaria != null && this.complementaria.length != 0){
        if(this.complementaria[0].nrounico == ""
          && this.complementaria[0].tiene_tasa_complementaria == "N"
          && this.complementaria[0].es_tasa_complementaria == "N"){
            this.cargarDatosUsuario();
        }
      }
    },
  },
});
</script>
