<template>
  <v-form ref="formu" class="contenedorBuscador" v-model="valid" :lazy-validation="lazy">
    <v-row class="contenedorBuscador">
      <v-col cols="12">
        <v-row class="d-flex contenedorBuscador justify-space-between align-self-center align-center mx-4">
          <v-col cols="12" class="pa-0"  v-if="errores !== ''">
            <v-alert
              text
              outlined
              color="red"
              class="py-2 mb-2 mt-1 text-center body-2"
              width="100%"
              close-icon="close"
              dismissible
              v-if="errores !== ''"
            >
              <template v-slot:close="{}">
                <v-icon size="18" color="error" @click="resetearError"
                  >close</v-icon
                >
              </template>
              {{ errores }}
            </v-alert>
          </v-col>
          <v-col sm="6" md="4" class="pb-0 pt-4 px-0 completo">
            <v-text-field
              v-model="tramite"
              @keypress="onlyNumber($event)"
              :rules="requiredNumTramite"
              maxlength="7"
              label="Número de trámite"
              outlined
              dense
              :disabled="buscando == true"
              class="centered-input shrink unico"
            ></v-text-field>
          </v-col>
          <v-col sm="5" md="3" class="d-flex justify-end py-2 px-0">
            <v-btn
              outlined
              color="primary"
              class="no-text-transforms boton"
              @click="buscar()"
              :disabled="buscando == true"
              :loading="buscando == true"
            >
              buscar
              <v-icon color="primary" size="14"> </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
import Vue from "vue";
import { mapActions, mapGetters } from "vuex";

export default Vue.extend({
  name: "BusquedaPorTramite",
  data() {
    return {
      tramite: null,
      valid: true,
      lazy: false,
      requiredNumTramite: [
        (v) => !!v || "Campo requerido.",
        (v) => /(?![A-Za-z]|\s)./g.test(v) || "Campo numérico",
       // (v) => (v || "").length == 10 || "Debe estar compuesto por 10 dígitos",
      ],
    };
  },
  computed: {
    ...mapGetters("Consulta-documentos", ["buscando", "errores"]),
  },
  methods: {
    ...mapActions("Consulta-documentos", ["busquedaPorTramite", "resetearError"]),
    onlyNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (charCode < 48 || charCode > 57) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    buscar() {
      if (!this.$refs.formu.validate()) {
        return;
      }
      this.busquedaPorTramite(this.tramite);
    },
  },
});
</script>

<style scoped>
@media (max-width: 475px) {
  .completo {
    flex-basis: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .unico {
    max-width: 100% !important;
    width: 100%;
  }
}

.unico {
  max-width: 225px;
}
.contenedorBuscador{
  height:100%;
}
</style>
